import React, {useState} from 'react';
import { withRouter } from "react-router-dom";
import Summary from "components/pages/H-MY/Summary";
import CustomList from "components/molecule/list/CustomList";

const dataItem = {
  datas: [
    {
      title: false,
      datas: [
        {
          content: "서비스 이용약관",
          arrow : true,
          link: "/H-MY/07300100",
        },
        {
          content: "위치기반 서비스 이용약관",
          arrow : true,
          link: "/H-MY/07300100",
        },
        {
          content: "통신판매업 이용약관",
          arrow : true,
          link: "/H-MY/07300100",
        },
        {
          content: "개인정보 처리방침",
          arrow : true,
          link: "/H-MY/07300100",
        },
      ],
    },
  ]
}

const Hmy07300000 = (props) => {
  return (
    <>
      <div className="page-my page-wrapper t-termlist">
        <div className="m-main">
          <div className="my-flex flex">
            <div className="col-summary">
              <Summary />
            </div>

            <div className="col-right">
              <div className="right-outer t3">
                <div className="bg-box">
                  <header className="right-header">
                    <strong className="tit">약관 및 정책</strong>
                  </header>
                  <section>
                    <div className="m-main">
                      <div className="list-container">
                        <CustomList dataItem={dataItem} />
                      </div>
                    </div>
                  </section>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default withRouter(Hmy07300000);
