import React from "react";
import FormSearch from "components/molecule/search/FormSearch";
import LatelySearch from "components/molecule/search/LatelySearch";

const SearchForm = (pros) => {
  const { latelyTag, onClick, placeholder } = pros;
  return (
    <>
      <div className="search-form form-group">
        <FormSearch
          attr={{
            name: "",
            placeholder: placeholder
              ? placeholder
              : "팀, 라이브, 동영상을 검색해보세요.",
            onClick: "",
          }}
        />
        {latelyTag && <LatelySearch latelyTag={latelyTag} />}
      </div>
    </>
  );
};

export default SearchForm;
