import React, { useState } from "react";
import { Link } from "react-router-dom/cjs/react-router-dom";
import Button from "components/atom/button/Button";
import Confirm from "components/molecule/alert/Confirm";
const ButtonAlert = (props) => {
  const {
    color,
    size,
    state,
    className,
    style,
    onClick,
    disabled,
    text,
    solidIcon,
    regularIcon,
    animatedIcon,
    iconProps,
    iconOnly,
    icon,
    widthTxt,
    imgProps,
    addText,
    arrow,
    out,
    shape,
    radius,
    alertDatas,
  } = props.attr;

  /*const {
    alert,
    title,
    titleSize,
    titleColor,
    smallTit,
    smallTitSize,
    smallTitColor,
    dec,
    balls,
    chkList,
    button,
    className,
    icon,
    nowrap,
  } = alertDatas;*/

  const [alertOpen, setAlertOpen] = useState(false);

  const openAlertHandler = () => {
    setAlertOpen(!alertOpen);
  };

  return (
    <>
      <Button
        attr={{
          text,
          onClick: () => openAlertHandler(),
          color,
          size,
          className,
        }}
      />
      {alertOpen && (
        <Confirm
          data={{
            alert:alertDatas.alert,
            title:alertDatas.title,
            titleSize:alertDatas.titleSize,
            titleColor:alertDatas.titleColor,
            smallTitSize:alertDatas.smallTitSize,
            smallTitColor:alertDatas.smallTitColor,
            smallTit:alertDatas.smallTit,
            dec:alertDatas.dec,
            className:alertDatas.className,
            balls:alertDatas.balls,
            chkList:alertDatas.chkList,
            icon:alertDatas.icon,
            nowrap:alertDatas.nowrap,
            button:alertDatas.button,
          }}
          onClose={openAlertHandler}
        />
      )}
    </>
  );
};

export default ButtonAlert;
