import React, { useState, useEffect, createRef } from "react";
import { Route, Link } from "react-router-dom";
import TopMenuNavList from "./top-menu-nav-list";
import menus from "./menu";
import Slider from "react-slick";

function TopMenuNav(props) {
  const [active, setActive] = useState(-1);
  const [clicked, setClicked] = useState(-1);
  const [controlLeft, setControlLeft] = useState(false);
  const [controlRight, setControlRight] = useState(false);
  const [marginLeft, setMarginLeft] = useState(0);
  const [marginRight, setMarginRight] = useState(0);
  const [navWidth, setNavWidth] = useState(0);

  const topMenu = createRef();

  const handleExpand = (e, i, match) => {
    e.preventDefault();

    setActive(active === i ? -1 : i);
  };

  const controlToRight = (e) => {
    e.preventDefault();
    var containerWidth = document.querySelector(".top-menu").offsetWidth - 88;
    var widthLeft = navWidth + -marginLeft - containerWidth;
    var finalScrollWidth = 0;

    if (widthLeft <= containerWidth) {
      finalScrollWidth = widthLeft - -marginLeft + 128;

      setControlRight(false);
    } else {
      finalScrollWidth = containerWidth - -marginLeft - 128;
    }

    if (finalScrollWidth !== 0) {
      if (!document.body.classList.contains("rtl-mode")) {
        setMarginRight(0);
        setMarginLeft(finalScrollWidth);
      } else {
        setMarginLeft(0);
        setMarginRight(finalScrollWidth);
      }
      setControlLeft(true);
    }
  };

  const controlToLeft = (e) => {
    e.preventDefault();
    var widthLeft = marginLeft;
    var containerWidth = document.querySelector(".top-menu").offsetWidth;
    var finalScrollWidth = 0;

    if (widthLeft <= containerWidth) {
      finalScrollWidth = 0;

      setControlLeft(false);
    } else {
      finalScrollWidth = widthLeft - containerWidth - 88;

      setControlLeft(true);
    }
    if (!document.body.classList.contains("rtl-mode")) {
      setMarginLeft(finalScrollWidth);
      setMarginRight(0);
      setControlRight(true);
    } else {
      setMarginLeft(0);
      setMarginRight(finalScrollWidth);
      setControlRight(true);
    }
  };

  useEffect(() => {
    var windowWidth = topMenu.current.offsetWidth - 128;
    var listFullWidth = 0;
    var listPrevWidth = 0;
    var listActive = false;

    document.querySelectorAll(".top-menu .nav > li").forEach(function (elm) {
      listFullWidth += elm.offsetWidth;
      listPrevWidth += !listActive ? elm.offsetWidth : 0;
      listActive = elm.classList.contains("active") ? true : listActive;
    });

    listPrevWidth = !listActive ? 0 : listPrevWidth;

    if (listPrevWidth >= windowWidth) {
      var finalScrollWidth = listPrevWidth - windowWidth + 128;
      var finalLeft, finalRight;
      if (!document.body.classList.contains("rtl-mode")) {
        finalLeft = finalScrollWidth;
        finalRight = 0;
      } else {
        finalRight = finalScrollWidth;
        finalLeft = 0;
      }

      setMarginLeft(finalLeft);
      setMarginRight(finalRight);
    }

    setNavWidth(listFullWidth);
    setControlLeft(
      listPrevWidth >= windowWidth && listFullWidth >= windowWidth
        ? true
        : false
    );
    setControlRight(
      listPrevWidth !== listFullWidth && listFullWidth >= windowWidth
        ? true
        : false
    );
  }, [topMenu]);

  const settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 5,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 5,
          slidesToScroll: 1,
          adaptiveHeight: true,
        },
      },
      {
        breakpoint: 580,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          adaptiveHeight: true,
        },
      },
      {
        breakpoint: 860,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 1200,
        settings: {
          slidesToShow: 5,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <Slider {...settings} ref={topMenu} className={"nav"}>
      {menus.map((menu, i) => (
        <Route
          path={menu.path}
          exact={menu.exact}
          key={i}
          children={({ match }) => (
            <TopMenuNavList
              data={menu}
              expand={(e) => handleExpand(e, i, match)}
              active={i === active}
              clicked={clicked}
            />
          )}
        />
      ))}
    </Slider>
  );
}

export default TopMenuNav;
