import React, { useState, } from "react";
import { withRouter } from "react-router-dom";
import Summary from "components/pages/H-MY/Summary";
import FormWrap from "components/molecule/form/FormWrap";
import FormItem from "components/molecule/form/FormItem";
import Button from 'components/atom/button/Button';
import BtnLayout from "components/molecule/layout/BtnLayout";
import Checkbox from "components/molecule/form/Checkbox";
import Terms from "components/atom/Terms";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const fromData = [
  {
    label: "탈퇴사유",
    type: "radio",
    id : "radio",
    name : "radio",
    options: [
      {
        text: "원하는 정보 부족",
        value: "",
        size : "big",
      },
      {
        text: "취소 등 민원처리 불만",
        value: "",
        size : "big",
      },
      {
        text: "어려운 앱 사용성",
        value: "",
        size : "big",
      },
      {
        text: "기타 불만",
        value: "",
        size : "big",
      },
    ],
  },
]

const HmyM1020000 = (props) => {

  const [data, setData] = useState({
    ball: "100",
    season: "7일시즌권",
  });

  return (
    <div className="page-my page-wrapper t-withdraw">
      <div className="m-main">
        <div className="my-flex flex">
          <div className="col-summary">
            <Summary />
          </div>

          <div className="col-right">
            <div className="right-outer t3">
              <div className="bg-box">
                <header className="right-header">
                  <strong className="tit">내 정보</strong>
                </header>
                <FullHeight attr={{pageHeader:true}}>
                  <ColTop>
                    <strong className="noti">
                      탈퇴 전, 아래의 유의사항을 <br />
                      꼭 확인해주세요!
                    </strong>
                    <div className="over-wrap box">
                      <div className="dec-wrap">
                        <Terms type="withdraw" />
                      </div>
                    </div>

                    <div className="detail-box box">
                      <dl className="top-flex flex">
                        <dt>보유 뽈</dt>
                        <dd>
                          <strong className="ball">{data.ball}</strong> 뽈
                        </dd>
                      </dl>

                      <dl className="btt-flex flex">
                        <dt>보유 시즌권</dt>
                        <dd>{data.season}</dd>
                      </dl>
                    </div>

                    <div className="form-container _wrap">
                      { fromData
                        .map((item, index) => (
                          <FormItem
                            key={index}
                            attr={item}
                          />
                        ))
                      }
                    </div>

                    <div className="form-box">
                      <FormWrap attr={{
                        type: "textarea",
                        id : "textarea",
                        name : "textarea",
                        placeholder: "어떤 이유로 호각을 탈퇴할 결심을 하셨나요?",
                        lengths:{
                          position:"bottom",
                          length:"0",
                          maxLength:"40",
                        },
                      }} />
                    </div>
                  </ColTop>
                  <ColBottom>
                    <div className="term-flex flex">
                      <Checkbox attr={{
                        id:"chk2",
                        name:"chk2",
                        value:"",
                        required:"",
                        label:"유의사항을 모두 확인하였으며,\n회원탈퇴 시 포인트(뽈) 소멸에 동의합니다.",
                      }} />
                    </div>
                    <BtnLayout
                      attr={[
                        {
                          className : 'w100',
                          text: '탈퇴하기',
                          disabled : true,
                          size : 'btn-lg',
                          color : 'btn-primary'
                        },
                      ]}
                      className="full"
                    />
                  </ColBottom>
                </FullHeight>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default withRouter(HmyM1020000);
