const Textarea = ({ attr }) => {
  const {
    id,
    name,
    value,
    className,
    readOnly,
    disabled,
    onChange,
    onKeyPress,
    placeholder,
    autoComplete,
    maxLength,
    ref,
  } = attr.formData;


  const handleChange = (e) => {};

  return (
    <textarea
      id={id}
      name={name}
      className={"form-control "+ (className || "")}
      readOnly={readOnly}
      disabled={disabled}
      onChange={onChange || handleChange}
      onKeyPress={onKeyPress || handleChange}
      placeholder={placeholder}
      autoComplete={autoComplete}
      defaultValue={value}
      maxLength={maxLength}
      ref={ref}
    />
  );
};

export default Textarea;
