import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";
import useMediaQueryHook from "hook/useMediaQuery";
import Button from "components/atom/button/Button";
import handleLocation from "handler/handleLocation";

import useradd from "assets/images/icon/icon_useradd.svg";
import userchadd from "assets/images/icon/icon_userchadd.svg";
import foreignadd from "assets/images/icon/icon_foreignadd.svg";

const Hlog01000000 = (props) => {
  const { currentMediaQuery, isMobile } = useMediaQueryHook();
  const [isOpen, setIsOpen] = useState(false);

  const toggleBottomSheet = () => {
    setIsOpen(!isOpen);
  };
  return (
    <>
      <div className="page-wrapper joinPage bg-gray">
        <section className="join_wrapper sec-height _fix">
          <div className="join_container">
            <div className="m-main">
              <div className="join_info">
                가입 정보를
                <br /> 선택해주세요.
              </div>

              <div className={`join_btn_wrapper ${isMobile && "mobile"} flexCC`}>
                <Button
                  attr={{
                    text: "내국인",
                    className: `white join_btn ${isMobile && "mobile"}`,
                    imgProps: {
                      src: useradd,
                      style: {
                        width: "32px",
                        height: "27px",
                      },
                    },
                    addText: "(만 14세 이상)",
                    onClick: handleLocation("/H-LOG/01010000"),
                  }}
                />
                <Button
                  attr={{
                    text: "내국인",
                    className: `white join_btn ${isMobile && "mobile"}`,
                    imgProps: {
                      src: userchadd,
                      style: {
                        width: "32px",
                        height: "27px",
                      },
                    },
                    addText: "(만 14세 미만)",
                    onClick: handleLocation("/H-LOG/01020000"),
                  }}
                />
                <Button
                  attr={{
                    text: "외국인",
                    className: `white join_btn ${isMobile && "mobile"}`,
                    imgProps: {
                      src: foreignadd,
                      style: {
                        width: "32px",
                        height: "27px",
                      },
                    },
                    addText: "(foreigner)",
                    onClick: handleLocation("/H-LOG/01030000"),
                  }}
                />
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  );
};

export default withRouter(Hlog01000000);
