import React from "react";
import { Link } from "react-router-dom/cjs/react-router-dom";

const List = ({ data }) => {
  const {type, lists} = data;
  return (
    <ul className={`bbs-container bbs-list ${type ? type : ""}`}>
      {/*Todo if nodata*/}
      {type !== 'qna' && (
        <li>
          <div className="nodata">
            {type === 'notice' && (
              <p className="dec">등록된 공지사항이 없습니다.</p>
            )}
            {type === 'event' && (
              <p className="dec">등록된 이벤트가 없습니다.</p>
            )}
          </div>
        </li>
      )}
      {lists && lists.map((item, index) => (
        <li key={index}>
          <Link to={item.link} className="item-flex flex">
            {data.type === 'qna' && (
              <div className="col-status">
                <span className={`status-label ${item.labelColor && item.labelColor}`}>{item.status}</span>
              </div>
            )}
            <div className="col-dec">
              <p className="tit ellip">{item.title}</p>
              <span className="date">{item.date}</span>
            </div>
          </Link>
        </li>
      ))}
    </ul>
  );
};

export default List;
