import React, { useState, forwardRef, useImperativeHandle } from 'react';
import SelectSet from "components/molecule/form/SelectSet";
import useMediaQueryHook from "hook/useMediaQuery";

const BubbleTab = forwardRef(({ tabs, isFixed, selectSetRef }, ref) => {
  // 첫 번째 탭을 기본적으로 활성화 상태로 설정
  const [selectedTab, setSelectedTab] = useState(tabs[0].label);
  const { currentMediaQuery, isMobile } = useMediaQueryHook();

  const handleTabClick = (tab) => {
    setSelectedTab(tab.label); // 탭 클릭 시 선택된 탭으로 상태 업데이트
    if (tab.onClick) {
      tab.onClick(); // tab.onClick 핸들러가 있을 경우 호출
    }
  };

  useImperativeHandle(ref, () => ({
    // 부모 컴포넌트에서 사용할 함수를 선언
    handleTabClick
  }))

  const handleSelectChange = (e) => {
    const selectedValue = e.target.value;

    // 선택된 값에 따라 적절한 탭 찾기
    const selectedTab =
      tabs.find((tab) => tab.label === selectedValue) ||
      tabs.find((tab) => tab.options && tab.options.includes(selectedValue)); // options가 정의되어 있는지 확인

    if (selectedTab) {
      setSelectedTab(selectedTab.label);
    }
  };

  return (
    <ul className={`bubble-tab-container ${isFixed ? "fixed" : ""}`}>
      {tabs.map((tab, index) => (
        <li key={index}>
          <div
            className={`bubble-tab ${
              selectedTab === tab.label ? "active" : ""
            } ${tab.className ? tab.className : ""} ${
              tab.select ? "_select" : ""
            }`}
          >
            {tab.refesh ? (
              <div className="tab-label" onClick={tab.onClick}>
                <span className="txt ico refesh">초기화</span>
                <span className="cnt">{tab.cnt}</span>
              </div>
            ) : tab.select ? (
              <SelectSet
                attr={{
                  //status:status,
                  type: "select",
                  id: tab.id,
                  name: tab.name,
                  text: tab.label,
                  onClick: tab.onClick,
                  onChange: tab.onChange,
                  placeholder: tab.label,
                  options: tab.options,
                  layers: {
                    selectType: "bubbleTab",
                    optionLabel: tab.layers
                      ? tab.layers.optionLabel
                        ? tab.layers.optionLabel
                        : tab.label
                      : tab.label,
                    optionType: tab.layers ? tab.layers.optionType : "radio",
                  },
                  buttons: {
                    text: tab.buttons ? tab.buttons.text : tab.label,
                    className: tab.buttons ? tab.buttons.className : "",
                    color: tab.buttons ? tab.buttons.color : "",
                    size: tab.buttons ? tab.buttons.size : "",
                    shape: tab.buttons ? tab.buttons.shape : "",
                    icon: tab.buttons ? tab.buttons.icon : "ico-down",
                    iconOnly: tab.buttons ? tab.buttons.iconOnly : "",
                    widthTxt: tab.buttons ? tab.buttons.widthTxt : "",
                    onClick: tab.buttons ? tab.buttons.onClick : "",
                  },
                  dataIndex:index,
                }}
                ref={selectSetRef}
              />
            ) : (
              <div className="tab-label" onClick={() => handleTabClick(tab)}>
                <span className="txt">{tab.label}</span>
              </div>
            )}
          </div>
        </li>
      ))}
    </ul>
  );
});

export default BubbleTab;
