import React, { useState } from "react";

const Select = ({ attr }) => {
  const {
    id,
    name,
    value,
    required,
    placeholder,
    formClassName,
    disabled,
    onChange,
    onBlur,
    options,
    buttons,
    layers,
    ref,
  } = attr;

  return (
    <select
      id={id}
      name={name}
      className={formClassName}
      onChange={onChange}
      disabled={disabled}
      onBlure={onBlur}
      defaultValue={value}
    >
      <option value="">{placeholder}</option>
      {options?.map((item, index) => (
        <option key={index} value={item.value}>
          {item.text}
        </option>
      ))}
    </select>
  );
};

export default Select;
