import React from "react";

const ClipVod = (props) => {
  const { img, no } = props;
  return (
    <div className="chk-label">
      <input type="checkbox" id={`chk_${no}`} />
      <label className="label" htmlFor={`chk_${no}`}><div className="img-box thumb" style={{ backgroundImage: `url(${img})` }}></div></label>
    </div>
  );
};

export default ClipVod;
