import React from "react";
import Slider from "react-slick";

import LiveCard from "components/molecule/Card/LiveCard";
import ScheduleCard from "components/molecule/Card/ScheduleCard";

function CardSlider({ data, type }) {
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: data?.length === 2 ? 2 : 3.01,
    slidesToScroll: data?.length === 2 ? 2 : 3,
    responsive: [
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 1.01,
          slidesToScroll: 1,
          adaptiveHeight: true,
        },
      },
      /*{
        breakpoint: 768,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
        },
      },*/
      {
        breakpoint: 880,
        settings: {
          slidesToShow: 2.01,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: data?.length === 2 ? 2 : 3.01,
          slidesToScroll: data?.length === 2 ? 2 : 3,
        },
      },
    ],
  };
  return (
    <div className="slider-container">
      {data.length === 1 ? (
        <div className="card_slider">
          {type === "schedule" ? (
            <ScheduleCard
              progressType={data[0].progressType}
              img={data[0].img}
              startDate={data[0].startDate}
              endDate={data[0].endDate}
            />
          ) : (
            <LiveCard
              title={data[0].title}
              team1={data[0].team1}
              team2={data[0].team2}
              stadium={data[0].stadium}
              score={data[0].score}
              date={data[0].date}
            />
          )}
        </div>
      ) : (
        <Slider {...settings}>
          {data.map((item, index) =>
            type === "schedule" ? (
              <div className="card_slider" key={index}>
                <ScheduleCard
                  progressType={item.progressType}
                  img={item.img}
                  startDate={item.startDate}
                  endDate={item.endDate}
                />
              </div>
            ) : (
              <div className="card_slider" key={index}>
                <LiveCard
                  title={item.title}
                  team1={item.team1}
                  team2={item.team2}
                  stadium={item.stadium}
                  score={item.score}
                  date={item.date}
                />
              </div>
            )
          )}
        </Slider>
      )}
    </div>
  );
}

export default CardSlider;
