import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom/cjs/react-router-dom.min";
import useMediaQueryHook from "hook/useMediaQuery";
import AppHeader from "components/organism/header/AppHeader";
import handleChange from "handler/handleChange";
import PageHeader from "components/molecule/layout/PageHeader";
import Button from "components/atom/button/Button";
import TimeSelect from "components/molecule/reserve/TimeSelect";
import Checkbox from "components/molecule/form/Checkbox";
import Radio from "components/molecule/form/Radio";
import "react-datetime/css/react-datetime.css";
import Datetime from "react-datetime";

const Hmain07200100 = (props) => {
  const [repeatVisible, setRepeatVisible] = useState(false);

  const handleChange = (e, state, setState) => {
    let { name, value } = e.target;

    if(value === "repeat") {
      setRepeatVisible(true)
    } else {
      setRepeatVisible(false)
    }
  }

  return (
    <>
      <PageHeader onClick="" title="촬영예약" />
      <div className="page page-wrapper page-shoot">
        <section className="sec sec-height _fix _between">
          <div className="m-main">
            <div className="col-top">
              <div className="location-container">
                <header className="location-header">
                  <strong className="stadium">동해시 공설운동장 테니스 A코트</strong>
                </header>
              </div>
              <div className="shoot-container">
              <dl className="shoot-dl">
                <dt>예약 날짜 및 시간</dt>
                <dd>
                  <div className="form-box">
                    <div className="form-container">
                      <div className="row">
                        <div className="form-group">
                          <div className="col-form w100">
                            <Radio attr={{
                              name:"radio1",
                              id:"radio1",
                              label:"일반 등록",
                              value:"",
                              checked:"true",
                              onChange:handleChange,
                            }}/>

                            <Radio attr={{
                              name:"radio1",
                              id:"radio2",
                              label:"매주 반복",
                              value:"repeat",
                              onChange:handleChange,
                            }}/>
                          </div>
                        </div>
                      </div>

                      {repeatVisible && (
                        <ul className="row repeat-list">
                          <li>
                            <div className="chk-label checkbox t-btn square">
                              <input type="checkbox" name="" id="chk1" />
                              <label className="txt" htmlFor="chk1">월</label>
                            </div>
                          </li>
                          <li>
                            <div className="chk-label checkbox t-btn square">
                              <input type="checkbox" name="" id="chk2" />
                              <label className="txt" htmlFor="chk2">화</label>
                            </div>
                          </li>
                          <li>
                            <div className="chk-label checkbox t-btn square">
                              <input type="checkbox" name="" id="chk3" />
                              <label className="txt" htmlFor="chk3">수</label>
                            </div>
                          </li>
                          <li>
                            <div className="chk-label checkbox t-btn square">
                              <input type="checkbox" name="" id="chk4" />
                              <label className="txt" htmlFor="chk4">목</label>
                            </div>
                          </li>
                          <li>
                            <div className="chk-label checkbox t-btn square">
                              <input type="checkbox" name="" id="chk5" />
                              <label className="txt" htmlFor="chk5">금</label>
                            </div>
                          </li>
                          <li>
                            <div className="chk-label checkbox t-btn square">
                              <input type="checkbox" name="" id="chk6" />
                              <label className="txt" htmlFor="chk6">토</label>
                            </div>
                          </li>
                          <li>
                            <div className="chk-label checkbox t-btn square">
                              <input type="checkbox" name="" id="chk7" />
                              <label className="txt" htmlFor="chk7">일</label>
                            </div>
                          </li>
                        </ul>
                      )}

                      <div className="row">
                        <div className="form-group col-50 _w100">
                          <div className="col-form-label">시작시간</div>
                          <div className="col-form">
                            <ul className="time-flex flex f2">
                              <li>
                                <div className="form-wrap _arrow">
                                  <Datetime value="시작날짜" closeOnSelect={true} inputProps={{
                                    closeOnSelect:true,
                                  }}/>
                                </div>
                              </li>
                              <li>
                                <div className="form-wrap _arrow">
                                  <Datetime value="시작시간" closeOnSelect={true} inputProps={{
                                    closeOnSelect:true,
                                  }}/>
                                </div>
                              </li>
                            </ul>
                          </div>
                        </div>

                        <div className="form-group col-50 _w100">
                          <div className="col-form-label">종료시간</div>
                          <div className="col-form w100">
                            <ul className="time-flex flex f2">
                              <li>
                                <div className="form-wrap _arrow">
                                  <Datetime value="종료날짜" closeOnSelect={true} inputProps={{
                                    closeOnSelect:true,
                                  }}/>
                                </div>
                              </li>
                              <li>
                                <div className="form-wrap _arrow">
                                  <Datetime value="종료시간" closeOnSelect={true} inputProps={{
                                    closeOnSelect:true,
                                  }}/>
                                </div>
                              </li>
                            </ul>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </dd>
              </dl>
            </div>

              <ul className="blit-list">
                <li>
                  촬영 시작 20분 전까지는 촬영취소가 가능하며, <br />
                  촬영 시작 후에는 취소가 불가합니다.
                </li>
              </ul>
            </div>

            <div className="btt-btn">
              <div className="agree-container">
                <Checkbox attr={{
                  name:"agree",
                  id:"agree",
                  label:"초상권 및 저작권 사용 동의 (필수)",
                }}/>

                <Button attr={{
                  className : "w100",
                  text: '예약하기 (1/3)',
                  onClick: "",
                  disabled: true,
                  color:"btn-black",
                  size:"btn-lg",
                }}
                />
              </div>
            </div>
          </div>
        </section>


      </div>
    </>
  );
};

export default withRouter(Hmain07200100);
