import React from "react";
import VodUtil from "components/molecule/vod/VodUtil";

const utilData = {
  report : true,
  share : true,
  heart : true,
  etc : true
}

const VodHeader = ({ data }) => {
  const {
    title,
    time,
    ball,
  } = data;
  return (
    <header className="tit-header">
      <dl className="top-flex flex">
        <dt><strong className="tit">{title}</strong></dt>
        <dd><VodUtil data={utilData} /></dd>
      </dl>
      <div className="btt-flex flex">
        <div className="col-left"><p className="time">{time}</p></div>
        <div className="col-right"><p className="dec c-blue">{ball}뽈이 필요해요</p></div>
      </div>
    </header>
  )
};

export default VodHeader;
