import React, {useState, useContext, useEffect, useRef} from "react";
import { withRouter } from "react-router-dom";

import useMediaQueryHook from "hook/useMediaQuery";

import logo from "assets/images/example/city_logo.png";
import bschk from "assets/images/icon/Icon_bschk.svg";
import FormItem from "components/molecule/form/FormItem";
import handleChange from "handler/handleChange";
import Button from "components/atom/button/Button";
import PageHeader from "components/molecule/layout/PageHeader";
import Checkbox from "components/atom/Checkbox";
import handleCheck from "handler/handleCheck";
import handleLocation from "handler/handleLocation";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const Hgrp01010000 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); // 이전 스크롤 위치

  const handleScroll = () => {
    const currentScrollY = window.scrollY;
    setScrollY(currentScrollY);

    if (currentScrollY > prevScrollY && currentScrollY > 100) {
      setIsExpanded(false);
    } else if (currentScrollY < prevScrollY) {
      setIsExpanded(true);
    }

    setPrevScrollY(currentScrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollY]);

  const [data, setData] = useState({
    team: "동해시",
    logo: logo,
    state: "",
    name: "홍길동",
    birthdate: "19990101",
  });

  const formGroup = [
    {
      label: "이름",
      labelClassName: "",
      formClassName: "_readonly",
      formBtns: "",
      copyToClipboard: "",
      id: "name",
      name: "name",
      type: "text",
      value: data.name,
      status: "",
      onChange: (e) => handleChange(e, data, setData),
      readOnly: "readonly",
      onKeyPress: "",
      autocomplete: "",
      inValidMsg: "",
      noBtn: true,
    },
    {
      label: "생년월일",
      labelClassName: "",
      formClassName: "_readonly",
      formBtns: "",
      copyToClipboard: "",
      id: "birthdate",
      name: "birthdate",
      type: "text",
      value: data.birthdate,
      status: "",
      onChange: (e) => handleChange(e, data, setData),
      readOnly: "readonly",
      onKeyPress: "",
      autocomplete: "",
      inValidMsg: "",
      noBtn: true,
    },
  ];

  const kvRef = useRef(null);
  return (
    <>
      <div className="teamJoin_wrapper grp">
        <PageHeader onClick="" title="가입하기" noBtn="true" />
        <div className="assJoin_Page flexCC" ref={kvRef}>
          <div className="assJoin_image_wrapper flexCC">
            <img src={data.logo} />
            <img className="chk" src={bschk} />
          </div>
          {data.team}
        </div>

        <div className="page page-wrapper">
          <FullHeight attr={{pageHeader:true, kvRef:kvRef}}>
            <ColTop>
              <section className="subHome_section teamJoin">
                <div className="teamJoin_noti">
                  가입 정보로 팀 가입 신청 됩니다. {isMobile && <br />} 본인
                  정보가 맞는지 확인해주세요.
                </div>
                <div className={`teamJoin_form ${isMobile && "mobile"} flexCC`}>
                  <div className="form-container">
                    {formGroup.map((item, index) => (
                      <FormItem key={index} attr={item} />
                    ))}
                  </div>
                </div>
              </section>
            </ColTop>
            <ColBottom>
              <div className="btn_wrapper">
                <Button
                  attr={{
                    className: "w100",
                    text: "다음",
                    onClick: handleLocation("/H-GRP/01010100"),
                    color: "btn-primary",
                    size: "btn-lg",
                  }}
                />
              </div>
            </ColBottom>
          </FullHeight>
        </div>
      </div>
    </>
  );
};

export default withRouter(Hgrp01010000);
