import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";

import useMediaQueryHook from "hook/useMediaQuery";

import logo from "assets/images/example/ex_teamlogo.png";
import player from "assets/images/example/team_member.png";
import Button from "components/atom/button/Button";
import PageHeader from "components/molecule/layout/PageHeader";
import MemberInfoCard from "components/molecule/Card/MemberInfoCard";
import CustomList from "components/organism/table/CustomList";
import Confirm from "components/molecule/alert/Confirm";
import handleLocation from "handler/handleLocation";

import AlertPopup from "components/organism/popup/AlertPopup";
import DepthRow from "components/molecule/layer/DepthRow";
import DepthItem from "components/molecule/layer/DepthItem";

const Hgrp02010000 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); // 이전 스크롤 위치
  const [length, setLength] = useState(0); // 현재 글자 수 상태 추가

  const [isOpen, setIsOpen] = useState(false);
  const [showAlert, setShowAlert] = useState(false);

  const [confirmType, setConfirmType] = useState(null);
  const [confirmOpen, setConfirmOpen] = useState(false);

  const openConfirmHandler = (id) => {
    setConfirmOpen(!confirmOpen);
  };

  const handleScroll = () => {
    const currentScrollY = window.scrollY;
    setScrollY(currentScrollY);

    if (currentScrollY > prevScrollY && currentScrollY > 100) {
      setIsExpanded(false);
    } else if (currentScrollY < prevScrollY) {
      setIsExpanded(true);
    }

    setPrevScrollY(currentScrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollY]);

  const handleToggleChange = (newState) => {
    if (!newState) {
      setConfirmType("toggle");
      setConfirmOpen(true);
    }
  };

  const handleTClick = () => {
    setConfirmType("leave");
    setConfirmOpen(true);
  };

  const handleArrowClick = () => {
    {
      isMobile ? setIsOpen(!isOpen) : setShowAlert(!showAlert);
    }
  };

  const members = [{ name: "홍길동", position: "매니저" }];

  const [data, setData] = useState({
    team: "잠실유소년야구단",
    logo: logo,
    type: "parent",
    playerName: "김호각",
    position: "투수",
    school: "경원초등학교",
    birthdate: "2010/10/11",
    backNumber: "",
    nickName: "",
    members: members,
  });

  const dataItem = [
    {
      title: "알림설정",
      datas: [
        {
          content: "앱푸시 설정",
          className: "",
          addText: "팀 영상 업로드 시 앱푸시 알림이 발송됩니다.",
          toggle: true,
          onChange: handleToggleChange,
        },
      ],
    },
    {
      title: "가입",
      datas: [
        {
          content: "소속 관리",
          className: "",
          arrow: true,
          link: "/H-GRP/0202H100",
        },
        {
          content: "홈 정보수정",
          className: "",
          arrow: true,
          link: "/H-GRP/0203H210",
        },
        {
          content: "공지사항",
          className: "",
          arrow: true,
          link: "/H-GRP/0203H300",
        },
      ],
    },
  ];

  const options = [
    {
      text: "모두보기",
      value: "",
      name: "s1",
      checked: "true",
    },
    {
      text: "라이브",
      value: "",
      name: "s1",
    },
    {
      text: "방송예정",
      value: "",
      name: "s1",
    },
    {
      text: "지난경기영상",
      value: "",
      name: "s1",
    },
    {
      text: "클립",
      value: "",
      name: "s1",
    },
  ];

  const ConfirmData =
    confirmType === "toggle"
      ? {
          icon: true,
          smallTit: (
            <>
              팀 경기영상 및 일정 업로드 시 <br /> 앱푸시 알림이 발송되지
              않습니다.
            </>
          ),
          alert: true,
          button: {
            text: "확인",
            onClick: openConfirmHandler,
          },
        }
      : {
          title: "탈퇴하기",
          dec: (
            <>
              탈퇴 시 업로드 하신 영상, 정보 등 수정 및 <br />
              삭제가 불가합니다. 그래도 탈퇴하시겠습니까?
            </>
          ),
          nowrap: true,
          button: [
            {
              text: "취소",
              color: "btn-line-white",
              onClick: openConfirmHandler,
            },
            {
              text: "탈퇴하기",
              color: "btn-red",
              onClick: () => {
                openConfirmHandler();
              },
            },
          ],
        };

  const headerData = (
    <header className="teamJoin_header">
      <div className="title">팀 선택</div>
      <Button
        attr={{
          className: `ico-close-black`,
          text: "닫기",
          iconOnly: true,
          onClick: () => handleArrowClick(),
        }}
      />
    </header>
  );
  const optionData = (
    <div className="select-content">
      <ul className="tag-container">
        {options.map((item, index) => (
          <li key={index}>
            <DepthItem
              attr={{
                type: "checkbox",
                name: item.name,
                onclick: item.onclick,
                onChange: item.onChange,
                text: item.text,
                value: item.value,
                checked: item.checked,
              }}
            />
          </li>
        ))}
      </ul>
    </div>
  );

  const alertData = (
    <>
      <header className="teamJoin_header">
        <div className="alert-title">팀 선택</div>
        <Button
          attr={{
            className: `ico-close-black`,
            text: "닫기",
            iconOnly: true,
            onClick: () => handleArrowClick(),
          }}
        />
      </header>

      <div className="select-content">
        <ul className="tag-container">
          {options.map((item, index) => (
            <li key={index}>
              <DepthItem
                attr={{
                  type: "checkbox",
                  name: item.name,
                  onclick: item.onclick,
                  onChange: item.onChange,
                  text: item.text,
                  value: item.value,
                  checked: item.checked,
                }}
              />
            </li>
          ))}
        </ul>
      </div>
    </>
  );

  return (
    <>
      <div className="teamSetting_wrapper_nomal grp">
        <PageHeader onClick="" title="동해시" noBtn="true" />

        <div className="page page-shoot">
          <section className="sec">
            <div className={`${isMobile ? "m-main" : ""}`}>
              <MemberInfoCard
                members={data.members}
                // onClick={handleLocation("/H-GRP/01010100")}
                // arrowClick={handleArrowClick}
              />
              <section className="subHome_section teamJoin">
                <CustomList dataItem={dataItem} />
              </section>
            </div>
          </section>
          <div className="btn_wrapper_bt">
            <Button
              attr={{
                className: "w100",
                text: "탈퇴하기",
                onClick: () => handleTClick(),
                color: "btn-line-white-nb",
                size: "btn-lg",
                out: true,
              }}
            />
          </div>
        </div>
        {showAlert && <AlertPopup children={alertData} />}
      </div>
      {confirmOpen && (
        <Confirm data={ConfirmData} onClose={openConfirmHandler} />
      )}
    </>
  );
};

export default withRouter(Hgrp02010000);
