import React from "react";

const ThumbVod = (props) => {
  const { title, img, status, carmera, view } = props;

  return (
    <dl>
      <dt>
        <div className="top">
          <strong className="carmera">[{carmera}]</strong>
          <p className="view">{view}</p>
        </div>
        <p className="ellip">{title}</p>
      </dt>
      <dd>
        <div className={`img-box thumb ${status ? status : ""}`} style={{ backgroundImage: `url(${img})` }}></div>
      </dd>
    </dl>
  );
};

export default ThumbVod;
