import React, {useEffect, useRef, useState} from "react";
import { withRouter } from "react-router-dom";
import useMediaQueryHook from "../../../hook/useMediaQuery";
import BubbleTab from "components/atom/BubbleTab";
import CardList from "components/molecule/list/CardList";

import { scheduleCardData } from "../main/data";

const bubbleTabData = [
  {
    selectedTab: "축구",
    onClick: "",
    label: "축구",
  },
  {
    selectedTab: "",
    onClick: "",
    label: "야구",
  },
  {
    selectedTab: "",
    onClick: "",
    label: "핸드볼",
  },
  {
    selectedTab: "",
    onClick: "",
    label: "배구",
  },
];

const scheduleCard = {
  totalCnt : "10",
  list : scheduleCardData,
  cardType : "schedule"
}

const Hmain02000000 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  return (
    <>
      <div className={`page-wrapper pag-main top-fix w-100 ${!isMobile && "flexCC_col"}`}>
        <div className="page-title w-100">
          <h2 className="page-tit">대회 경기일정</h2>
        </div>
        <div className="top-select">
          <div className="m-main">
            <BubbleTab tabs={bubbleTabData} />
          </div>
        </div>

        <section className="sec sec-main-sub w-100">
          <div className="m-main">
            <CardList data={scheduleCard}/>
          </div>
        </section>
      </div>
    </>
  );
};

export default withRouter(Hmain02000000);
