import React, { useState } from "react";
import BtnLayout from "components/molecule/layout/BtnLayout";
import Button from "components/atom/button/Button";
import clogo from "assets/images/icon/ico_clogo.svg";

const Confirm = (props) => {
  const {
    alert,
    title,
    titleSize,
    titleColor,
    smallTit,
    smallTitSize,
    smallTitColor,
    dec,
    balls,
    chkList,
    button,
    className,
    icon,
    nowrap,
  } = props.data;

  console.log("Confirm", button)

  return (
    <div className="pop-layer">
      <div className="pop-container">
        <div
          className={`pop-content confirm-container ${
            className ? className : ""
          }`}
        >
          <div
            className={`dec-wrapper ${icon ? icon : ""} ${
              nowrap ? "nowrap" : ""
            }`}
          >
            {icon && <img src={clogo} alt="Logo" className="alert-logo" />}
            <strong>
              <span style={{fontSize : titleSize && titleSize, color : titleColor && titleColor}}>{title}</span>
              <small style={{fontSize : smallTitSize && smallTitSize, color : smallTitColor && smallTitColor}}>{smallTit}</small>
            </strong>
            <p className="dec">{dec}</p>
          </div>
          {balls ? (
            <div className="box">
              {balls.map((item, index) => (
                <dl key={index}>
                  <dt>{item.dt}</dt>
                  <dd>
                    <strong>{item.ball}</strong>
                  </dd>
                </dl>
              ))}
            </div>
          ) : (
            ""
          )}

          {chkList ? (
            <div className="box">
              <ul className="chk-list">
                {chkList.map((item, index) => (
                  <li key={index}>
                    <div className="chk-label radio">
                      <input
                        type={item.type}
                        id={`${item.name}_${index}`}
                        name={item.name}
                      />
                      <label htmlFor={`${item.name}_${index}`} className="txt">
                        {item.label}
                      </label>
                    </div>
                  </li>
                ))}
              </ul>
            </div>
          ) : (
            ""
          )}

          {Array.isArray(button) ? (
            <BtnLayout
              attr={button.map((btn, index) => ({
                className: "w100",
                text: btn.text,
                color: btn.color ? btn.color : "btn-primary",
                onClick: btn.onClick || props.onClose,
              }))}
              className="half"
            />
          ) : alert ? (
            <BtnLayout
              attr={[
                {
                  className: "w100",
                  text: button.text,
                  color: button.color ? button.color : "btn-primary",
                  onClick: button.onClick || props.onClose,
                },
              ]}
            />
          ) : (
            <BtnLayout
              attr={[
                {
                  className: "w100",
                  text: "취소",
                  color: "btn-line-white",
                  onClick: props.onClose,
                },
                {
                  className: "w100",
                  text: button.text,
                  color: button.color ? button.color : "btn-primary",
                  onclick: button.onClick,
                },
              ]}

              className="half"
            />
          )}
        </div>
      </div>
    </div>
  );
};

export default Confirm;
