import React from "react";

const Refund = ({ data }) => {
  const { title, date, status, labelColor, reason, info, refundStatus} = data;

  return (
    <div className="bbs-container refund-detail">
      <dl className="detail-wrap">
        <dt>
          <div className="item-flex flex">
            <div className="col-status">
              <span className={`status-label ${labelColor && labelColor}`}>{status}</span>
            </div>
            <div className="col-dec">
              <strong className="tit">{title}</strong>
              <span className="date">{date}</span>
            </div>
          </div>
        </dt>
        <dd>
          <dl className="box">
            <dt>환불사유</dt>
            <dd>
              <strong className="tit">{reason.type}</strong>
              <span className="dec">{reason.dec}</span>
            </dd>
          </dl>
        </dd>
      </dl>

      <dl className="info-wrap">
        <dt>환불안내</dt>
        <dd>
          <dl>
            <dt>구매 상품 정보</dt>
            <dd>{info.prd}</dd>
          </dl>
          <dl>
            <dt>상품 금액</dt>
            <dd>{info.price}뽈</dd>
          </dl>
          <dl>
            <dt>구매 상품 정보</dt>
            <dd>{info.refund}뽈</dd>
          </dl>
        </dd>
      </dl>

      {!refundStatus ? (
        <dl className="box term">
          <dl>
            <dt>환불 정책 안내</dt>
            <dd>
              <p className="dec">
                환불 정책 안내환불 정책 안내환불 정책 안내환불 정책 안내환불 정책 안내환불 정책 안내환불 정책 안내환불 정책 안내환불 정책 안내환불 정책 안내환불 정책 안내환불 정책 안내
              </p>
            </dd>
          </dl>
        </dl>
      ) : (
        <div className="box">
          <strong>환불 신청이 완료된 상태입니다.</strong>
        </div>
      ) }
    </div>
  );
};

export default Refund;
