import React, {useState} from 'react';
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom/cjs/react-router-dom";
import Summary from "components/pages/H-MY/Summary";
import BubbleTab from "components/atom/BubbleTab";
import Button from "components/atom/button/Button";
import FormItem from "components/molecule/form/FormItem";
import FormWrap from "components/molecule/form/FormWrap";
import Select from "components/atom/form/Select";
import BtnLayout from "components/molecule/layout/BtnLayout";
import ButtonAlert from "components/molecule/alert/ButtonAlert";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const popData = [
  {
    text: '연장하기',
    color: "btn-gray",
    size: "btn-xxs",
    alertDatas: {
      alert: "",
      title: "10,000원(3,000뽈)상품을\n선택하셨습니다.",
      titleSize: "14px",
      titleColor: "blue",
      smallTit: "300뽈 서비스로 총 3,300뽈이 충전됩니다.",
      smallTitSize: "14px",
      className: "alert-charge",
      button: {
        text: "확인",
        onClick: "",
      },
    }
  },
  {
    text: 'pop2',
    color: "btn-gray",
    size: "btn-xxs",
    alertDatas: {
      alert:true,
      title:"3,300뽈 충전이 완료되었습니다.",
      className:"alert-charge",
      button: {
        text: "확인",
        onClick: "",
      },
    }
  },
  {
    text: 'pop3',
    color: "btn-gray",
    size: "btn-xxs",
    alertDatas: {
      alert:true,
      title:"1,000뽈 충전이 완료되었습니다.\n뽈 유효기간은 1년입니다.",
      className:"alert-charge",
      button: {
        text: "확인",
        onClick: "",
      },
    }
  },
]

const HmyB2000000_1 = (props) => {
  const fromData = [
    {
      label: "기프트뽈",
      type: "text",
      id : "text",
      name : "text",
      value: "",
      required: "",
      placeholder: "코드를 입력해주세요.",
      groupClassName: "",
      labelClassName: "",
      formClassName: "",
      status: "",
      readOnly: "",
      disabled: "",
      onChange: "",
      onKeyPress: "",
      onKeyUp: "",
      onBlur: "",
      copyToClipboard: "",
      autocomplete: "",
      etcs:{
        tip:"일치하지 않은 코드입니다.",
        tipColor:"c-red",
      }
    },
  ]

  const bubbleTabData = [
    {
      select: "",
      selectedTab : "",
      label: "계좌",
    },
    {
      select: "",
      selectedTab : "",
      label: "카드",
    },
    {
      select: "",
      selectedTab : "",
      label: "휴대폰",
    },
    {
      select: "",
      selectedTab : "기프트뽈",
      label: "기프트뽈",
    },
  ]

  return (
    <>
      <div className="page-my page-wrapper bg-gray t-charge">
        <div className="m-main">
          <div className="my-flex flex">
            <div className="col-summary">
              <Summary />
            </div>

            <div className="col-right">
              <div className="right-outer">
                <div className="bg-box">
                  <header className="right-header">
                    <strong className="tit">뽈 사용내역</strong>
                  </header>
                  <FullHeight attr={{pageHeader:true}}>
                    <ColTop>
                      <div className="top-first">
                        <BubbleTab tabs={bubbleTabData} />
                      </div>

                      <div className="bg-box">
                        <div className="form-container _wrap">
                        { fromData
                          .map((item, index) => (
                            <FormItem
                              key={index}
                              attr={item}
                            />
                          ))
                        }
                      </div>
                      </div>
                    </ColTop>

                    <ColBottom>
                      <div className="bg-box">
                        <BtnLayout
                          attr={[
                            {
                              className : 'w100',
                              text: '뽈 받기',
                              disabled : true,
                              size : 'btn-lg',
                              color : 'btn-primary'
                            },
                          ]}
                          className="full"
                        />
                      </div>
                    </ColBottom>
                  </FullHeight>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="_temp">
        {/*temp*/}
        {popData.map((item, index) => (
          <ButtonAlert attr={item} />
        ))}
      </div>
    </>
  );
};

export default withRouter(HmyB2000000_1);
