import React from "react";
import { Link } from "react-router-dom/cjs/react-router-dom";
import CardList from "components/molecule/list/CardList";
const SearchSection = ({ data }) => {
  return (
    <section className="sec-video">
      {data &&
        data.map((item, index) => (
          <div className={`video-wrapper`}>
            <div className="m-main">
              {item.datas.cardType !== "society" &&
              item.datas.cardType !== "member" ? (
                <header className="list-header flex">
                  <strong>{item.section}</strong>
                  {item.datas.totalCnt > 1 ? (
                    <Link className="more" to={item.datas.moreLink}>
                      더보기
                    </Link>
                  ) : (
                    ""
                  )}
                </header>
              ) : (
                ""
              )}

              <CardList data={item.datas} />
            </div>
          </div>
        ))}
    </section>
  );
};

export default SearchSection;
