import React, {useRef, useState} from "react";
import Button from "components/atom/button/Button";
import SelectSet from "components/molecule/form/SelectSet";
import { toast } from "react-toastify";
import {VodUtilData} from "components/pages/H-VOD/FmData";

const VodUtil = ({ data }) => {
  const { report, share, heart, etc } = data;

  const [heartActive, setHeartActive] = useState(false); // 찜 상태
  const handleHeart = () => {
    let msg = !heartActive ? "찜을 등록했습니다." : "찜을 해제했습니다.";
    toast(msg, {
      position: "top-center",
    });
    setHeartActive(!heartActive);
  };

  return (
    <ul className="ico-ul">
      {report ? (
        <li>
          <SelectSet attr={VodUtilData.report} />
        </li>
      ) : ("")}
      {share ? (
        <li>
          <Button attr={{
            className: "btn-ico ico-share",
            iconOnly : true,
          }} />
        </li>
      ) : ("")}
      {heart ? (
        <li>
          <Button attr={{
            className: "btn-ico ico-heart" + (heartActive ? ' _active' : ""),
            iconOnly : true,
            onClick:handleHeart,
          }} />
        </li>
      ) : ("")}
      {etc ? (
        <li>
          <SelectSet attr={VodUtilData.etc} />
        </li>
      ) : ("")}
    </ul>
  )
};

export default VodUtil;
