import { Link } from "react-router-dom";
const NavLink = ({ attr, onClick }) => {
  return (
    <Link
      to={attr.link ? attr.link : "/"}
      className={`nav_link ${attr.active ? "active" : ""}`}
      onClick={onClick}
    >
      <div className="nav_link_img flexCC">
        <img
          src={attr.active ? attr.active_img : attr.img_src}
          alt=""
          style={attr.img_style}
        />
      </div>
      <p>{attr.title}</p>
    </Link>
  );
};

export default NavLink;
