import React, { useState } from "react";
import { withRouter } from "react-router-dom";

import { societyCardData, societyCardData2 } from "../main/data";
import useMediaQueryHook from "../../../hook/useMediaQuery";
import SocietyCard from "components/molecule/Card/SocietyCard";
import PageHeader from "components/molecule/layout/PageHeader";
import Button from "components/atom/button/Button";
import handleLocation from "handler/handleLocation";
import CardList from "components/molecule/list/CardList";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const societyCard = {
  totalCnt : "9",
  list : societyCardData2,
  cardType : "society"
}

const Hass0202H101 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  return (
    <>
      <div className="teamSetting_wrapper">
        <PageHeader onClick="" title="소속단체관리" noBtn="true" />
        <div className="page-wrapper">
          <FullHeight attr={{pageHeader:true}}>
            <ColTop>
              <div className="card_slider flexStart wrap">
                <CardList data={societyCard}/>
              </div>
            </ColTop>
            <ColBottom>
              <div className="btn_wrapper">
                <Button
                  attr={{
                    className: "w100",
                    text: "등록하기",
                    onClick: handleLocation("/H-TEAM/0703H102"),
                    color: "btn-black",
                    size: "btn-lg",
                  }}
                />
              </div>
            </ColBottom>
          </FullHeight>
        </div>
      </div>
    </>
  );
};

export default withRouter(Hass0202H101);
