/**
 *
 * @param {*} props type : badge 타입 [live / clip / game]
 * @param {*} props style : 스타일
 * @param {*} props text : badge 안 텍스트
 * @param {*} props img : img
 * @param {*} props live : text 옆 live 표시
 */

import { useState } from "react";

const Toggle = ({ initialChecked = false, onChange }) => {
  const [isChecked, setIsChecked] = useState(initialChecked);

  const handleToggle = () => {
    setIsChecked(!isChecked);
    if (onChange) {
      onChange(!isChecked);
    }
  };

  return (
    <div
      className={`toggle ${isChecked ? "checked" : ""}`}
      onClick={handleToggle}
    >
      <div className={`switch ${isChecked ? "checked" : ""}`} />
    </div>
  );
};

export default Toggle;
