import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";

import useMediaQueryHook from "hook/useMediaQuery";
import logo from "assets/images/logo_text_bk 1.png";
import splash from "assets/images/login_splash.png";
import Button from "components/atom/button/Button";
import handleLocation from "handler/handleLocation";

const Hlog00000000 = (props) => {
  const { currentMediaQuery, isMobile } = useMediaQueryHook();

  return (
    <>
      <div className={`page-wrapper loginPage ${isMobile && "splash"} bg-gray`}>
        <section className="login_wrapper sec-height _fix">
          <div className="m-main">
            <div className="col-wrap">
              <div className="login_logo">
                <div className="logo_text">Hogak</div>
                <div>내 경기 실시간 중계</div>
              </div>
              <div className={`login_btn_wrapper ${isMobile && "mobile"}`}>
                <Button
                  attr={{
                    text: "네이버로 로그인",
                    className: `login_btn ${isMobile && "mobile"} naver`,
                    onClick: handleLocation("/H-LOG/01000000"),
                    size:"btn-xl",
                  }}
                />
                <Button
                  attr={{
                    text: "카카오로 로그인",
                    className: `login_btn ${isMobile && "mobile"} kakao`,
                    size:"btn-xl",
                  }}
                />
                <Button
                  attr={{
                    text: "Google로 로그인",
                    className: `login_btn ${isMobile && "mobile"} google`,
                    size:"btn-xl",
                  }}
                />
                <Button
                  attr={{
                    text: "Apple로 로그인",
                    className: `login_btn ${isMobile && "mobile"} apple`,
                    size:"btn-xl",
                  }}
                />
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  );
};

export default withRouter(Hlog00000000);
