import React, {useState} from 'react';
import { withRouter } from "react-router-dom";
import MyInfo from "components/molecule/mypage/MyInfo";
import MyQuick from "components/molecule/mypage/MyQuick";
import MyNotice from "components/molecule/mypage/MyNotice";
import PageFooter from "components/molecule/mypage/PageFooter";
import MyNav from "components/molecule/mypage/MyNav";

//검색창 data
const myInfo = {
  name: "홍길동",
  ball : "20,000",
  season : false,
};

const myInfo2 = {
  name: "홍길동",
  ball : "20,000",
  season : {
    title : "7일시즌권",
    date : "7일시즌권",
  },
};

const Summary = (props) => {
  return (
    <>
      <div className="summary-component">
        <section className="sec-info">
          <MyInfo data={myInfo} />

          <MyInfo data={myInfo2} />

          <div className="m-main">
            <MyQuick />
          </div>
        </section>

        <section className="sec sec-link">
          <div className="m-main">
            <MyNotice />

            <MyNav />
          </div>

          <PageFooter />
        </section>
      </div>
    </>
  );
};

export default Summary;
