import React from "react";
import ClipVod from "components/atom/ClipVod";

const ClipContainer = ({ clipData }) => {
  return (
    <div className="clip-layer">
      <div className="over-wrap">
        <div className="thumb-list">
          {clipData.map((item, index) => (
            <ClipVod
              img={item.img}
              no={index}
            />
          ))}
        </div>
      </div>
    </div>
  )
};

export default ClipContainer;
