import React, {useState, useRef} from 'react';
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom/cjs/react-router-dom";
import Summary from "components/pages/H-MY/Summary";
import BubbleTab from "components/atom/BubbleTab";
import Button from "components/atom/button/Button";
import Input from "components/atom/form/Input";
import ButtonAlert from "components/molecule/alert/ButtonAlert";

const HmyB1000000 = (props) => {
  const [data, setData] = useState({
    ball:"20,000",
    extinction:"100",
    lists:[
      {
        content:"(시즌권) 7일 시즌권",
        ball:"-800",
        date:"2024.10.24",
      },
      {
        content:"(시즌권) 7일 시즌권",
        ball:"-800",
        date:"2024.10.24",
      },
      {
        content:"(시즌권) 7일 시즌권(시즌권) 7일 시즌권(시즌권) 7일 시즌권(시즌권) 7일 시즌권(시즌권) 7일 시즌권",
        ball:"-800",
        date:"2024.10.24",
      },
      {
        content:"(시즌권) 7일 시즌권",
        ball:"-800",
        date:"2024.10.24",
      },{
        content:"(시즌권) 7일 시즌권",
        ball:"-800",
        date:"2024.10.24",
      },
    ]

  });

  //버블탭 selectSet setting
  const selectSetComponentRef = useRef({});
  const selectActiveRef = useRef({});
  const selectChangeHandler = (e) => {
    //BS cloas
    selectSetComponentRef.current.handleBsClose();
    //change label
    let label = e.target.dataset.label;
    let index = e.target.dataset.index;
    let newBubbleTabData = [...bubbleTabData];
    newBubbleTabData[index].label = label;
    setBubbleTabData(newBubbleTabData);
    // tab active
    selectActiveRef.current.handleTabClick({label:label})
  };
  const [bubbleTabData, setBubbleTabData] = useState([
    {
      select: true,
      selectedTab : "전체",
      label: "전체",
      id:"select",
      name:"select",
      onChange:selectChangeHandler,
      options: [
        {
          text: "모두보기",
          type:"radio",
        },
        {
          text: "적립",
          type:"radio",
        },
        {
          text: "충전",
          type:"radio",
        },
        {
          text: "사용",
          type:"radio",
        },
        {
          text: "만료",
          type:"radio",
        },
      ],
      layers : {
        selectType:"btn",
        optionLabel:"선택",
        optionType:"radio",
      },
    },
  ]);

  return (
    <>
      <div className="page-my page-wrapper bg-gray t-balluse">
        <div className="m-main">
          <div className="my-flex flex">
            <div className="col-summary">
              <Summary />
            </div>

            <div className="col-right">
              <div className="right-outer">
                <div className="bg-box">
                  <header className="right-header">
                    <strong className="tit">뽈 사용내역</strong>
                  </header>
                  <section className="sec sec1">
                    <div className="m-main">
                      <dl className="box flex">
                        <dt>내뽈</dt>
                        <dd><strong>{data.ball}</strong> 뽈</dd>
                      </dl>

                      <dl className="extinction flex">
                        <dt>소멸 예정 뽈(15일 내)</dt>
                        <dd>{data.extinction} 뽈</dd>
                      </dl>
                    </div>
                  </section>

                  <section className="sec2">
                    <div className="bg-box">
                      <div className="m-main">
                        <BubbleTab
                          tabs={bubbleTabData}
                          selectSetRef={selectSetComponentRef}
                          ref={selectActiveRef}
                        />

                        <div className="use-container">
                          <ul className="use-list">
                            {data.lists.map((item, index) => (
                              <li key={index}>
                                <dl>
                                  <dt className="flex">
                                    <p className="tit">{item.content}</p>
                                    <p className="ball">{item.ball}</p>
                                  </dt>
                                  <dd>
                                    <span className="date">{item.date}</span>
                                  </dd>
                                </dl>
                              </li>
                            ))}
                          </ul>
                        </div>
                      </div>
                    </div>
                  </section>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default withRouter(HmyB1000000);
