import React, { useState } from "react";
import { withRouter } from "react-router-dom";
import Confirm from "components/molecule/alert/Confirm";
import { toast } from "react-toastify";
import Button from "components/atom/button/Button";

const ConfirmData = [
  {
    popBtn: "해체하기",
    title: "해체하기",
    dec: (
      <>
        팀을 해체할 경우 <br /> 다른 매니저 권한을 모두 해체한 후 <br /> 해체할
        수 있습니다.
      </>
    ),
    nowrap: true,
    alert: true,
    button: {
      text: "확인",
      onClick: "",
    },
  },

  {
    popBtn: "해체하기2",
    title: "해체하기",
    dec: (
      <>
        팀을 해체할 경우 해당 팀페이지와 모든 영상 <br /> 정보는 삭제되며, 팀
        해체와 동시에 <br /> 탈퇴됩니다. 그래도 해체하시겠습니까?
      </>
    ),
    nowrap: true,
    button: [
      {
        text: "취소",
        color: "btn-line-white",
        onClick: "",
      },
      {
        text: "해체하기",
        color: "btn-red",
        onClick: "",
      },
    ],
  },
  {
    popBtn: "탈퇴하기",
    title: "탈퇴하기",
    dec: (
      <>
        팀을 최고 관리자 권한을 대신 할 <br /> 매니저에게 팀 양도 후 탈퇴
        가능합니다.
      </>
    ),
    nowrap: true,
    alert: true,
    button: {
      text: "확인",
      onClick: "",
    },
  },
  {
    popBtn: "탈퇴하기2",
    title: "탈퇴하기",
    dec: (
      <>
        탈퇴 시 업로드하신 영상, 정보 등 <br /> 수정 및 삭제가 불가하며, 권한도
        <br />
        상실하게 됩니다. 그래도 탈퇴하시겠습니까?
      </>
    ),
    nowrap: true,
    button: [
      {
        text: "취소",
        color: "btn-line-white",
        onClick: "",
      },
      {
        text: "탈퇴하기",
        color: "btn-red",
        onClick: "",
      },
    ],
  },
];

const Hteam01000000P1 = (props) => {
  const [confirmOpen, setConfirmOpen] = useState(false);

  const openConfirmHandler = (id) => {
    setConfirmOpen((el) => (el.confirmOpen !== id ? id : ""));
    if (confirmOpen === id) {
      setConfirmOpen(false);
    }
  };

  const handleShowToast = (msg) => {
    return () => toast(msg);
  };

  return (
    <>
      <section className="sec">
        <div className="m-main">
          <ul className="flex">
            {ConfirmData.map((item, index) => (
              <li style={{ marginRight: "5px" }}>
                <button
                  key={index}
                  className="btn"
                  onClick={() => {
                    openConfirmHandler(index);
                  }}
                >
                  {item.popBtn}
                </button>
                {confirmOpen === index ? (
                  <Confirm
                    data={item}
                    onClose={openConfirmHandler}
                    id={index}
                  />
                ) : (
                  ""
                )}
              </li>
            ))}
            <Button
              attr={{
                text: "팀 가입 toast",
                onClick: handleShowToast("가입된 팀원만 확인할 수 있습니다."),
              }}
            />
            <Button
              attr={{
                text: "찜 등록 toast",
                onClick: handleShowToast("찜을 등록했습니다."),
              }}
            />
            <Button
              attr={{
                text: "찜 해제 toast",
                onClick: handleShowToast("찜을 해제했습니다."),
              }}
            />
            <Button
              attr={{
                text: "주소 복사 toast",
                onClick: handleShowToast("주소를 복사했습니다."),
              }}
            />
            <Button
              attr={{
                text: "승인 완료 toast",
                onClick: handleShowToast("승인이 완료되었습니다."),
              }}
            />
            <Button
              attr={{
                text: "승인 거절 toast",
                onClick: handleShowToast("승인이 거절되었습니다."),
              }}
            />
            <Button
              attr={{
                text: "가입 신청 거절 toast",
                onClick: handleShowToast("가입 신청이 거절되었습니다."),
              }}
            />
            <Button
              attr={{
                text: "가입 신청 완료 toast",
                onClick: handleShowToast("가입 신청이 완료되었습니다."),
              }}
            />
          </ul>
        </div>
      </section>
    </>
  );
};

export default withRouter(Hteam01000000P1);
