import no_img from "assets/images/icon/logo_ball.svg";
import arrow from "assets/images/icon/icon_arrow_right_gray.svg";
import Button from "components/atom/button/Button";
import React from "react";

const MemberInfoCard = ({ members, edit, onClick, arrowClick }) => {
  return (
    <div className="mem_infobox">
      {edit && (
        <div className={`edit_btn ${arrowClick && "arrow"}`}>
          <Button
            attr={{
              className: `ico-gray-edit`,
              iconOnly: true,
              onClick: onClick,
            }}
          />
        </div>
      )}
      <div className={`container ${members?.length > 1 ? "flexCC" : ""}`}>
        {members.map((member, index) => (
          <React.Fragment key={index}>
            <div className="mam_wrapper">
              <div className="logo_ball">
                <img src={member.img ? member.img : no_img} alt="member" />
              </div>
              <div className="mem_info">
                <div className="name">
                  <span className="name_span">{member.name + " 님"}</span>
                </div>
                <div className="position flexCC">{member.position}</div>
              </div>
            </div>

            {index < members.length - 1 && <div className="line" />}
          </React.Fragment>
        ))}
        {arrowClick && (
          <div className="arrow_wrapper">
            <Button
              attr={{
                className: `btn-gray-next`,
                iconOnly: true,
                onClick: arrowClick,
              }}
            />
          </div>
        )}
      </div>
    </div>
  );
};
export default MemberInfoCard;
