import React from "react";
import ThumbVod from "components/atom/ThumbVod";
import Button from "components/atom/button/Button";

const MultiContainer = ({ multiData, close }) => {
  function handleCloseClick() {
    close();
  }

  return (
    <div className="multi-layer">
      <ul className="thumb-list">
        {multiData.map((item, index) => (
          <li key={index}>
            <ThumbVod
              title={item.title}
              img={item.img}
              status={item.status}
              carmera={item.carmera}
              view={item.view}
            />
          </li>
        ))}
      </ul>
      <Button
        attr={{
          className : `ico-down`,
          text: '닫기',
          iconOnly: true,
          onClick : handleCloseClick,
        }} />
    </div>
  )
};

export default MultiContainer;
