import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";

import useMediaQueryHook from "hook/useMediaQuery";
import Button from "components/atom/button/Button";
import PageHeader from "components/molecule/layout/PageHeader";
import BbsCard from "components/molecule/Card/BbsCard";
import handleLocation from "handler/handleLocation";
import CustomList from "components/organism/table/CustomList";
import BtnLayout from "components/molecule/layout/BtnLayout";
import Confirm from "components/molecule/alert/Confirm";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const Hgrp0203H330 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); // 이전 스크롤 위치
  const [confirmOpen, setConfirmOpen] = useState(false);

  const openConfirmHandler = (id) => {
    setConfirmOpen(!confirmOpen);
  };

  const handleScroll = () => {
    const currentScrollY = window.scrollY;
    setScrollY(currentScrollY);

    if (currentScrollY > prevScrollY && currentScrollY > 100) {
      setIsExpanded(false);
    } else if (currentScrollY < prevScrollY) {
      setIsExpanded(true);
    }

    setPrevScrollY(currentScrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollY]);

  const dataItem = [
    {
      datas: [
        {
          content: "공지사항 제목",
          className: "border_bt",
          addText: "2024/07/07 18:45",
          link: "/H-TEAM/0703H100",
        },
      ],
    },
  ];

  const [data, setData] = useState({
    title: "공지사항 제목",
    className: "border_bt",
    addText: "2024/07/07 18:45",
    link: "/H-GRP/0203H310",
    content:
      "내용내용 공지사항 내용내용 공지사항공지사항 내용내용 공지사항 내용내용 공지사항 내용내용 공지사항 내용내용 공지사항 내용내용 공지사항 내용내용 공지사항 내용내용 공지사항 내용내용 공지사항 내용내용 공지사항 내용내용내용내용 공지사항 내용내용 공지사항공지사항 내용내용 공지사항 내용내용 공지사항 내용내용 공지사항 내용내용 공지사항 내용내용 공지사항 내용내용 공지사항 내용내용 공지사항 내용내용 공지사항 내용내용 공지사항 내용내용내용내용 공지사항 내",
  });

  const btnData2 = [
    {
      className: "w100",
      text: "삭제",
      size: "btn-lg",
      color: "btn-red",
      onClick: openConfirmHandler,
    },
    {
      className: "w100",
      text: "수정",
      size: "btn-lg",
      color: "btn-black",
      onClick: "",
    },
  ];

  const ConfirmData = {
    title: "공지사항 삭제",
    dec: (
      <>
        공지사항을 삭제하시겠습니까? <br /> 삭제 시 해당 공지사항은 복구할 수
        없습니다.
      </>
    ),
    nowrap: true,
    button: [
      {
        text: "취소",
        color: "btn-line-white",
        onClick: "",
      },
      {
        text: "삭제하기",
        color: "btn-red",
        onClick: "",
      },
    ],
  };

  return (
    <>
      <div className="teamSetting_wrapper">
        <PageHeader onClick="" title="공지사항" noBtn="true" />
        <div className="page page-wrapper">
          <FullHeight attr={{pageHeader:true}}>
            <ColTop>
              <div className="notice_wrapper">
                <div className="notice_container">
                  <div className="notice_title">
                    <div className="title">{data.title}</div>
                    {data.addText && <p className="addText">{data.addText}</p>}
                  </div>
                  <div className="notice_content">{data.content}</div>
                </div>
              </div>
            </ColTop>
            <ColBottom>
              <div className="btn_wrapper">
                <BtnLayout attr={btnData2} className="half" />
              </div>
            </ColBottom>
          </FullHeight>
        </div>
      </div>
      {confirmOpen && (
        <Confirm data={ConfirmData} onClose={openConfirmHandler} />
      )}
    </>
  );
};

export default withRouter(Hgrp0203H330);
