import Toggle from "components/atom/Toggle";
import { Link } from "react-router-dom/cjs/react-router-dom";
import arrow from "../../../assets/images/icon/icon_arrow_right_gray.svg";
import no_img from "assets/images/icon/logo_ball.svg";
import Button from "components/atom/button/Button";

const MemberList = ({ dataItem }) => {
  return (
    <div className="mem_list">
      <div className="mem_list_container">
        {dataItem.members.map((member, index) => (
          <>
            <div className="mam_wrapper">
              <div className="logo_ball">
                <img src={member.img ? member.img : no_img} alt="member" />
              </div>
              <div className="mem_info">
                <div className="name">
                  <span className="name_span">
                    {member.name + "님"}
                    <span className="position">{member.position}</span>
                  </span>
                  {member.badge && (
                    <div className="badge flexCC">{member.badge}</div>
                  )}
                </div>
                {member.btnProps && <Button attr={member.btnProps} />}
              </div>
            </div>
            {index < dataItem.members.length - 1 && <div className="line" />}
          </>
        ))}
      </div>
    </div>
  );
};

export default MemberList;
