import React, { useState } from "react";
import Button from "components/atom/button/Button";
import Radio from "components/molecule/form/Radio";
import handleDate from "handler/handleDate";
import "react-datetime/css/react-datetime.css";
import Datetime from "react-datetime";

const radioData = [
  {
    id:"radio",
    label:"1개월",
    name:"radio",
    className:"type-btn",
    value:"",
    ref:"",
    onChange:"",
    disabled:"",
    checked:"",
  },
  {
    id:"radio",
    label:"3개월",
    name:"radio",
    className:"type-btn",
    value:"",
    ref:"",
    onChange:"",
    disabled:"",
    checked:"",
  },
  {
    id:"radio",
    label:"1년",
    name:"radio",
    className:"type-btn",
    value:"",
    ref:"",
    onChange:"",
    disabled:"",
    checked:"",
  }
]
const TermSearch = () => {
  const [data, setData] = useState({
    nowmonth : "2024년 10월"
  })

  return (
    <div className="term-search">
      <div className="form-group">
        <ul className="top-flex flex">
          {radioData.map((item, index) => (
            <li key={index}>
              <Radio attr={item} />
            </li>
          ))}
        </ul>
        <div className="form-wrap _arrow">
          <Datetime value={data.nowmonth} closeOnSelect={true} inputProps={{
            closeOnSelect:true,
          }}/>
        </div>

        <p className="dec">이번달로부터 1년 이내로 조회 가능합니다.</p>
      </div>
    </div>
  );
};

export default TermSearch;
