import React from 'react';

const TabItem = ({ label, attr, activeTab, onClick }) => {
  const handleClick = () => {
    onClick(label);
  };

  return (
    <div
      className={`tab ${activeTab === label ? 'active' : ''}`}
      onClick={handleClick}
    >
      {label}
    </div>
  );
};

export default TabItem;