import React, { useState } from "react";
import useMediaQueryHook from "../../../hook/useMediaQuery";
import Sticky from 'react-sticky-el';

const Tabs = ({ tabs, sticky }) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();
  const [activeTab, setActiveTab] = useState(tabs[0].label);

  const handleTabClick = (tabLabel) => {
    setActiveTab(tabLabel);
  };

  return (
    <div className="tabs">
      {isMobile && sticky ? (
        <Sticky>
          <ul className={`tab-list`}>
            {tabs.map((tab) => (
              <li
                key={tab.label}
                className={`tab-item ${activeTab === tab.label ? "active" : ""}`}
                onClick={() => handleTabClick(tab.label)}
              >
                {tab.label}
              </li>
            ))}
          </ul>
        </Sticky>
      ) : (
        <ul className={`tab-list`}>
          {tabs.map((tab) => (
            <li
              key={tab.label}
              className={`tab-item ${activeTab === tab.label ? "active" : ""}`}
              onClick={() => handleTabClick(tab.label)}
            >
              {tab.label}
            </li>
          ))}
        </ul>
      )}
      <div className="tab-content">
        {tabs.map((tab) =>
          activeTab === tab.label ? (
            <div className="tab-wrapper" key={tab.label}>
              {tab.content}
            </div>
          ) : null
        )}
      </div>
    </div>
  );
};

export default Tabs;
