import Badge from "components/atom/Badge";
import TeamItem from "components/molecule/Card/TeamItem";

import camera from "assets/images/icon/ico_lcarmera.svg";
import ball from "assets/images/card/icon_ball.svg";

import useMediaQueryHook from "hook/useMediaQuery";

const GameScheduleCard = ({
  title,
  status,

  team1,
  team2,
  stadium,
  score,
  date,
  calc,
  onClick,
}) => {
  const { currentMediaQuery, isMobile } = useMediaQueryHook();
  return (
    <div
      className={`card_wrapper ${calc && "calc"} gameSchedule `}
      onClick={onClick}
    >
      <ul className="card_top gameSchedule">
        <li className="col-left">
          {status === "live" && <img src={camera} />}

          <div className="card_title">{title}</div>
        </li>
        <li className="live_icon">
          <dl className="game-info flexCC">
            <dd className="flexCC">
              <span className="stadium flexCC">{stadium}</span>
            </dd>
          </dl>
        </li>
      </ul>
      <div className="card_bottom gameSchedule">
        <TeamItem teamLogo={team1.teamLogo} teamName={team1.teamName} />
        <div className="score_wrapper">
          {/* <div className="stadium">{stadium}</div> */}
          {status === "cancel" ? (
            <div className="game_score cancel flexCC">경기취소</div>
          ) : status === "schedule" && isMobile ? (
            <div className="game_score flexCC">
              {" "}
              <div className="schedule_date">{date}</div>
            </div>
          ) : (
            <div className="game_score flexCC">
              <span
                className={`score ${team1.score > team2.score ? "_win" : ""}`}
              >
                {team1.score}
              </span>
              <span>:</span>
              <span
                className={`score ${team1.score < team2.score ? "_win" : ""}`}
              >
                {team2.score}
              </span>
            </div>
          )}
          {status !== "schedule" && <div className="game_date">{date}</div>}
        </div>
        <TeamItem
          teamLogo={team2.teamLogo}
          teamName={team2.teamName}
          // reverse={!isMobile ? true : false}
        />
        {isMobile && <img className="event" src={ball} />}
      </div>
    </div>
  );
};

export default GameScheduleCard;
