import React from "react";
import Button from "components/atom/button/Button";
import clogo from "assets/images/icon/ico_clogo.svg";

const AlertPopup = ({
  logo,
  title,
  message,
  buttonText,
  buttonText1,
  buttonText2,
  onButtonClick,
  onButton1Click,
  onButton2Click,
  additionalText,
  nowrap,
  children,
  className,
}) => {
  return (
    <div className="alert-popup">
      <div className={`alert-content ${className ? className : ""}`}>
        {children ? (
          children
        ) : (
          <>
            {logo && <img src={clogo} alt="Logo" className="alert-logo" />}

            {title && <h2 className="alert-title">{title}</h2>}

            {message && (
              <p className={`alert-message ${nowrap && "nowrap"}`}>{message}</p>
            )}

            {additionalText && (
              <div className="additional-text-container">
                <p className="additional-text">{additionalText}</p>
              </div>
            )}

            <div className="button-container">
              {buttonText && (
                <Button
                  attr={{
                    text: buttonText,
                    className: "alert-button",
                    size: "btn-lg",
                    color: "btn-primary",
                    onClick: onButtonClick,
                  }}
                />
              )}
              {(buttonText1 || buttonText2) && (
                <>
                  <Button
                    attr={{
                      text: buttonText1,
                      className: "alert-button",
                      size: "btn-lg",
                      color: "btn-line-white",
                      onClick: onButton1Click,
                    }}
                  />

                  <Button
                    attr={{
                      text: buttonText2,
                      className: "alert-button",
                      size: "btn-lg",
                      color: "btn-primary",
                      onClick: onButton2Click,
                    }}
                  />
                </>
              )}
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default AlertPopup;
