import React from "react";
import Button from "components/atom/button/Button";

const MyInfo = ({data}) => {
  const {name, ball, season} = data;

  return (
    <div className="my-info">
      <div className="m-main">
        <div className="info-top">
          <storng className="name">{name}님!</storng>
          <p className="dec">오늘도 호각과 함께 즐거운 스포츠 하루되세요!</p>
        </div>
        <div className="ball-box">
          <div className="ball-flex flex">
            <strong className="ico ball more">{ball} <small>뽈</small></strong>
            <Button
              attr={{
                className: "btn-charge",
                text: "충전",
                icon : "charge",
                onClick: "",
                color:"btn-primary",
              }}
            />
          </div>
          {!season ? (
            <Button
              attr={{
                className: "btn-season",
                icon : "season more",
                text: "시즌권 구매 ",
                onClick: "",
                color:"btn-line-black",
                size: "btn-xxs",
              }}
            />
          ) : (
            <dl className="btt-flex flex">
              <dt>
                <span className="btn-season">
                  <span className="txt ico season">{season.title}</span>
                </span>
              </dt>
              <dd><span className="more c-black">{season.date}</span></dd>
            </dl>
          )}
        </div>
      </div>
    </div>
  );
};

export default MyInfo;
