import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";

import useMediaQueryHook from "hook/useMediaQuery";

import map from "assets/images/example/exMap.png";
import copy from "assets/images/icon/ico_copy.svg";
import phone from "assets/images/icon/ico_phone.svg";
import bschk from "assets/images/icon/Icon_bschk.svg";
import logo from "assets/images/example/city_logo.png";

import AppHeader from "components/organism/header/AppHeader";
import Button from "components/atom/button/Button";
import FormItem from "components/molecule/form/FormItem";
import handleChange from "handler/handleChange";
import { Link } from "react-router-dom/cjs/react-router-dom.min";
import BtnLayout from "components/molecule/layout/BtnLayout";
import Map from "components/molecule/Map/Map";

const Hgch03000000 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); // 이전 스크롤 위치
  const [state, setState] = useState("manager"); // user state
  const [edit, setEdit] = useState(false); // 수정 ui 변경 state
  const [isOpen, setIsOpen] = useState(false); // 아코디언 상태 관리
  const maxLength = 65;

  const handleScroll = () => {
    const currentScrollY = window.scrollY;
    setScrollY(currentScrollY);

    if (currentScrollY > prevScrollY && currentScrollY > 100) {
      setIsExpanded(false);
    } else if (currentScrollY < prevScrollY) {
      setIsExpanded(true);
    }

    setPrevScrollY(currentScrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollY]);

  console.log("isExpanded", isExpanded);

  const handleToggle = () => {
    setIsOpen(!isOpen); // 아코디언 열림/닫힘 토글
  };

  const handleEdit = () => {
    setEdit(!edit); // 수정 ui 변경
  };

  const [data, setData] = useState({
    name: "호각 아카데미 본점",
    logo: logo,
    member: 30,
    video: 182,
    phone: "02-262-7899",
    officeAddress: "서울시 송파구 북항로 32번길 66 우앤이건물 201호",
    stadiumAddress: "서울특별시 노원구 화랑대로 223",
    website: "https://aisportstv.co.kr/",
    association:
      "대한야구소프트볼협회 U-13 클럽 정식 승인 다양한 팀구성 - U13팀(선수반) 새싹팀, 꿈 나무팀, 유소년팀, 주니어팀으로 구성",
    foundation: "2011년 창단",
    manager: "홍길동 감독",
    trainingTime: "- 화,수,목,금 16:00~18:30 <br/> - 주말 14:30~17:00 ",
    trainingSite: "청라공촌유수지야구장 / 전용 실내야구연습장",
  });

  const infoForm = [
    {
      label: "전화번호",
      type: "text",
      id: "phone",
      name: "phone",
      value: data.phone,
      required: "",
      placeholder: "",
      status: "",
      checked: "",
      readOnly: "",
      disabled: "",
      onChange: (e) => handleChange(e, data, setData),
      onKeyPress: "",
      onKeyUp: "",
      onBlur: "",
      copyToClipboard: "",
      autocomplete: "",
      formBtns: {
        noBtn: "",
        use: "",
        type: "",
        text: "",
        className: "",
        onClick: "",
        disabled: "",
      },
    },
    {
      label: "사무실 주소",
      id: "officeAddress",
      name: "officeAddress",
      type: "text",
      value: data.officeAddress,
      onChange: (e) => handleChange(e, data, setData),
      link: {
        pathname: "/H-GCH/03020201",
        label: data.name,
        name: "사무실 주소",
      },
    },
    {
      label: "구장 주소",
      id: "stadiumAddress",
      name: "stadiumAddress",
      type: "text",
      value: data.stadiumAddress,
      onChange: (e) => handleChange(e, data, setData),
      link: {
        pathname: "/H-GCH/03020201",
        label: data.name,
        name: "구장 주소",
      },
    },
    {
      label: "홈페이지",
      type: "text",
      id: "website",
      name: "website",
      value: data.website,
      required: "",
      placeholder: "",
      status: "",
      checked: "",
      readOnly: "",
      disabled: "",
      onChange: (e) => handleChange(e, data, setData),
      onKeyPress: "",
      onKeyUp: "",
      onBlur: "",
      copyToClipboard: "",
      autocomplete: "",
      formBtns: {
        noBtn: "",
        use: "",
        type: "",
        text: "",
        className: "",
        onClick: "",
        disabled: "",
      },
    },
    {
      label: "팀 설명",
      type: "textarea",
      id: "association",
      name: "association",
      value: data.association,
      required: "",
      placeholder: "",
      status: "",
      checked: "",
      readOnly: "",
      disabled: "",
      onChange: (e) => handleChange(e, data, setData),
      onKeyPress: "",
      onKeyUp: "",
      onBlur: "",
      copyToClipboard: "",
      autocomplete: "",
      maxLength: "300",
      etcs: {
        tip: "",
        tipSize: "",
        tipColor: "",
        length: "0",
        maxLength: "300",
        lengthPosition: "bottom",
        validMsg: "",
        inValidMsg: "",
      },
    },
  ];

  return (
    <>
      {/* {isMobile && (
        <AppHeader className={"content"} pageTitle={data.name} />
      )} */}
      <div className="subHome_wrapper ass">
        {!isMobile && !edit ? (
          <div className="teamInfo_Page flexCC">단체정보</div>
        ) : !isMobile && edit ? (
          <div className="teamInfo_Page flexCC">{data.name}</div>
        ) : (
          ""
        )}

        <div className={`${!isMobile && "m-main"}`}>
          {!edit && (
            <section className="subHome_section teamInfo_map flexCC">
              <div className="map_wrapper flexCC">
                {/* <img src={map} /> */}
                <Map />
              </div>
            </section>
          )}
        </div>
        <section className="subHome_section teamInfo flexCC">
          {!edit && (
            <div className="team_info_chk flexCC">
              <div className="team_info_content">
                <div className="team_image_wrapper">
                  <img src={data.logo} alt="팀 로고" />
                  <img className="chk" src={bschk} alt="체크 아이콘" />
                </div>
                <span>{data.name}</span>
              </div>
              <Button
                attr={{
                  className: `ico-share`,
                  text: "공유하기",
                  iconOnly: true,
                }}
              />
            </div>
          )}
          <div className={`team_inforamtion ${edit && "edit"}`}>
            {/* edit이 false일 때 팀 정보 표시 */}
            {!edit ? (
              <>
                <div className="info-row">
                  <span className="label">전화번호</span>
                  {isMobile ? (
                    <a href="" className="info">
                      {data.phone}
                      <img src={phone} />
                    </a>
                  ) : (
                    <div className="info">{data.phone}</div>
                  )}
                </div>
                <div className="info-row">
                  <span className="label">사무실 주소</span>
                  <span className="info-btn">
                    {data.officeAddress}{" "}
                    <Button
                      attr={{
                        className: `ico-copy`,
                        iconOnly: true,
                      }}
                    />
                  </span>
                </div>
                <div className="info-row">
                  <span className="label">구장 주소</span>
                  <span className="info-btn">
                    {data.stadiumAddress}{" "}
                    <Button
                      attr={{
                        className: `ico-copy`,
                        iconOnly: true,
                      }}
                    />
                  </span>
                </div>
                <div className="info-row">
                  <span className="label">홈페이지</span>
                  <a href={data.website} className="info">
                    {data.website}
                  </a>
                </div>
                <div className="info-row">
                  <span className="label-toggle">
                    단체 설명{" "}
                    <span className="toggle-icon" onClick={handleToggle}>
                      {isOpen || data.association.length >= maxLength ? (
                        <Button
                          attr={{
                            className: isOpen ? `ico-dn` : `ico-up`,
                            iconOnly: true,
                          }}
                        />
                      ) : (
                        ""
                      )}
                    </span>
                  </span>
                  <div className="info" style={{ cursor: "pointer" }}>
                    {isOpen || data.association.length <= maxLength ? (
                      data.association
                    ) : (
                      <>{data.association.substring(0, maxLength)}...</>
                    )}
                  </div>
                </div>
              </>
            ) : (
              // edit이 true일 때 form 표시
              <div className={`join_term_wrapper ${isMobile && "mobile"}`}>
                <div className="form-box">
                  <div className="form-container">
                    {infoForm.map((item, index) => (
                      <FormItem key={index} attr={item} />
                    ))}
                    {/*<div className="col-right">
                      <span>0</span>/30
                    </div>*/}
                  </div>
                </div>
              </div>
            )}
          </div>
        </section>

        <section className="subHome_section etcInfo ass">
          <div className="etcInfo_wrapper">
            <div className="etcInfo_container">
              <div className="ico logo">
                단체 정보{" "}
                <div className={edit && "more"}>
                  {edit && (
                    <Link
                      to={{
                        pathname: "/H-GCH/03020200",
                        state: {
                          label: "호각 아카데미 본점",
                        },
                      }}
                    >
                      수정하기{" "}
                    </Link>
                  )}
                </div>
              </div>
              <div className="etcInfo_content">
                <div>
                  <span className="label">설립</span>
                  <span className="info">{data.foundation} </span>
                </div>
                <div>
                  <span className="label">감독</span>
                  <span className="info">{data.manager} </span>
                </div>
              </div>
            </div>
          </div>
          {state === "manager" && !edit && (
            <div className="fixed-button ass">
              <Button
                attr={{
                  className: `ico-edit`,
                  iconOnly: true,
                  onClick: handleEdit,
                }}
              />
            </div>
          )}
        </section>
      </div>

      {edit && (
        <div className={`${isMobile ? "fixed-save-button" : "save-button"}`}>
          <BtnLayout
            attr={[
              {
                className: "w100",
                text: "저장하기",
                size: "btn-lg",
                color: "btn-primary",
              },
            ]}
            className="full"
          />
        </div>
      )}
    </>
  );
};

export default withRouter(Hgch03000000);
