const Hmain05000000 = (props) => {
  const [selectVisible, setSelectVisible] = useState(false);
  const openSelectHandler = () => {
    setSelectVisible(!selectVisible);
  };

  //버블탭 selectSet setting
  const selectSetComponentRef = useRef({});
  const selectActiveRef = useRef({});
  const selectChangeHandler = (e) => {
    //BS cloas
    selectSetComponentRef.current.handleBsClose();
    //change label
    let label = e.target.dataset.label;
    let index = e.target.dataset.index;
    let newBubbleTabData = [...bubbleTabData];
    newBubbleTabData[index].label = label;
    setBubbleTabData(newBubbleTabData);
    // tab active
    selectActiveRef.current.handleTabClick({label:label})
  };
  const [bubbleTabData, setBubbleTabData] = useState([
    {
      select: true,
      id: "bubbleTabID",
      name: "bubbleTab",
      selectedTab: "영상",
      label: "영상",
      onChange:selectChangeHandler,
      options: [
        {
          text: "모두보기",
          value: "",
          type:"radio",
        },
        {
          text: "라이브",
          value: "",
          type:"radio",
        },
        {
          text: "방송예정",
          value: "",
          type:"radio",
        },
        {
          text: "지난경기영상",
          value: "",
          type:"radio",
        },
        {
          text: "클립",
          value: "",
          type:"radio",
        },
      ],
      layers: {
        selectType: "",
        optionLabel: "영상",
      },
    },
    { label: "팀" },
    { label: "대회" },
    { label: "단체" },
  ]);

  return (
    <>
      <div className="page-search">
        <div className="search-container">
          <div className="m-main">
            <SearchForm latelyTag={latelyTag} onClick={openSelectHandler} />

            <BubbleTab
              tabs={bubbleTabData}
              selectSetRef={selectSetComponentRef}
              ref={selectActiveRef}
            />
          </div>
        </div>

        <section className="sec-kv">
          <div className="img-box" style={{ backgroundColor: "#002365" }}></div>
        </section>

        <SearchSection data={secData} />

        <ResultSection data={reseltData} />
      </div>
    </>
  );
};

export default withRouter(Hmain05000000);
