import React, { useEffect, useState } from "react";
import {
  useHistory,
  useLocation,
  Link,
} from "react-router-dom/cjs/react-router-dom.js";
import left_arrow from "assets/images/icon/icon_arrow_left.svg";
import left_arrow_w from "assets/images/icon/icon_arrow_left_white.svg"; // 흰색 화살표 아이콘
import home from "assets/images/icon/Icon_mhome.svg";
import home_b from "assets/images/icon/Icon_chome.svg"; // 흰색 홈 아이콘
import bell from "assets/images/icon/Bell_w.svg";
import bell_b from "assets/images/icon/Icon_cnotice.svg"; // 흰색 벨 아이콘
import { PageSettings } from "../../../config/page-settings.js";

function AppHeader({ title, className, pageTitle, scrolled }) {
  const [isScrolled, setIsScrolled] = useState(false); // 스크롤 상태
  const history = useHistory();
  const location = useLocation();

  const trimmedTitle =
    title?.length > 15 ? `${title.substring(0, 15)}...` : title;
  const trimmedPageTitle =
    pageTitle?.length > 15 ? `${pageTitle.substring(0, 15)}...` : pageTitle;

  const handleBackClick = () => {
    history.goBack(); // 이전 페이지로 이동
  };

  const handleScroll = () => {
    const scrollY = window.scrollY;
    setIsScrolled(scrollY > 0); // 스크롤이 0보다 크면 true
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  // 기본 아이콘 설정
  const arrowIcon =
    className === "content"
      ? scrolled && isScrolled
        ? left_arrow
        : left_arrow_w
      : left_arrow;
  const homeIcon =
    className === "content" ? (scrolled && isScrolled ? home : home_b) : null;
  const bellIcon =
    className === "content" ? (scrolled && isScrolled ? bell : bell_b) : null;

  return (
    <PageSettings.Consumer>
      {() => (
        <div
          id="header"
          className={`appHeader navbar-default flexSbC ${className} ${
            scrolled && isScrolled ? "scrolled" : ""
          }`}
        >
          <div
            className="logo"
            onClick={handleBackClick}
            style={{ cursor: "pointer" }}
          >
            <img src={arrowIcon} alt="" />
          </div>
          {/* {scrolled && isScrolled && (
            <div className="title">{!pageTitle ? title : pageTitle}</div>
          )} */}
          {className !== "content" ? (
            <div className="title">
              {!trimmedPageTitle ? trimmedTitle : trimmedPageTitle}
            </div>
          ) : (
            scrolled &&
            isScrolled && (
              <div className="title">
                {!trimmedPageTitle ? trimmedTitle : trimmedPageTitle}
              </div>
            )
          )}
          {className === "content" ? (
            <div
              className={`wrap_service ${
                scrolled && isScrolled ? "scrolled" : ""
              }`}
            >
              <Link to="/main">
                <img
                  src={homeIcon}
                  alt=""
                  className={
                    scrolled && isScrolled ? "icon-small" : "icon-large"
                  }
                />
              </Link>
              <Link to="/notifications">
                <img
                  src={bellIcon}
                  alt=""
                  className={
                    scrolled && isScrolled ? "icon-small" : "icon-large"
                  }
                />
              </Link>
            </div>
          ) : (
            <div className="wrap_service"></div>
          )}
        </div>
      )}
    </PageSettings.Consumer>
  );
}

export default AppHeader;
