import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";

import useMediaQueryHook from "hook/useMediaQuery";

import logo from "assets/images/example/ex_teamlogo.png";
import player from "assets/images/example/team_member.png";
import Button from "components/atom/button/Button";
import PageHeader from "components/molecule/layout/PageHeader";
import MemberInfoCard from "components/molecule/Card/MemberInfoCard";
import CustomList from "components/organism/table/CustomList";
import Confirm from "components/molecule/alert/Confirm";
import handleLocation from "handler/handleLocation";

import AlertPopup from "components/organism/popup/AlertPopup";
import DepthRow from "components/molecule/layer/DepthRow";
import DepthItem from "components/molecule/layer/DepthItem";

const Hgch01000000 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); // 이전 스크롤 위치

  const [isOpen, setIsOpen] = useState(false);
  const [showAlert, setShowAlert] = useState(false);

  const [confirmOpen, setConfirmOpen] = useState(false);

  const openConfirmHandler = (id) => {
    setConfirmOpen(!confirmOpen);
  };

  const handleScroll = () => {
    const currentScrollY = window.scrollY;
    setScrollY(currentScrollY);

    if (currentScrollY > prevScrollY && currentScrollY > 100) {
      setIsExpanded(false);
    } else if (currentScrollY < prevScrollY) {
      setIsExpanded(true);
    }

    setPrevScrollY(currentScrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollY]);

  const handleTClick = () => {
    setConfirmOpen(true);
  };

  const members = [{ name: "홍길동", position: "매니저" }];

  const [data, setData] = useState({
    name: "호각 아카데미 본점",
    logo: logo,
    type: "parent",
    playerName: "김호각",
    position: "투수",
    school: "경원초등학교",
    birthdate: "2010/10/11",
    backNumber: "",
    nickName: "",
    members: members,
  });

  const dataItem = [
    {
      title: "홈/경기 정보",
      datas: [
        {
          content: "홈 정보수정",
          className: "",
          arrow: true,
          link: "/H-GCH/0103H210",
        },
        {
          content: "공지사항",
          className: "",
          arrow: true,
          link: "/H-GCH/0103H300",
        },
      ],
    },
  ];

  const ConfirmData = {
    title: "탈퇴하기",
    dec: (
      <>
        탈퇴 시 업로드 하신 영상, 정보 등 수정 및 <br />
        삭제가 불가합니다. 그래도 탈퇴하시겠습니까?
      </>
    ),
    nowrap: true,
    button: [
      {
        text: "취소",
        color: "btn-line-white",
        onClick: openConfirmHandler,
      },
      {
        text: "탈퇴하기",
        color: "btn-red",
        onClick: () => {
          openConfirmHandler();
        },
      },
    ],
  };

  return (
    <>
      <div className="teamSetting_wrapper_nomal grp">
        <PageHeader onClick="" title={data.name} noBtn="true" />

        <div className="page page-shoot">
          <section className="sec">
            <div className={`${isMobile ? "m-main" : ""}`}>
              <MemberInfoCard members={data.members} />
              <section className="subHome_section teamJoin">
                <CustomList dataItem={dataItem} />
              </section>
            </div>
          </section>
          <div className="btn_wrapper_bt">
            <Button
              attr={{
                className: "w100",
                text: "탈퇴하기",
                onClick: () => handleTClick(),
                color: "btn-line-white-nb",
                size: "btn-lg",
                out: true,
              }}
            />
          </div>
        </div>
      </div>
      {confirmOpen && (
        <Confirm data={ConfirmData} onClose={openConfirmHandler} />
      )}
    </>
  );
};

export default withRouter(Hgch01000000);
