import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";

import useMediaQueryHook from "hook/useMediaQuery";

import player from "assets/images/example/team_member.png";
import FormItem from "components/molecule/form/FormItem";
import BtnLayout from "components/molecule/layout/BtnLayout";
import handleChange from "handler/handleChange";
import Confirm from "components/molecule/alert/Confirm";
import PageHeader from "components/molecule/layout/PageHeader";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const Hteam05010200 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const [confirmOpen, setConfirmOpen] = useState(false);

  const openConfirmHandler = (id) => {
    setConfirmOpen(!confirmOpen);
  };

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); // 이전 스크롤 위치

  const handleScroll = () => {
    const currentScrollY = window.scrollY;
    setScrollY(currentScrollY);

    if (currentScrollY > prevScrollY && currentScrollY > 100) {
      setIsExpanded(false);
    } else if (currentScrollY < prevScrollY) {
      setIsExpanded(true);
    }

    setPrevScrollY(currentScrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollY]);

  const [data, setData] = useState({
    type: "player",
    img: player,
    name: "홍길동",
    position: "투수",
    school: "경원초등학교",
    birthdate: "2010/10/11",
    backNumber: "12",
  });

  const ConfirmData = {
    title: "강퇴시키기",
    dec: (
      <>
        {data.name} 님을 강퇴시키겠습니까? <br /> 강퇴 시 해당 팀에 다시 가입할
        수 없습니다. <br /> 강퇴 해제 시 재가입 가능합니다.
      </>
    ),
    nowrap: true,
    button: [
      {
        text: "취소",
        color: "btn-line-white",
        onClick: () => openConfirmHandler(),
      },
      {
        text: "강퇴하기",
        color: "btn-red",
        onClick: () => openConfirmHandler(),
      },
    ],
  };

  const formGroup = [
    {
      label: "번호",
      type: "text",
      id: "backNumber",
      name: "backNumber",
      required: true,
      value: data.backNumber,
      onChange: (e) => handleChange(e, data, setData),
      placeholder: "번호",
      etcs: {
        tip: "",
        tipSize: "",
        tipColor: "",
        length: "",
        maxLength: "",
        lengthPosition: "",
        validMsg: "",
        inValidMsg: "",
      },
      formBtns: {
        noBtn : "",
        use: "",
        type: "",
        text: "",
        className: "",
        onClick: "",
        disabled: "",
      },
    },
    {
      label: "포지션",
      type: "select",
      id: "position",
      name: "position",
      required: true,
      options: [
        { value: "1", text: "투수" },
        { value: "2", text: "포수" },
        { value: "3", text: "타자" },
        { value: "4", text: "멀티" },
        { value: "5", text: "미정" },
      ],
      onChange: "",
      placeholder: "포지션을 선택해주세요.",
    },
  ];

  const btnData2 = [
    {
      className: "w100",
      text: "강퇴",
      size: "btn-lg",
      color: "btn-red",
      onClick: () => openConfirmHandler(),
    },
    {
      className: "w100",
      text: "수정완료",
      size: "btn-lg",
      color: "btn-primary",
    },
  ];

  return (
    <>
      <PageHeader onClick="" title="멤버관리" noBtn={true}/>
      <div className="page-wrapper subHome_wrapper teamManage">
        <FullHeight attr={{pageHeader:true}}>
          <ColTop>
            <section className="subHome_section teamManage flexCC">
              <div className={`team_manage_wrapper ${isMobile && "mobile"} flexCC`}>
                <div className="member_img_wrapper">
                  <img src={data.img} alt="player_img" />
                </div>
                <div className="member_nb_wrapper">
                  <div className="member_name">
                    <div className="label">성함</div>
                    <div className="content">박호각</div>
                  </div>
                  <div className="member_date">
                    <div className="label">생년월일</div>
                    <div className="content">2010.10.10</div>
                  </div>
                </div>

                <div className="form-container">
                  {formGroup.map((item, index) => (
                    <FormItem key={index} attr={item} />
                  ))}
                </div>
              </div>
            </section>
          </ColTop>
          <ColBottom>
            <div className="btn_wrapper">
              <BtnLayout attr={btnData2} clasName="half" />
            </div>
          </ColBottom>
        </FullHeight>
      </div>
      {confirmOpen && (
        <Confirm data={ConfirmData} onClose={openConfirmHandler} />
      )}
    </>
  );
};

export default withRouter(Hteam05010200);
