import React from "react";
import Button from "components/atom/button/Button";

const tagBtn = [
  {
    className : `tag t1`,
    text: `골`,
  },
  {
    className : `tag t2`,
    text: `득점`,
  },
  {
    className : `tag t3`,
    text: `세이브`,
  },
  {
    className : `tag t4`,
    text: `파울`,
  },
  {
    className : `tag t5`,
    text: `레드카드`,
  },
  {
    className : `tag t1`,
    text: `골`,
  },
  {
    className : `tag t2`,
    text: `득점`,
  },
  {
    className : `tag t3`,
    text: `세이브`,
  },
  {
    className : `tag t4`,
    text: `파울`,
  },
  {
    className : `tag t5`,
    text: `레드카드`,
  },
]

const TagContainer = ({close}) => {
  function handleCloseClick() {
    close("tagLayerClose");
  }
  return (
    <>
      <div className="tag-layer">
        <div className="flex flex-top">
          <strong>태그추가</strong>
          <Button attr={{
            className : `ico-close-white`,
            text:`닫기`,
            iconOnly: true,
            onClick : handleCloseClick,
          }} />
        </div>
        <ul className="tag-list">
          {tagBtn.map((item, index) => (
            <li key={index}>
              <Button
                attr={{
                  className : item.className,
                  text: item.text,
                  widthTxt: true,
                }} />
            </li>
          ))}
        </ul>
      </div>
    </>
  )
};

export default TagContainer;
