import { withRouter } from "react-router-dom";
import CardSlider from "components/organism/Slider/CardSlider";
import {
  liveCardData,
  scheduleCardData,
  clipCardData,
  videoCardData,
  gameScheduleCardData,
} from "components/pages/main/data";
