import React, { useState } from "react";
import { withRouter } from "react-router-dom";

import {
  societyCardData,
  societyCardData2,
  societyCardData3,
} from "../main/data";
import useMediaQueryHook from "../../../hook/useMediaQuery";
import SocietyCard from "components/molecule/Card/SocietyCard";
import PageHeader from "components/molecule/layout/PageHeader";
import Button from "components/atom/button/Button";
import handleLocation from "handler/handleLocation";
import SearchSection from "components/molecule/layout/SearchSection";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const Hgcb0300H101 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const secData = [
    {
      section: "societyCard",
      datas: {
        totalCnt: "9",
        list: societyCardData3,
        cardType: "society",
      },
    },
  ];

  return (
    <>
      <div className="teamSetting_wrapper grp">
        {!isMobile && <div className="teamInfo_Page flexCC">팀관리</div>}

        <div className={"socierty_wrapper "}>
          <FullHeight attr={{pageHeader:true}}>
            <ColTop>
              {secData && secData?.length !== 0 ? (
                <SearchSection data={secData} />
              ) : (
                ""
              )}
            </ColTop>
            <ColBottom>
              <div className="btn_wrapper">
                <Button
                  attr={{
                    className: "w100",
                    text: "팀 생성하기",
                    onClick: handleLocation("/H-GCB/0300H102"),
                    color: "btn-primary",
                    size: "btn-lg",
                  }}
                />
              </div>
            </ColBottom>
          </FullHeight>
        </div>
      </div>
    </>
  );
};

export default withRouter(Hgcb0300H101);
