import { UPDATE, LOGIN, LOGOUT } from '../types';

const initialStates = {
    data: {}
}

const reducers = function(state = initialStates, action) {

    switch (action.type) {
        case LOGIN: 
            return { data: action.data }
        case UPDATE: 
            state.data.result = {...state.data.result, ...action.data.result};
            return { ...state }
        case LOGOUT: 
            return initialStates;
        default:
            return state;
    }
}

export default reducers;
