import { PageSettings } from "../../../config/page-settings.js";
import menus from "./mobileMenu";
import NavLink from "components/atom/NavLink.js";
import { Route } from "react-router-dom";
import { useState } from "react";

function MobileNav(mediaQuery) {
  const media = mediaQuery.mediaQuery;

  const [active, setActive] = useState(0);

  const handleMenuActive = (index) => {
    setActive(index);
  };

  console.log("mediaQuery", media);

  return (
    <PageSettings.Consumer>
      {() => (
        <div className="mobile_nav">
          <div className="mobile_nav_wrapper">
            {menus.map((menu, i) => (
              <Route
                key={i}
                children={({ match }) => (
                  <NavLink
                    key={i}
                    attr={{
                      ...menu,
                      active: active === i,
                    }}
                    onClick={() => handleMenuActive(i)}
                  />
                )}
              />
            ))}
          </div>
        </div>
      )}
    </PageSettings.Consumer>
  );
}

export default MobileNav;
