import React, {useState} from 'react';
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom/cjs/react-router-dom";
import Summary from "components/pages/H-MY/Summary";
import Button from "components/atom/button/Button";
import Input from "components/atom/form/Input";
import ButtonAlert from "components/molecule/alert/ButtonAlert";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const popData = [
  {
    text: '인증메일팝',
    color:"btn-gray",
    size:"btn-xxs",
    onClick:"",
    alertDatas : {
      alert:true,
      dec:"입력하신 주소로 인증 메일을 보냈습니다.\n메일을 확인해 인증을 완료해주세요.",
      button: {
        text: "확인",
        onClick: "",
      },
    },
  },
]

const HmyM1000000 = (props) => {
  const [data, setData] = useState({
    name:"홍길동",
    mail:"hogak@hogak.co.kr",
  });

  const logoutPopData = {
    text: '로그아웃',
    className:"link-item",
    onClick:"",
    alertDatas : {
      alert:"",
      dec: "호각 로그아웃을 하시겠습니까?",
      button: {
        text: "확인",
        onClick: "",
      },
    },
  }

  return (
    <>
      <div className="page-my page-wrapper">
        <div className="m-main">
          <div className="my-flex flex">
            <div className="col-summary">
              <Summary />
            </div>

            <div className="col-right">
              <div className="right-outer t3">
                <div className="bg-box">
                  <header className="right-header">
                    <strong className="tit">내 정보</strong>
                  </header>
                  <FullHeight attr={{pageHeader:true}} className="sec-myinfo">
                    <ColTop>
                      <div className="myinfo-container">
                        <div className="box">
                          <div className="img-box add-img" onClick=""></div>
                          <ul className="blit-list">
                            <li>프로필 사진은 각 팀 홈 가입 및 멤버정도에 사용됩니다.</li>
                            <li>팀원분들이 회원님을 알아볼 수 있도록 프로필 사진을 등록해주세요.</li>
                          </ul>
                        </div>

                        <dl className="dl-form">
                          <dt>
                            <div className="col-left">
                              <strong className="name">{data.name} 님</strong>
                            </div>

                            <div className="col-right">
                              <Button attr={{
                                className: 'btn-edit',
                                size: 'btn-xxs',
                                color: 'btn-light-gray-stroke',
                                text: '수정',
                                onClick: "",
                              }} />
                            </div>
                          </dt>

                          <dd>
                            <div className="col-left">
                              <div className="ico kakao"></div>
                            </div>

                            <div className="col-right">
                              <Input attr={{
                                type:"text",
                                id:"text",
                                name:"text",
                                value:data.mail,
                                required:"",
                                placeholder:"",
                                onChange:"",
                                onBlur:"",
                              }}/>
                            </div>
                          </dd>
                        </dl>
                      </div>
                    </ColTop>
                    <ColBottom>
                      <div className="btt-btn">
                        <ul className="btn-ul">
                          <li>
                            <ButtonAlert attr={logoutPopData}/>
                          </li>
                          <li>
                            <Link to="/H-MY/M1020000" className="link-item">회원탈퇴</Link>
                          </li>
                        </ul>
                      </div>
                    </ColBottom>
                  </FullHeight>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="_temp">
        {/*temp*/}
        {popData.map((item, index) => (
        <ButtonAlert attr={item} />
      ))}
      </div>
    </>
  );
};

export default withRouter(HmyM1000000);
