import React, {useState} from "react";
import { Link } from "react-router-dom";
import Button from "components/atom/button/Button";
import BtnLayout from "components/molecule/layout/BtnLayout";

const SelectLayer = ({data}) => {
  const {title, datas} = data;

  return (
    <dl className="reserve-container">
      <dt>{title}</dt>
      <dd>
        <ul className="list-content">
          {datas.map((item, index) => (
            <li key={index}>
              <div className="col-left">
                <p className="time">{item.date}</p>
                <strong className="tit">{item.stadium}</strong>
              </div>
              <div className="col-right">
                <BtnLayout
                  className={item.btns.length === 2 ? "half" : ""}
                  attr={item.btns}
                />

              </div>
            </li>
          ))}
        </ul>
      </dd>
    </dl>
  )
}

export default SelectLayer;

