/* Todo 버튼 셀렉트 클릭시 hidden 데이터및 placehoder 교채 */
import React, { useState, forwardRef, useImperativeHandle } from 'react';
import useMediaQueryHook from "../../../hook/useMediaQuery";
import Input from "components/atom/form/Input";
import Button from "components/atom/button/Button";
import Select from "components/atom/form/Select";
import SelectLayer from "components/molecule/layer/SelectLayer";

const SelectSet = forwardRef(({attr}, ref) => {
  const {label, id, name, value, required, placeholder, formClassName, disabled, onChange, onBlur, options, status, dataIndex} = attr;

  const buttons = !attr.buttons ? ({
    text: placeholder ? placeholder : label,
    className: "selectLayer",
    color: "",
    size: "",
    shape: "",
    icon: "",
    iconOnly: "",
    widthTxt: "",
    onClick: "",
  }) : attr.buttons
  const layers = !attr.layers ? ({
    selectType: "",
    optionLabel: label ? label : placeholder,
    tagHide: "",
    optionType: "radio",
  }) : attr.layers

  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const handleChange = (e) => {};

  const [selectVisible, setSelectVisible] = useState(false);
  const handleBsClose = () => {
    setSelectVisible(!selectVisible);
  };

  useImperativeHandle(ref, () => ({
    // 부모 컴포넌트에서 사용할 함수를 선언
    handleBsClose
  }))

  return (
    <>
      {isMobile ? (
        <>
          <Button attr={buttons} subClick={() => handleBsClose()} />
          <Input
            attr={{
              type: "hidden",
              name: name,
              value: placeholder,
            }}
          />
        </>
      ) : (
        layers.selectType === "btn" || layers.selectType === "bubbleTab" ? (
          <>
            <Button attr={buttons} subClick={() => handleBsClose()} />
            <Input
              attr={{
                type: "hidden",
                name: name,
                value: placeholder,
              }}
            />
          </>
        ) : (
          <Select attr={attr} />
        )
      )}
      {selectVisible && (
        <SelectLayer
          layerAttr={{
            status:status,
            id:id,
            name:name,
            onChange:onChange,
            layers:layers,
            options:options,
            close : () => setSelectVisible(!selectVisible),
            dataIndex,
          }}
        />
      )}
    </>
  );
});

export default SelectSet;
