import React, { useState } from "react";
import { withRouter } from "react-router-dom";
import Confirm from "components/molecule/alert/Confirm";
import handleLocation from "handler/handleLocation";

const ConfirmData = [
  {
    popBtn: "이미 가입된 계정",
    smallTit: "이미 가입된 계정입니다.",
    alert: true,
    button: {
      text: "확인",
      onClick: "",
    },
  },
  {
    popBtn: "사용 불가능 계정",
    smallTit: "사용할 수 없는 계정입니다.",
    alert: true,
    button: {
      text: "확인",
      onClick: "",
    },
  },
  {
    popBtn: "만 14세",
    icon: true,
    title: "만 14세 미만인가요?",
    dec: "만 14세 미만은 보호자 동의가 필요합니다.",
    button: [
      {
        text: "아니오",
        color: "btn-line-white",
        onClick: "",
      },
      {
        text: "네",
        onClick: handleLocation("/H-LOG/01010201"),
      },
    ],
  },
];

const Hlog01010000P1 = (props) => {
  const [confirmOpen, setConfirmOpen] = useState(false);

  const openConfirmHandler = (id) => {
    setConfirmOpen((el) => (el.confirmOpen !== id ? id : ""));
    if (confirmOpen === id) {
      setConfirmOpen(false);
    }
  };

  return (
    <>
      <section className="sec">
        <div className="m-main">
          <ul className="flex">
            {ConfirmData.map((item, index) => (
              <li style={{ marginRight: "5px" }}>
                <button
                  key={index}
                  className="btn"
                  onClick={() => {
                    openConfirmHandler(index);
                  }}
                >
                  {item.popBtn}
                </button>
                {confirmOpen === index ? (
                  <Confirm
                    data={item}
                    onClose={openConfirmHandler}
                    id={index}
                  />
                ) : (
                  ""
                )}
              </li>
            ))}
          </ul>
        </div>
      </section>
    </>
  );
};

export default withRouter(Hlog01010000P1);
