import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";
import moment from "moment";

import useMediaQueryHook from "hook/useMediaQuery";
import Button from "components/atom/button/Button";
import PageHeader from "components/molecule/layout/PageHeader";
import BbsCard from "components/molecule/Card/BbsCard";
import handleLocation from "handler/handleLocation";
import CustomList from "components/organism/table/CustomList";
import handleChange from "handler/handleChange";
import FormItem from "components/molecule/form/FormItem";
import DatePicker from "components/atom/DatePicker";
import RangePicker from "components/atom/RangePicker";
import handleDate from "handler/handleDate";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const Hgcb0300H320 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); // 이전 스크롤 위치
  const [length, setLength] = useState(0); // 현재 글자 수 상태 추가

  const [data, setData] = useState({
    title: "",
    notiStartTime: "",
    notiEndTime: "",
    content: "",
  });

  const handleScroll = () => {
    const currentScrollY = window.scrollY;
    setScrollY(currentScrollY);

    if (currentScrollY > prevScrollY && currentScrollY > 100) {
      setIsExpanded(false);
    } else if (currentScrollY < prevScrollY) {
      setIsExpanded(true);
    }

    setPrevScrollY(currentScrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollY]);

  const notiForm = [
    {
      label: "단체 공지 제목",
      labelClassName: "",
      formClassName: "_wirte",
      copyToClipboard: "",
      id: "title",
      name: "title",
      type: "text",
      value: data.title,
      onChange: (e) => handleChange(e, data, setData),
      onKeyPress: "",
      placeholder: "",
      autocomplete: "",
      noBtn: true,
      length: length ? length : "0",
      maxLength: "10",
    },
    {
      label: "공지 노출 시간",
      startDate: data.notiStartTime,
      endDate: data.notiEndTime,
      formClassName: "",
      formBtns: "",
      copyToClipboard: "",
      id: "notiStartTime",
      name: "notiStartTime",
      type: "rangepicker",
      value: data.notiStartTime,
      data: data,
      setData: setData,
      status: "",
      onChange: (e) => handleChange(e, data, setData),
      readOnly: false,
    },
    {
      label: "단체 공지 내용",
      labelClassName: "",
      formClassName: "",
      formBtns: "",
      copyToClipboard: "",
      id: "content",
      name: "content",
      type: "textarea",
      value: data.content,
      status: "",
      onChange: (e) => handleChange(e, data, setData),
      readOnly: false,
      onKeyPress: "",
      placeholder: "",
      autocomplete: "",
      etcs: {
        tip: "",
        tipSize: "",
        tipColor: "",
        length: "0",
        maxLength: "300",
        lengthPosition: "bottom",
        validMsg: "",
        inValidMsg: "",
      },
    },
  ];

  return (
    <>
      <div className="teamSetting_wrapper">
        <PageHeader onClick="" title="공지사항 등록" noBtn="true" />
        <div className="page page-wrapper">
          <FullHeight attr={{pageHeader:true}}>
            <ColTop>
              <div className="notice_wrapper">
                <div className="form-box">
                  <div className="form-container _wrap">
                    {notiForm.map((item, index) => (
                      <FormItem key={index} attr={item} />
                    ))}
                  </div>
                </div>
              </div>
            </ColTop>
            <ColBottom>
              <div className="btn_wrapper">
                <Button
                  attr={{
                    className: "w100",
                    text: "등록하기",
                    // onClick: handleLocation("/H-GRP/0203H320"),
                    color: "btn-black",
                    size: "btn-lg",
                  }}
                />
              </div>
            </ColBottom>
          </FullHeight>
        </div>
      </div>
    </>
  );
};

export default withRouter(Hgcb0300H320);
