import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";
import useMediaQueryHook from "hook/useMediaQuery";
import { useLocation } from "react-router-dom/cjs/react-router-dom.min";
import Terms from "components/atom/Terms";

const Hterm00000001 = (props) => {
  const { currentMediaQuery, isMobile } = useMediaQueryHook();
  const location = useLocation();
  const label = location.state?.label;

  return (
    <>
      <div className="page-wrapper termPage joinPage bg-gray">
        <section className="join_wrapper sec-height _fix">
          <div className="join_container">
            {!isMobile && <div className="term_title ">전자금융거래 이용약관</div>}
            <div className={`term_content`}>
              <Terms type="service" />
            </div>
          </div>
        </section>
      </div>
    </>
  );
};

export default withRouter(Hterm00000001);
