import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom/cjs/react-router-dom";
import useMediaQueryHook from "hook/useMediaQuery";
import handleCheck from "handler/handleCheck";
import handleLocation from "handler/handleLocation";
import BtnLayout from "components/molecule/layout/BtnLayout";
import Checkbox from "components/molecule/form/Checkbox";

import useradd from "assets/images/icon/icon_useradd.svg";
import userchadd from "assets/images/icon/icon_userchadd.svg";
import foreignadd from "assets/images/icon/icon_foreignadd.svg";


const Hlog01030000 = (props) => {
  const { currentMediaQuery, isMobile } = useMediaQueryHook();

  const [data, setData] = useState({
    terms: {
      all: false,
      serviceChecked: false,
      termsChecked: false,
      privacyChecked: false,
      thirdPartyChecked: false,
      marketingChecked: false,
    },
  });

  const isAllRequiredChecked =
    data.terms.serviceChecked &&
    data.terms.termsChecked &&
    data.terms.privacyChecked &&
    data.terms.thirdPartyChecked;
  return (
    <>
      <div className="page-wrapper joinPage bg-gray">
        <section className="join_wrapper sec-height _fix _between">
          <div className="join_container t1">
            <div className="col-top">
              <div className="m-main">
                <div className="join_info">
                  Please check the Hogak
                  <br /> service terms and conditions.
                </div>
              </div>
            </div>

            <div className={`join_term_wrapper ${isMobile && "mobile"}`}>
              <div className="m-main">
                <Checkbox
                  attr={{
                    id: "checkbox-all",
                    label: "Full agreement",
                    checked: data.terms.all,
                    onChange: (e) => handleCheck(e, data, setData),
                    size: "big",
                    shape: "round",
                    name: "all",
                    className: "main-checkbox",
                    dataGroup: "terms",
                  }}
                />

                <Checkbox
                  attr={{
                    id: "checkbox-terms",
                    label: (
                      <Link
                        to={{
                          pathname: "/H-TERM/00000001",
                          state: { label: "서비스 이용약관에 동의" },
                        }}
                      >
                        서비스 이용약관에 동의 &gt;{" "}
                      </Link>
                    ),
                    checked: data.terms.serviceChecked,
                    onChange: (e) => handleCheck(e, data, setData),
                    shape: "round",
                    name: "serviceChecked",
                    dataGroup: "terms",
                    require: true,
                  }}
                />
                <Checkbox
                  attr={{
                    id: "checkbox-terms",
                    label: (
                      <Link
                        to={{
                          pathname: "/H-TERM/00000001",
                          state: { label: "전자금융거래 이용약관" },
                        }}
                      >
                        전자금융거래 이용약관 &gt;{" "}
                      </Link>
                    ),
                    checked: data.terms.termsChecked,
                    onChange: (e) => handleCheck(e, data, setData),
                    shape: "round",
                    name: "termsChecked",
                    dataGroup: "terms",
                    require: true,
                  }}
                />
                <Checkbox
                  attr={{
                    id: "checkbox-privacy",
                    label: (
                      <Link
                        to={{
                          pathname: "/H-TERM/00000001",
                          state: { label: "개인정보 수집, 이용" },
                        }}
                      >
                        개인정보 수집, 이용 &gt;{" "}
                      </Link>
                    ),
                    checked: data.terms.privacyChecked,
                    onChange: (e) => handleCheck(e, data, setData),
                    shape: "round",
                    name: "privacyChecked",
                    dataGroup: "terms",
                    require: true,
                  }}
                />
                <Checkbox
                  attr={{
                    id: "checkbox-third-party",
                    label: (
                      <Link
                        to={{
                          pathname: "/H-TERM/00000001",
                          state: { label: "개인정보 제3자 제공 동의" },
                        }}
                      >
                        개인정보 제3자 제공 동의 &gt;{" "}
                      </Link>
                    ),
                    checked: data.terms.thirdPartyChecked,
                    onChange: (e) => handleCheck(e, data, setData),
                    shape: "round",
                    name: "thirdPartyChecked",
                    dataGroup: "terms",
                    require: true,
                  }}
                />
                <Checkbox
                  attr={{
                    id: "checkbox-marketing",
                    label: (
                      <Link
                        to={{
                          pathname: "/H-TERM/00000001",
                          state: { label: "마케팅 정보수신 동의" },
                        }}
                      >
                        마케팅 정보수신 동의 &gt;{" "}
                      </Link>
                    ),
                    checked: data.terms.marketingChecked,
                    onChange: (e) => handleCheck(e, data, setData),
                    shape: "round",
                    name: "marketingChecked",
                    dataGroup: "terms",
                    require: false,
                  }}
                />
              </div>
            </div>

            <div className="btt-btn">
              <div className="m-main">
                <BtnLayout
                  attr={[
                    {
                      className : 'w100',
                      text: '다음',
                      size: "btn-xxl",
                      color : 'btn-primary',
                      disabled: !isAllRequiredChecked,
                      onClick: handleLocation("/H-LOG/01030100"),
                      radius:false,
                    },
                  ]}
                  className="full"
                />
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  );
};

export default withRouter(Hlog01030000);
