import React from "react";
import { withRouter } from "react-router-dom";
import PageHeader from "components/molecule/layout/PageHeader";
import Button from "components/atom/button/Button";
import BtnLayout from "components/molecule/layout/BtnLayout";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const Hmain07030000 = (props) => {
  return (
    <>
      <PageHeader onClick="" title="촬영예약" />
      <div className="page page-wrapper page-shoot">
        <FullHeight attr={{pageHeader:true}}>
          <ColTop>
            <div className="location-container">
              <header className="location-header">
                <strong className="stadium">동해시 공설운동장 테니스 A코트</strong>
                <p className="date">2024년 10월 24일 12:00~14:55</p>
              </header>
              <p className="tip">* 카메라 정비시간으로 인해 촬영은 종료시작 5분전(55분)에 종료됩니다.</p>
            </div>
            <div className="score-container">
              <div className="m-main">
                <dl className="score-dl">
                  <dt>라이브 스코어 </dt>
                  <dd className="flex">
                    <div className="col-dec">
                      <strong>라이브 스코어 관리</strong>
                      <p>앱에서 점수 표기 여부를 설정할 수 있습니다.</p>
                    </div>
                    <div className="col-btn">
                      <Button
                        attr={{
                          className : 'round',
                          text: '입력하기',
                          size : 'btn-lg',
                          color : 'btn-line-white',
                          onlick : "",
                        }}
                      />
                    </div>
                  </dd>
                </dl>
              </div>
            </div>
          </ColTop>
          <ColBottom />
        </FullHeight>
      </div>
    </>
  );
};

export default withRouter(Hmain07030000);
