import React, { useRef, useState } from "react";
//import { CopyToClipboard } from "pivotal-ui/react/copy-to-clipboard";
import Input from "components/atom/form/Input";
import Textarea from "components/atom/form/Textarea";
import SelectSet from "components/molecule/form/SelectSet";
import Checkbox from "components/molecule/form/Checkbox";
import Radio from "components/molecule/form/Radio";
import Etc from "components/molecule/form/Etc";
import Tip from "components/molecule/form/Tip";
import EtcButton from "components/molecule/form/EtcButton";
import Length from "components/molecule/form/Length";
import handleDate from "handler/handleDate";
import moment from "moment";
import RangePicker from "components/atom/RangePicker";
import Button from "components/atom/button/Button";

/**
 *
 * @param {*} props type :              필수 id       - text || pasword || checkbox || radio || select || textarea
 * @param {*} props id :                필수 id       - string
 * @param {*} props name :              필수 name     - boolean
 * @param {*} props value :             value        - string
 * @param {*} props required :          required    - boolean
 * @param {*} props placeholder :       placeholder - string
 * @param {*} props formClassName :     form-groupclassName
 * @param {*} props status :            status
 * @param {*} props readOnly :          readOnly      - boolean
 * @param {*} props disabled :          disabled      - boolean
 * @param {*} props onChange :          onChange      - function
 * @param {*} props onKeyPress :        onKeyPress    - function
 * @param {*} props onKeyUp :           onKeyUp       - function
 * @param {*} props onBlur :            onBlur        - function
 * @param {*} props copyToClipboard :   copyToClipboard
 * @param {*} props autocomplete :      autocomplete  - boolean
 * @param {*} props maxLength :         maxLength     - number
 * @param {*} props options :           options       - only select, checkbox, radio  사용
 * @param {*} props etcs :              etcs data
 * @param {*} props formBtns :          formBtns      - only type=text, password 버튼
 * @param {*} props layers :            layers data   - only select 사용
 * @param {*} props buttons :           buttons datas - only select 사용
 * @param {*} props datePicker :        datePicker datas - only datePicker 사용
 *
 */

const FormWrap = ({ attr, dataIndex, selectSetRef }) => {
  const {
    label,
    type,
    id,
    name,
    value,
    required,
    placeholder,
    formClassName,
    status,
    readOnly,
    disabled,
    onChange,
    onKeyPress,
    onKeyUp,
    onBlur,
    copyToClipboard,
    autocomplete,
    options,
    etcs,
    lengths,
    formBtns,
    layers,
    buttons,
    datePicker,
    onClick,
  } = attr;

  const inputRef = useRef(null);

  const handleChange = (e) => {};

  // Todo onclick delValue
  const delValue = () => {};

  //const [value, setValue] = useState()
  const [btnVisible, setBtnVisible] = useState(false);
  const formPressHandler = (e) => {
    if (e.target.value.length > 0) {
      setBtnVisible(true);
    } else {
      setBtnVisible(false);
    }
  };

  const [val, setVal] = useState("");
  const displayText = (e) => {
    setVal(e.target.value);
  };
  const resetValuee = (e) => {
    setVal("");
  };

  console.log("ATTR", attr);

  const { minDateRange, maxDateRange, minDateChange, maxDateChange } =
    handleDate(attr.data, attr.setData);

  const formData = {
    label,
    type,
    id,
    name,
    value,
    required,
    placeholder,
    formClassName: formClassName + (status || null),
    status,
    readOnly,
    disabled,
    onChange: onChange || formPressHandler,
    onKeyPress,
    onKeyUp,
    onBlur,
    autocomplete,
    options,
    etcs,
    formBtns,
    layers,
    buttons,
    datePicker,
    ref: inputRef,
    dataIndex,
  };

  return (
    <>
      <div
        className={`form-wrap ${formClassName || ""} ${type || ""} ${
          copyToClipboard ? "copyToClipboard" : ""
        }
        `}
      >
        <>
          {type === "text" || type === "password" ? (
            <>
              <Input attr={formData} />

              {etcs && <Etc attr={etcs} />}
              {formClassName === "out" && lengths ? (
                <Length attr={lengths} />
              ) : (
                ""
              )}
              {formBtns && (
                <EtcButton
                  attr={formBtns}
                  lengthData={formClassName !== "out" && lengths ? lengths : ""}
                />
              )}

              {/*Todo CopyToClipboard */}
              {/*{item.copyToClipboard && (
                  <CopyToClipboard text={item.value} tooltip="Copied!">
                    <i
                      className="ion ion-md-copy fa-2x"
                      style={{ color: "#253640" }}
                    />
                  </CopyToClipboard>
                )}*/}
            </>
          ) : type === "textarea" ? (
            <>
              <Textarea attr={{ formData }} />
              {etcs && <Etc attr={etcs} />}
            </>
          ) : type === "select" || type === "custom" ? (
            <SelectSet attr={formData} ref={selectSetRef}/>
          ) : type === "checkbox" || type === "radio" ? (
            options.map((item, index) =>
              type === "checkbox" ? (
                <Checkbox
                  key={index}
                  attr={{
                    type,
                    name,
                    id: id + "_" + index,
                    label: item.text,
                    value: item.value || "",
                    className: item.className || "",
                    checked: item.checked || "",
                    disabled: item.disabled || "",
                    shape: item.shape || "",
                    size: item.size || "",
                  }}
                />
              ) : (
                <Radio
                  key={index}
                  attr={{
                    type,
                    name,
                    id: id + "_" + index,
                    label: item.text,
                    value: item.value || "",
                    className: item.className || "",
                    checked: item.checked || "",
                    disabled: item.disabled || "",
                    shape: item.shape || "",
                    size: item.size || "",
                  }}
                />
              )
            )
          ) : type === "rangepicker" ? (
            <div className="form-wrap rangepicker">
              <RangePicker
                attr={{
                  // label: label,
                  startDate: attr.startDate,
                  endDate: attr.endDate,
                  maxDateDisabled: attr.startDate === null,
                  minDateChange: (value) => minDateChange(value),
                  maxDateChange: (value) => maxDateChange(value),
                  minDateRange: (value) => minDateRange(value),
                  maxDateRange: (value) => maxDateRange(value),
                }}
              />
            </div>
          ) : type === "p" ? (
            <p className="dec">{value}</p>
          ) : type === "button" ? (
            <>
              <Button attr={{
                onclick:onClick,
                className:"dec t-btn",
                text:placeholder,
              }} />
              <input type="hidden" id={id} name={name} />
            </>
          ) : (
            ""
          )}
        </>
      </div>

      {etcs || lengths ? (
        <Tip
          attr={etcs}
          lengthData={lengths && lengths.position === "bottom" ? lengths : ""}
        />
      ) : (
        ""
      )}
    </>
  );
};

export default FormWrap;
