import React, {useState, useRef} from 'react';
import { withRouter } from "react-router-dom";
import Summary from "components/pages/H-MY/Summary";
import BubbleTab from "components/atom/BubbleTab";
import CardList from "components/molecule/list/CardList";

import {
  clipCardData,
} from "components/pages/main/data";

const cardData = {
  totalCnt : "5",
  list : clipCardData,
  cardType : "clip",
  className : "t2",
}

const Hmy04000000 = (props) => {

  //버블탭 selectSet setting
  const selectSetComponentRef = useRef({});
  const selectActiveRef = useRef({});
  const selectChangeHandler = (e) => {
    //BS cloas
    selectSetComponentRef.current.handleBsClose();
    //change label
    let label = e.target.dataset.label;
    let index = e.target.dataset.index;
    let newBubbleTabData = [...bubbleTabData];
    newBubbleTabData[index].label = label;
    setBubbleTabData(newBubbleTabData);
    // tab active
    selectActiveRef.current.handleTabClick({label:label})
  };
  const [bubbleTabData, setBubbleTabData] = useState([
    {
      select: true,
      selectedTab : "영상",
      label: "영상",
      onChange:selectChangeHandler,
      options: [
        {
          text: "모두보기",
          type:"radio",
        },
        {
          text: "라이브",
          type:"radio",
        },
        {
          text: "방송예정",
          type:"radio",
        },
        {
          text: "지난경기영상",
          type:"radio",
        },
        {
          text: "클립",
          type:"radio",
        },
      ],
    },
    {
      label: "팀",
    },
    {
      label: "대회",
    },
    {
      label: "단체",
    },
  ]);
  return (
    <>
      <div className="page-my page-wrapper bg-gray">
        <div className="m-main">
          <div className="my-flex flex">
            <div className="col-summary">
              <Summary />
            </div>

            <div className="col-right">
              <div className="right-outer t3">
                <div className="bg-box">
                  <header className="right-header">
                    <strong className="tit">찜</strong>
                  </header>
                  <section>
                    <div className="m-main">
                      <div className="bubble-list">
                        <BubbleTab
                          tabs={bubbleTabData}
                          selectSetRef={selectSetComponentRef}
                          ref={selectActiveRef}
                        />
                        <CardList data={cardData} />
                      </div>
                    </div>
                  </section>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default withRouter(Hmy04000000);
