/**
 *
 * @param {*} props img : 통이미지 일경우
 * @param {*} props bg : bg color
 * @param {*} props rightImg : right image
 * @param {*} props title : title
 * @param {*} props dec : description
 *
 */

const KvCard = ({ img, bg, rightImg, title, dec }) => {
  return (
    <div className="slide-item" style={{ backgroundColor: bg }}>
      <div className="m-main">
        {
          img ? (
            <div className="img-box" style={{ backgroundImage: `url(${img})`}}></div>
          ) : (
            ""
          )
        }

        <dl className="col-dec">
          <dt>{title}</dt>
          <dd>{dec}</dd>
        </dl>
        <div className="col-right">
          <img src={rightImg} />
        </div>
      </div>
    </div>
  );
};

export default KvCard;
