const TeamItem = ({ teamLogo, teamName, reverse, className }) => {
  return (
    <dl className={`card-team ${className && className}`}>
      {reverse ? (
        <>
          <dd>{teamName}</dd>
          <dt>
            <img src={teamLogo} />
          </dt>{" "}
        </>
      ) : (
        <>
          <dt>
            <img src={teamLogo} />
          </dt>{" "}
          <dd>{teamName}</dd>
        </>
      )}
    </dl>
  );
};

export default TeamItem;
