import { MAIN_COLOR } from "branding/theme";
import SolidIcon from "components/atom/icons/SolidIcon";
import RegularIcon from "components/atom/icons/RegularIcon";
import AnimatedIcon from "components/atom/icons/AnimatedIcon";

import ico_arrow from "assets/images/icon/icon_arrow_right_gray.svg";
import ico_out from "assets/images/icon/ico_out.svg";

/* [Call Format] 

    <Button
      subClick={() => {}},              // btnProps 를 넘겨받은 중간 단계의 컴포넌트에서(넘겨받은 props는 수정 불가능하므로) 이벤트를 정의해주어야 할 때 
      attr={
        color: 'primary',               // 색상(string)
        size: 'sm',                     // 크기(string)
        state: 'disabled',              // View 상태(string)
        disabled: {true},               // 기능 상태(boolean)
        className: '',                  // 클래스명(string)
        style: {{}},                    // 인라인 스타일(object)
        onClick: {() => {}},            // 이벤트 함수(function)
        text: '등록하기',                // 버튼명(string)
        solidIcon: 'camera',            // Color-Admin - Icon 참조(string)
        regularIcon: 'add',             // Color-Admin - Icon 참조(string)
        animatedIcon: 'spinner',        // Color-Admin - Icon 참조(string)
        iconProps: {{                   // icon 스타일 지정(Object)
          iconColor: 'success',         // 아이콘 색상(string)
          iconSize: 'lg',               // 아이콘 크기(string)
          className: '',                // 클래스명(string)
        }},
        iconOnly: true,                 // iconOnly true / default false
        widthTxt: true,                 // icon + text true / default false
    }/>

*/

const Button = (props) => {
  const { attr, subClick } = props;
  const {
    color,
    size,
    state,
    className,
    style,
    onClick,
    disabled,
    text,
    solidIcon,
    regularIcon,
    animatedIcon,
    iconProps,
    iconOnly,
    icon,
    widthTxt,
    imgProps,
    addText,
    arrow,
    out,
    shape,
    radius,
  } = attr;

  return (
    <button
      type="button"
      className={`btn ${className || ""} ${
        iconOnly ? `ico btn-ico` : ``
      }  ${widthTxt ? `ico width-txt` : ``} ${size || ""} ${
        color ? color : ""
      } ${shape || ""} ${radius === false && "_non_radius"}
      `}
      style={style}
      onClick={subClick || onClick}
      disabled={disabled}
      children={
        <>
          {imgProps && <img src={imgProps.src} style={imgProps.style} />}
          {solidIcon && (
            <SolidIcon solidIcon={solidIcon} iconProps={iconProps} />
          )}

          {regularIcon && (
            <RegularIcon regularIcon={regularIcon} iconProps={iconProps} />
          )}

          {animatedIcon && (
            <AnimatedIcon animatedIcon={animatedIcon} iconProps={iconProps} />
          )}
          {iconOnly ? (
            <span className="a11y">{text}</span>
          ) : addText ? (
            <div className="btn_text-container">
              <span>{text}</span>
              {addText && <p>{addText}</p>}
            </div>
          ) : (
            <span className={`txt ${icon ? `ico ${icon}` : ""}`}>{text}</span>
          )}
          {arrow && <img className="btn_arrow" src={ico_arrow} />}
          {out && <img className="btn_out" src={ico_out} />}
        </>
      }
    />
  );
};

export default Button;
