import Button from "components/atom/button/Button";
import React, { useState, useRef } from "react";

import ico_img from "assets/images/icon/ico_imgage.svg";

const ImageUploader = ({ maxImages }) => {
  const [selectedImages, setSelectedImages] = useState([]);
  const fileInputRef = useRef();

  const handleImageChange = (event) => {
    const files = Array.from(event.target.files);

    // 새로운 이미지를 추가할 수 있는지 확인
    if (selectedImages.length + files.length > maxImages) {
      alert(`최대 ${maxImages}개의 이미지만 선택할 수 있습니다.`);
      return; // 최대 개수를 초과하면 리턴
    }

    const newImages = files.map((file) => {
      return new Promise((resolve) => {
        const reader = new FileReader();
        reader.onloadend = () => {
          resolve({ src: reader.result, id: Math.random() });
        };
        reader.readAsDataURL(file);
      });
    });

    // 모든 이미지가 읽혀진 후 상태 업데이트
    Promise.all(newImages).then((resolvedImages) => {
      setSelectedImages((prevImages) => [...prevImages, ...resolvedImages]);
    });
  };

  const handleButtonClick = () => {
    if (fileInputRef.current) {
      fileInputRef.current.click();
    }
  };

  const removeImage = (id) => {
    setSelectedImages(selectedImages.filter((image) => image.id !== id));
  };

  return (
    <div className="imageUploader">
      <Button
        attr={{
          className: "w100 img-btn",
          text: "앨범에서 사진 선택",
          color: "btn-light-gray-stroke",
          size: "btn-lg",
          imgProps: {
            src: ico_img,
            style: {
              width: "24px",
              height: "24px",
            },
          },
          onClick: handleButtonClick,
        }}
      />
      <input
        ref={fileInputRef}
        type="file"
        accept="image/*"
        onChange={handleImageChange}
        style={{ display: "none" }}
        multiple
      />
      <div className="image_wrapper flexCC_col">
        <div className="title">현재 등록된 이미지</div>
        <div className="preview">
          {selectedImages.map((image) => (
            <div key={image.id} className="image_container">
              <Button
                attr={{
                  className: `ico-blackdel`,
                  text: "dsa",
                  iconOnly: true,
                  onClick: () => removeImage(image.id),
                }}
              />
              <img
                src={image.src}
                alt="미리보기"
                style={{ width: "100%", height: "auto" }}
              />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default ImageUploader;
