import {useEffect, useRef} from "react";
import useMediaQueryHook from "../../../hook/useMediaQuery";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const FullHeight = (props) => {
  const {children, attr, className } = props;

  const containerRef = useRef(null);
  const contentRef = useRef(null);

  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();
  function resize() {
    if(isMobile) {
      let kvHeight = attr !== undefined && attr.kvRef ? attr.kvRef.current.offsetHeight : 0;
      let headerHeight = attr !== undefined && attr.pageHeader ? 0 : 0;
      let winHeight = window.innerHeight - 36 - kvHeight;
      let contentHeight = contentRef.current.offsetHeight;
      let containerHeight = containerRef.current.offsetHeight;
      let Height = winHeight < contentHeight ? "auto" : winHeight +"px";
      containerRef.current.style.setProperty('height', Height);
    }
  }

  useEffect(() => {
    const handleLoad = () => {
      resize();
    }

    window.addEventListener("load", handleLoad);
    handleLoad();

    return () => window.removeEventListener("load", handleLoad);

    const handleResize = () => {
      resize();
    }

    window.addEventListener("resize", handleResize);
    handleResize();

    return () => window.removeEventListener("resize", handleResize);
  }, []);

  return (
    <div className={`sec full-height ${className ? className : ""}`} ref={containerRef}>
      <div className="m-main"  ref={contentRef}>
        <div className="col-top">
          {children[0].props.children}
        </div>

        {children[1].props.children ? (
          <div className="btt-btn">
            {children[1].props.children}
          </div>
        ) : ("")}
      </div>
    </div>
      
  )
}

export default FullHeight;