import useMediaQueryHook from "hook/useMediaQuery";
import React, { useState } from "react";
import Slider from "react-slick";
import noImg from "../../../assets/images/no_background_img.png";

export default function ImageSlider({
  images,
  overlay,
  status,
  title,
  slideCounter,
}) {
  const [currentSlide, setCurrentSlide] = useState(0); // 현재 슬라이드 상태

  var settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    afterChange: (current) => setCurrentSlide(current), // 슬라이드가 변경될 때마다 현재 슬라이드 업데이트

    responsive: [
      {
        breakpoint: 360,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          adaptiveHeight: true,
        },
      },
      {
        breakpoint: 860,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 1200,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <div className="image_slider">
      <Slider {...settings}>
        {images && images?.length !== 0 ? (
          images.map((image, index) => (
            <div key={index} className="slide">
              {!overlay ? (
                <img
                  src={image}
                  alt={`image ${index + 1}`}
                  className="slide-image"
                />
              ) : overlay && !status ? (
                <>
                  <img
                    src={image}
                    alt={`image ${index + 1}`}
                    className="slide-image"
                  />
                  <div className="overlay flexCC">
                    {title && <div className="overlay_txt">{title}</div>}
                  </div>
                </>
              ) : (
                <div className="overlay status flexCC">
                  <div className="overlay_txt">
                    더 이상 운영하지 않는 팀입니다.
                  </div>
                </div>
              )}
            </div>
          ))
        ) : (
          <div className="slide">
            <img src={noImg} alt={`noImg`} className="slide-image" />
          </div>
        )}
      </Slider>
      {images && slideCounter ? (
        <div className="slide-counter">
          {currentSlide + 1}/{images.length}
        </div>
      ) : (
        ""
      )}
    </div>
  );
}
