import React, { useState, useContext } from 'react';
import { Route, Link } from 'react-router-dom';
import { PageSettings } from '../../../config/page-settings.js';

function FloatSubMenuList(props) {

    const [active, setActive] = useState(-1);
    const [clicked, setClicked] = useState(-1);

    const { handleFloatSubMenuClick } = useContext(PageSettings);

    const handleExpand = (e, i, match) => {
        e.preventDefault();

        setActive(active === i ? -1 : i);
        setClicked(1);

        setTimeout(() => {
            handleFloatSubMenuClick();
        }, 0);
    }

    var icon = props.data.icon && <i className={props.data.icon}></i>;
    var img = props.data.img && <span className="icon-img"><img src={props.data.img} alt="" /></span>;
    var caret = (props.data.children && !props.data.badge) && <b className="caret"></b>;
    var label = props.data.label && <span className="label label-theme m-l-5">{props.data.label}</span>;
    var badge = props.data.badge && <span className="badge pull-right">{props.data.badge}</span>;
    var title = props.data.name && <span>{props.data.name} {label}</span>;
    
    return (
        <PageSettings.Consumer>
            {({pageSidebarMinified}) => (
                <Route path={props.data.path} exact={props.data.exact} children={({ match }) => (
                    <li className={(match ? "active " : "") + ((props.active || (props.clicked === -1 && match)) ? 'expand ' : 'closed ') + (props.data.children ? "has-sub " : "")}>
                        {props.data.children ? (
                            <Link to={props.data.path} onClick={props.expand}>{ img } { icon } { title } { badge } { caret }</Link>
                        ):(
                            <Link to={props.data.path}>{ img } { icon } { title } { badge } { caret }</Link>
                        )}
                        {props.data.children && (
                            <ul className={"sub-menu " + (((props.active || (props.clicked === -1 && match)) && !pageSidebarMinified) ? 'd-block ' : 'd-none')}>
                                {props.data.children && props.data.children.map((submenu, i) => (
                                    <FloatSubMenuList
                                        data={submenu} 
                                        key={i} 
                                        expand={(e) => handleExpand(e, i, match)}
                                        active={i === active} 
                                        clicked={clicked}
                                    />
                                ))}
                            </ul>
                        )}
                    </li>
                )} />
            )}
        </PageSettings.Consumer>
    )
}

export default FloatSubMenuList;