import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";

import useMediaQueryHook from "hook/useMediaQuery";

import PageHeader from "components/molecule/layout/PageHeader";
import player from "assets/images/example/team_member.png";
import MemberList from "components/organism/table/MemberList";
import FormSearch from "components/molecule/search/FormSearch";

const Hteam0703M100 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); // 이전 스크롤 위치

  const handleScroll = () => {
    const currentScrollY = window.scrollY;
    setScrollY(currentScrollY);

    if (currentScrollY > prevScrollY && currentScrollY > 100) {
      setIsExpanded(false);
    } else if (currentScrollY < prevScrollY) {
      setIsExpanded(true);
    }

    setPrevScrollY(currentScrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollY]);

  const members = [
    {
      name: "김영미",
      position: "(김호각 모)",
      btnProps: {
        className: "list-btn flexCC",
        text: "양도",
        onClick: "",
        color: "btn-light-blue-stroke",
        size: "btn-xs",
      },
    },
    {
      name: "홍선수",
      position: "선수",
      img: player,
      btnProps: {
        className: "list-btn flexCC",
        text: "양도",
        onClick: "",
        color: "btn-light-blue-stroke",
        size: "btn-xs",
      },
    },
    {
      name: "홍길동",
      position: "관계자",
      btnProps: {
        className: "list-btn flexCC",
        text: "양도",
        onClick: "",
        color: "btn-light-blue-stroke",
        size: "btn-xs",
      },
    },
    {
      name: "홍길동",
      position: "코치진",
      btnProps: {
        className: "list-btn flexCC",
        text: "양도",
        onClick: "",
        color: "btn-light-blue-stroke",
        size: "btn-xs",
      },
    },
  ];

  const [memberData, setmemberData] = useState({
    members: members,
  });
  return (
    <>
      <div className="teamSetting_wrapper_manage">
        <PageHeader onClick="" title="팀 양도관리" noBtn="true" />
        <div className={`${isMobile ? "m-main" : ""}`}>
          <div className="m-main">
            <div className="page page-shoot">
              <section className="sec">
                <div className="form-group">
                  <FormSearch
                    attr={{
                      name: "",
                      placeholder: "이름(닉네임)을 검색해주세요.",
                      onClick: "",
                    }}
                  />
                </div>

                <div className="memberList_label">팀원</div>
                <div className="memberList_wrapper">
                  <MemberList dataItem={memberData} />
                </div>
              </section>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default withRouter(Hteam0703M100);
