import React, {useRef, useState} from "react";
import useMediaQueryHook from "../../../hook/useMediaQuery";
import Sticky from 'react-sticky-el';
import ClipCard from "components/molecule/Card/ClipCard_v2";
import Button from "components/atom/button/Button";
import BubbleTab from "components/atom/BubbleTab";
import FormItem from "components/molecule/form/FormItem";
import BtnLayout from "components/molecule/layout/BtnLayout";
import SelectSet from "components/molecule/form/SelectSet";

/**
 *
 * @param {*} props tab : clip || tag || analysis
 * @param {*} props type : list || write
 * @param {*} props btn.text : text
 * @param {*} props btn.onClick : onClick
 * @param {*} props btn.color : default btn-primary || btn-black
 * @param {*} props datas : datas
 */

const bubbleTabData = [
  {
    label: "전체",
    id:"",
    name:"selectedTabName",
    select: true,
    selectedTab: "",
    onClick: "",
    options: [
      {
        text: "전체 공개",
        value: "",
        name: "s1",
        rows : {
          type : "btn",
          name:"s1",
          checked:"",
          datas: [
            {
              text: "태그1",
              value:"태그1",
              onClick:"",
            },
            {
              text: "태그2",
              value:"태그2",
              onClick:"",
            },
            {
              text: "태그3",
              value:"태그3",
              onClick:"",
            },
          ]
        }
      },
      {
        text: "팀 공개",
        value: "",
        name: "s1",
        rows : {
          type : "radio",
          name:"s2",
          checked:"",
          datas: [
            {
              text: "팀 공개1",
              value:"팀 공개1",
              checked:"",
            },
            {
              text: "팀 공개2",
              value:"팀 공개2",
              checked:"",
            },
            {
              text: "팀 공개3",
              value:"팀 공개3",
              checked:"",
            },
          ]
        }
      },
      {
        text: "내 클립",
        value: "",
        name: "s1",
        rows : {
          type : "radio",
          name:"s3",
          checked:"",
          datas: [
            {
              text: "내 클립1",
              value:"내 클립1",
              checked:"",
            },
            {
              text: "내 클립2",
              value:"내 클립2",
              checked:"",
            },
            {
              text: "내 클립3",
              value:"내 클립3",
              checked:"",
            },
          ]
        }
      },
    ],
    layers: {
      selectType: "btn",
      optionLabel: "태그관리",
      tagHide: true,
      optionType: "radio",
    },
  },
];

const TabContainer = ({attr, changeParentsFn}) => {
  const {tabData, onClick } = attr;
  const {tab, datas, tagList } = tabData;
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const SelectSetCompRef = useRef({});

  const [fmVisible, setFmVisible] = useState(false);
  const handleFmOpen = (sid) => {
    setFmVisible(!fmVisible);
    changeParentsFn(sid);
  };

  const handleUpdateOpen = () => {
    SelectSetCompRef.current.handleBsClose();
    setFmVisible(!fmVisible);
  };

  const handleTaglayerOpen = () => {
    console.log("handleTaglayerOpen")
    changeParentsFn("tagLayer");
  };

  const handleFmClose = () => {
    setFmVisible(!fmVisible)
  };

  const handleMove = () => {
    window.location.href="/H-VOD/01000000";
  };

  const clipFmData1 = [
    {
      label: "클립구간",
      type: "p",
      value: "*화면에서 클립 구간을 설정해주세요.",
    },
    {
      label: "클립명",
      type: "text",
      id: "text",
      name: "text",
      value: "",
      required: "",
      placeholder: "클립명을 입력해주세요.",
      status: "",
      checked: "",
      readOnly: "",
      disabled: "",
      onChange: "",
      onKeyPress: "",
      onKeyUp: "",
      onBlur: "",
      copyToClipboard: "",
      autocomplete: "",
      formBtns: {
        noBtn : "",
        use: "",
        type: "",
        text: "",
        className: "",
        onClick: "",
        disabled: "",
      },
    },
    {
      label: "공개대상",
      type: "radio",
      id: "radio",
      name: "",
      value: "",
      required: true,
      status: "",
      shape:"",
      size:"",
      options: [
        {
          text:"전체공개",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:"팀공개",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:"비공개",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:"단체명 공개",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:"롬팀 공개",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:"비공개",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
      ],
    },
  ]

  const tagFmData1 = [
    {
      label: "팀정보",
      type: "select",
      id: "select",
      name: "",
      value: "",
      required: true,
      placeholder: "팀을 선택해주세요.",
      status: "",
      disabled: "",
      onChange: "",
      onKeyPress: "",
      onKeyUp: "",
      onBlur: "",
      options: [
        {
          text: "1번",
          value:"",
          status: "",
        },
        {
          text: "2번",
          value:"",
          status: "",
        },
        {
          text: "3번",
          value:"",
          status: "",
        },
      ],
      layers : {
        selectType: "select",
        optionLabel: "팀정보",
        tagHide: "",
        optionType: "radio",
      },
    },
    {
      label: "백넘버",
      type: "text",
      id: "text",
      name: "text",
      value: "",
      required: "",
      placeholder: "선수 백넘버를 입력해주세요.",
      status: "",
      checked: "",
      readOnly: "",
      disabled: "",
      onChange: "",
      onKeyPress: "",
      onKeyUp: "",
      onBlur: "",
      copyToClipboard: "",
      autocomplete: "",
      formBtns: {
        noBtn : "",
        use: "",
        type: "",
        text: "",
        className: "",
        onClick: "",
        disabled: "",
      },
    },
    {
      label: "공개대상",
      type: "radio",
      id: "radio",
      name: "",
      value: "",
      required: true,
      status: "",
      shape:"",
      size:"",
      options: [
        {
          text:"전체공개",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:"팀공개",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:"비공개",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:"단체명 공개",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:"롬팀 공개",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:"비공개",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
      ],
    },
  ]

  return (
    <>
      <div className={`tab-item ${tab}`}>
        <div className="content-item">
          {/*datas가 없을시*/}
          {tab === "highlight" ? (
            <dl className="nodata">
              <dd>등록된 하이라이트가 없습니다.</dd>
            </dl>
          ) : tab === "clip" ? (
            !fmVisible && (
              <dl className="nodata">
                <dt>클립을 생성해주세요.</dt>
                <dd>기록하고 싶은 장면을 클립으로 저장할 수 있습니다.</dd>
              </dl>
            )
          ) : tab === "tag" ? (
            !fmVisible && (
              <dl className="nodata">
                <dt>태그를 생성해주세요.</dt>
                <dd>기록하고 싶은 포인트를 쉽게 찾을 수 있도록 태그로 찍어주세요.</dd>
              </dl>
            )
          ) : (
            <dl className="nodata">
              <dd>BO 관리 <br />이미지 PDF 관리</dd>
            </dl>
          )}

          {fmVisible && (
            tab === "clip" ? (
              <>
                <div className="form-box">
                  <div className="form-container">
                    { clipFmData1.map((item, index) => (
                      <FormItem
                        key={index}
                        attr={item}
                      />
                    ))
                    }
                  </div>
                </div>

                <div className="btt-btn t1">
                  <BtnLayout
                    attr={[
                      {
                        className : 'w100',
                        text: '취소',
                        size : 'btn-lg',
                        color : 'btn-gray',
                        onClick:() => handleFmOpen(),
                      },
                      {
                        className : 'w100',
                        size : 'btn-lg',
                        color : 'btn-primary',
                        text: (tab === "clip" ? "클립" : "태그") +"저장",
                        onClick : "",
                      },
                    ]}
                    className="half"
                  />
                </div>
              </>
            ) : tab === "tag" ? (
              <>
                <div className="form-box">
                  <div className="form-container">
                    <div className="flex form-group">
                      <label className="col-form-label  req" htmlFor="text">태그시간</label>
                      <div className="col-form">
                        <div className="form-wrap  text">
                          <input id="text" name="text" type="text" className="form-control " placeholder="" autoComplete="off" value="" />
                        </div>
                      </div>
                    </div>

                    <div className="flex form-group">
                      <label className="col-form-label req" htmlFor="tagId">태그명</label>
                      <div className="col-form">
                        <div className="form-wrap button">
                          <Button attr={{
                            className: "dec t-btn",
                            text:"화면에서 태그를 선택해주세요.",
                            onClick : handleTaglayerOpen,
                          }} />
                          <input type="hidden" id="tagId" name="tagName" />
                        </div>
                      </div>
                    </div>

                    { tagFmData1.map((item, index) => (
                      <FormItem
                        key={index}
                        attr={item}
                      />
                    ))}
                  </div>
                </div>

                <div className="btt-btn t1">
                  <BtnLayout
                    attr={[
                      {
                        className : 'w100',
                        text: '취소',
                        size : 'btn-lg',
                        color : 'btn-gray',
                        onClick:() => handleFmOpen("tag"),
                      },
                      {
                        className : 'w100',
                        size : 'btn-lg',
                        color : 'btn-primary',
                        text: (tab === "clip" ? "클립" : "태그") +"저장",
                        onClick : "",
                      },
                    ]}
                    className="half"
                  />
                </div>
              </>
            ) : ("")
          )}

          {datas && !fmVisible && (
            <ul className="card-flex flex">
              {datas.map((item, index) => (
                <li key={index} className="f1">
                  <ClipCard
                    key={index}
                    title={item.title}
                    team={item.team}
                    content={item.content}
                    stadium={item.stadium}
                    img={item.img}
                    owner="my"
                  />
                </li>
              ))}
            </ul>
          )}

          {tagList && !fmVisible && (
            <div className="mark-container">
              <div className="chk-label t-btn">
                <BubbleTab tabs={bubbleTabData} />
              </div>

              <ul className="mark-list">
                {tagList.map((item, index) => (
                  <li key={index}>
                    <div className="flex-tag">
                      <div className="col col-mark"><p className={`mark ellip ${item.mark}`}>{item.dec}</p></div>
                      <div className="col col-tag">{item.tag}</div>
                      <div className="col col-time">{item.time}</div>
                      <div className="col col-btn">
                        {item.reported ? (
                          <SelectSet
                            attr={{
                              label: "태그관리",
                              options : [
                                {
                                  type: "btn",
                                  text: "수정하기",
                                  onClick: handleUpdateOpen,
                                },
                                {
                                  type: "pop",
                                  text: "삭제하기",
                                  popDatas:{
                                    popBtn: "태그를삭제",
                                    title: "생성하신 태그를\n삭제하시겠습니다?",
                                    button: {
                                      text: "삭제하기",
                                      onClick: "",
                                    },
                                  },
                                },
                              ],
                              layers: {
                                selectType: "btn",
                                optionLabel: "태그관리",
                                tagHide: "",
                                optionType: "btn",
                              },
                              buttons: {
                                text: "기타",
                                className: "ico-etc",
                                iconOnly: true,
                                btnText: "기타",
                              },
                            }}
                            ref={SelectSetCompRef}
                          />
                        ) : (
                          <Button
                            attr={{
                              text: "신고하기",
                              className: "ico-alarm",
                              iconOnly: true,
                              onClick:handleMove
                            }}
                          />
                        )}
                      </div>
                    </div>
                  </li>
                ))}
              </ul>
            </div>
          )}
        </div>

        {(tab === "clip" || tab === "tag") && (
          !fmVisible && (
            <div className="btt-btn t1">
              <Button
                attr={{
                  className : 'w100',
                  text: (tab === "clip" ? "클립" : "태그") +"생성",
                  size : 'btn-lg',
                  color : 'btn-primary',
                  onClick:() => handleFmOpen(tab)
                }}
              />
            </div>
          )
        )}
      </div>
    </>
  )
};

export default TabContainer;
