// 반응형 해상도 공용 훅
import { useMediaQuery } from "react-responsive";

const useMediaQueryHook = () => {
  const isMobile = useMediaQuery({ minWidth: 320, maxWidth: 480 });
  const isTablet = useMediaQuery({ minWidth: 481, maxWidth: 860 });
  const isWeb = useMediaQuery({ minWidth: 861, maxWidth: 1024 });
  const isLargeWeb = useMediaQuery({ minWidth: 1025 });

  let currentMediaQuery;

  if (isMobile) {
    currentMediaQuery = "mobile";
  } else if (isTablet) {
    currentMediaQuery = "tablet";
  } else if (isWeb) {
    currentMediaQuery = "web";
  } else if (isLargeWeb) {
    currentMediaQuery = "large Web";
  }

  return { currentMediaQuery, isMobile, isTablet, isWeb, isLargeWeb };
};

export default useMediaQueryHook;
