import React, { useState, useContext, useEffect, useRef } from "react";
import { withRouter } from "react-router-dom";

import useMediaQueryHook from "hook/useMediaQuery";

import { societyCardData2 } from "../main/data";
import FormSearch from "components/molecule/search/FormSearch";
import SearchSection from "components/molecule/layout/SearchSection";
import BubbleTab from "components/atom/BubbleTab";

const Hass04000000 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); // 이전 스크롤 위치

  const handleScroll = () => {
    const currentScrollY = window.scrollY;
    setScrollY(currentScrollY);

    if (currentScrollY > prevScrollY && currentScrollY > 100) {
      setIsExpanded(false);
    } else if (currentScrollY < prevScrollY) {
      setIsExpanded(true);
    }

    setPrevScrollY(currentScrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollY]);

  const newSecData = [
    {
      section: "societyCard",
      datas: {
        totalCnt: "3",
        list: societyCardData2,
        cardType: "society",
      },
    },
  ];

  const secData = [
    {
      section: "societyCard",
      datas: {
        totalCnt: "5",
        list: societyCardData2,
        cardType: "society",
      },
    },
  ];

  //버블탭 selectSet setting
  const selectSetComponentRef = useRef({});
  const selectActiveRef = useRef({});
  const selectChangeHandler = (e) => {
    //BS cloas
    selectSetComponentRef.current.handleBsClose();
    //change label
    let label = e.target.dataset.label;
    let index = e.target.dataset.index;
    let newBubbleTabData = [...locationSelectData];
    newBubbleTabData[index].label = label;
    setlocationSelectData(newBubbleTabData);
    // tab active
    selectActiveRef.current.handleTabClick({label:label})
  };
  const [locationSelectData, setlocationSelectData] = useState([
    {
      select: true,
      selectedTab: "지역",
      onChange:selectChangeHandler,
      label: "지역",
      options: [
        {
          text: "모두보기",
          type:"radio",
        },
        {
          text: "행정권 1",
          type:"radio",
        },
        {
          text: "행정권 2",
          type:"radio",
        },
        {
          text: "행정권 3",
          type:"radio",
        },
      ],
    },
  ]);

  return (
    <>
      <div className="teamSetting_wrapper ass">
        {!isMobile && <div className="teamInfo_Page flexCC">소속</div>}
        <div className={`${isMobile ? "m-main" : ""}`}>
          <div className="filter_wrapper ass ">
            <BubbleTab
              tabs={locationSelectData}
              selectSetRef={selectSetComponentRef}
              ref={selectActiveRef}
            />
          </div>
        </div>
        <div className={"socierty_wrapper "}>
          <section className="sec">
            {newSecData && newSecData?.length !== 0 ? (
              <div className="socierty_new_content">
                <div className="socierty_label">신규신청</div>
                <SearchSection data={newSecData} />
              </div>
            ) : (
              ""
            )}
            {secData && secData?.length !== 0 ? (
              <SearchSection data={secData} />
            ) : (
              ""
            )}
          </section>
        </div>
      </div>
    </>
  );
};

export default withRouter(Hass04000000);
