import React, { useState } from "react";
import { withRouter } from "react-router-dom";
import Confirm from "components/molecule/alert/Confirm";
import Button from "components/atom/button/Button";
import AlertPopup from "components/organism/popup/AlertPopup";
import ImageSlider from "components/organism/Slider/ImageSlider";
import exImg from "../../../assets/images/example/boimages.png";
import EventSlider from "components/organism/Slider/EventSlider";
import BtnLayout from "components/molecule/layout/BtnLayout";

import useMediaQueryHook from "hook/useMediaQuery";

const ConfirmData = [
  {
    icon: true,
    popBtn: "강제 업데이트",
    title: "최신 버전 엡이 업데이트 되었어요!",
    dec: (
      <>
        새로운 기능 이용을 위해 반드시 <br /> 최신 버전의 애플리케이션을
        이용해주세요.
      </>
    ),
    alert: true,
    button: {
      text: "업데이트하기",
      onClick: "",
    },
  },
  {
    icon: true,
    popBtn: "선택 업데이트",
    title: "최신 버전 엡이 업데이트 되었어요!",
    dec: "원활한 앱 사용을 위해 업데이트 부탁드려요.",
    nowrap: true,
    button: [
      {
        text: "나중에 할게요",
        color: "btn-line-white",
        onClick: "",
      },
      {
        text: "업데이트하기",
        onClick: "",
      },
    ],
  },
  {
    popBtn: "다른 기기에서 로그인",
    title: (
      <>
        다른 기기에서 로그인하여 <br /> 이 기기에서는 자동으로
        <br /> 로그아웃 되었습니다.
      </>
    ),
    dec: (
      <>
        회원님이 로그인을 하지 않으셨다면
        <br /> 로그인 후 비밀번호를 변경해주세요.
      </>
    ),
    alert: true,
    nowrap: true,
    button: {
      text: "확인",
      onClick: "",
    },
  },
];

const Hcom01000000 = (props) => {
  const { currentMediaQuery, isMobile } = useMediaQueryHook();

  const [confirmOpen, setConfirmOpen] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
  const [showAlert, setShowAlert] = useState(false);

  const openConfirmHandler = (id) => {
    setConfirmOpen((el) => (el.confirmOpen !== id ? id : ""));
    if (confirmOpen === id) {
      setConfirmOpen(false);
    }
  };

  const handleTClick = () => {
    {
      isMobile ? setIsOpen(!isOpen) : setShowAlert(!showAlert);
    }
  };

  const alertData = (
    <>
      <div className="event_content">
        <div className="event_slider">
          <EventSlider images={[exImg, exImg, exImg]} />
        </div>
        <BtnLayout
          attr={[
            {
              className: "w100",
              text: isMobile ? "오늘 그만보기" : "다시보지않기",
              color: "btn-line-white",
              onClick: () => handleTClick(),
            },
            {
              className: "w100",
              text: "닫기",
              color: "btn-primary",
              onClick: () => handleTClick(),
            },
          ]}
          className="half"
        />
      </div>
    </>
  );
  return (
    <>
      <section className="sec">
        <div className="m-main">
          <ul className="flex">
            {ConfirmData.map((item, index) => (
              <li style={{ marginRight: "5px" }}>
                <button
                  key={index}
                  className="btn"
                  onClick={() => {
                    openConfirmHandler(index);
                  }}
                >
                  {item.popBtn}
                </button>
                {confirmOpen === index ? (
                  <Confirm
                    data={item}
                    onClose={openConfirmHandler}
                    id={index}
                  />
                ) : (
                  ""
                )}
              </li>
            ))}
            <Button
              attr={{
                text: "이벤트 팝업",
                onClick: () => handleTClick(),
              }}
            />
          </ul>
        </div>
      </section>
      {showAlert && <AlertPopup children={alertData} className={"p-0"} />}
    </>
  );
};

export default withRouter(Hcom01000000);
