import React, {useState} from "react";
import { withRouter } from "react-router-dom";
import Confirm from "components/molecule/alert/Confirm";

const alertData = {
  alert : true,
  className : "login",
  icon : "symbol",
  dec: "로그인 후 사용해주세요.",
  button: {
    text: "로그인하러 가기",
    onClick: "",
  },
}

const Hmain07000000 = (props) => {
  const [confirmOpen, setConfirmOpen] = useState("");

  const openConfirmHandler = (id) => {
    setConfirmOpen((el) => (el.confirmOpen !== id ? id : ""));
    if (confirmOpen === id) {
      setConfirmOpen("");
    }
  };

  return (
    <>
      <section className="sec">
        <div className="m-main">
          <Confirm data={alertData} />
        </div>
      </section>
    </>
  );
};

export default withRouter(Hmain07000000);
