import React, {useState} from 'react';
import Button from "components/atom/button/Button";
import Length from "components/molecule/form/Length";

const EtcButton = ({attr, lengthData}) => {
  const {
    noBtn,
    use,
    type,
    text,
    className,
    onClick,
    disabled,
    value,
    btnVisible,
    readOnly,
  } = attr

  return (
    <>
      {!noBtn && !readOnly && (
        <ul className="btn-flex">
          {lengthData && lengthData.maxLength && lengthData.lengthPosition !== "bottom" && (
            <li><Length attr={lengthData} /></li>
          )}
          {btnVisible || value && (
            <li className={`col-del`}>
              <Button
                attr={{
                  className: "ico-btn btn-del",
                  text: "삭제",
                  iconOnly: true,
                  //onClick: resetValuee,
                }}
              />
            </li>
          )}
          {use && (
          <li className="col-util">
            <Button
              attr={{
                className: `ico-btn ${className || ""} ${disabled ? " disabled" : ""}`,
                text: text,
                iconOnly: className === `btn-auth` ? false : true,
                widthTxt: className === "btn-auth" ? true : false,
                onClick: onClick,
                disabled: disabled,
              }}
            />
          </li>
          )}
        </ul>
      )}
    </>
  )
}

export default EtcButton;