import React, { useState, } from "react";
import { withRouter } from "react-router-dom";
import Summary from "components/pages/H-MY/Summary";
import FormItem from "components/molecule/form/FormItem";
import Button from 'components/atom/button/Button';
import BtnLayout from "components/molecule/layout/BtnLayout";
import ImgUpload from "components/molecule/form/ImgUpload";
import ButtonAlert from "components/molecule/alert/ButtonAlert";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const fromData = [
  {
    label: "종목",
    type: "text",
    id : "text1",
    name : "text1",
    value: "",
    required: true,
    placeholder: "종목을 선택해주세요",
    groupClassName: "",
    labelClassName: "",
    formClassName: "",
    status: "",
    readOnly: "",
    disabled: "",
    onChange: "",
    onKeyPress: "",
    onKeyUp: "",
    onBlur: "",
    copyToClipboard: "",
    autocomplete: "",
  },
  {
    label: "팀명(풀네임)",
    type: "text",
    id : "text2",
    name : "text2",
    value: "",
    required: true,
    placeholder: "팀명을 입력해주세요.",
    groupClassName: "",
    labelClassName: "",
    formClassName: "",
    status: "",
    readOnly: "",
    disabled: "",
    onChange: "",
    onKeyPress: "",
    onKeyUp: "",
    onBlur: "",
    copyToClipboard: "",
    autocomplete: "",
    etcs : {
      tip:"팀명을 입력해주세요",
      tipColor:"c-red",
    },
  },
  {
    label: "팀명(숏네임)",
    type: "text",
    id : "text3",
    name : "text3",
    value: "",
    required: true,
    placeholder: "팀명을 입력해주세요.",
    groupClassName: "",
    labelClassName: "",
    formClassName: "",
    status: "",
    readOnly: "",
    disabled: "",
    onChange: "",
    onKeyPress: "",
    onKeyUp: "",
    onBlur: "",
    copyToClipboard: "",
    autocomplete: "",
  },
]

const popData = [
  {
    text: '이미지용량',
    color: "btn-gray",
    size: "btn-xxs",
    alertDatas: {
      alert:true,
      dec:"이미지 용량이 너무 큽니다.\n250KB 미만의 이미지로 업로드해주세요.",
      button: {
        text: "확인",
        onClick: "",
      },
    }
  },
  {
    text: 'PNG, JPG',
    color: "btn-gray",
    size: "btn-xxs",
    alertDatas: {
      alert:true,
      dec: "PNG, JPG 파일만 업로드 가능합니다.",
      button: {
        text: "구매하기",
        onClick: "",
      },
    }
  },
]


const Hmy02000000 = (props) => {
  const [openRefund, setOpenRefund] = useState(false);
  const [openTerms, setOpenTerms] = useState(false);

  return (
    <div className="page-my page-wrapper bg-gray t-creatTeam">
      <div className="m-main">
        <div className="my-flex flex">
          <div className="col-summary">
            <Summary />
          </div>

          <div className="col-right">
            <div className="right-outer t3">
              <div className="bg-box">
                <header className="right-header">
                  <strong className="tit">팀 생성하기</strong>
                </header>
                <FullHeight attr={{pageHeader:true}}>
                  <ColTop>
                    <p className="top-tip">* 팀 홈 관리페이지에서 수정할 수 있습니다.</p>
                    <div className="form-container _wrap">
                      { fromData
                        .map((item, index) => (
                          <FormItem
                            key={index}
                            attr={item}
                          />
                        ))
                      }

                      <div className="flex form-group ">
                        <span className="col-form-label">파일첨부</span>
                        <div className="col-form">
                          <div className="form-wrap imgUpload">
                            <ImgUpload
                              attr={{
                                id:"file",
                                className:"",
                                dates:"",
                                name:"file",
                                value:"",
                                shape:"round",
                                single:true,
                              }}
                              type="file"
                            />
                          </div>
                        </div>
                      </div>
                      <ul className="blit-list">
                        <li>PNG, JPG 파일만 업로드 가능합니다.</li>
                        <li>250KB 미만의 이미지로 업로드해주세요.</li>
                      </ul>
                    </div>
                  </ColTop>
                  <ColBottom>
                    <BtnLayout
                      attr={[
                        {
                          className : 'w100',
                          text: '팀 생성하기  (1/3)',
                          disabled : true,
                          size : 'btn-lg',
                          color : 'btn-primary'
                        },
                      ]}
                      className="full"
                    />
                  </ColBottom>
                </FullHeight>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="_temp">
        {/*temp*/}
        {popData.map((item, index) => (
          <ButtonAlert attr={item} />
        ))}
      </div>
    </div>
  );
};

export default withRouter(Hmy02000000);
