import { useEffect, useRef } from "react";
import icon from "../../../assets/images/icon/ico_location.svg";

const Map = () => {
  const mapElement = useRef(null);
  const mapRef = useRef(null);

  useEffect(() => {
    const naver = window.naver;

    if (!mapElement.current || !naver) return;

    const location = new naver.maps.LatLng(37.5656, 126.9769);
    const mapOptions = {
      center: location,
      logoControl: false, // 네이버 로고 표시 X
      mapDataControl: false, // 지도 데이터 저작권 컨트롤 표시 X
      scaleControl: true, // 지도 축척 컨트롤의 표시 여부
      tileDuration: 200, // 지도 타일을 전환할 때 페이드 인 효과의 지속 시간(밀리초)
      zoom: 14, // 지도의 초기 줌 레벨
      zoomControl: true, // 줌 컨트롤 표시
      zoomControlOptions: { position: 9 }, // 줌 컨트롤 우하단에 배치
    };

    mapRef.current = new naver.maps.Map(mapElement.current, mapOptions);

    const markerContent = `
  <img src=${icon} />
`;

    const marker = new naver.maps.Marker({
      position: location,

      map: mapRef.current,
      icon: {
        content: markerContent,
        anchor: new naver.maps.Point(0, 50),
      },
    });
    const infoWindow = new naver.maps.InfoWindow({
      content: [
        `
    <div style="display: flex; flex-direction: row; position: absolute; left: 12px;  justify-content: center; align-items: center; width: 100%; padding: 6px; gap: 4px; position: relative; background: #304FFE; box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.15); border-radius: 66px;">
      <div style="display: flex; flex-direction: row; align-items: center; padding: 0px; gap: 4px; ">
        <div style="display: none;  background: #666666;"></div>
        <div style="display: flex; flex-direction: row; justify-content: center; align-items: center; padding: 0px; font-family: 'Pretendard'; font-style: normal; font-weight: 600; font-size: 11px; line-height: 13px; color: #FFFFFF;">
          광명시 야구소프트볼 협회
        </div>
        
      </div>
    </div>
  `,
      ].join(``),

      anchorSize: {
        width: 0,
        height: 10,
        borderColor: "transparent",
      },

      width: "100%",
      borderColor: "transparent",
      backgroundColor: "transparent",
    });

    naver.maps.Event.addListener(marker, "click", () => {
      if (infoWindow.getMap()) {
        infoWindow.close();
      } else if (mapRef.current !== null) {
        const position = marker.getPosition();
        infoWindow.setPosition(
          new naver.maps.LatLng()
          // position.lat() - 0.00015,
          // position.lng() - 0.0001
        ); // Y 좌표와 X 좌표 조정
        infoWindow.open(mapRef.current, marker);
      }
    });
  }, [mapElement.current]);

  return (
    <>
      <div
        ref={mapElement}
        style={{ minHeight: "350px", minWidth: "1000px" }}
        className="map_wapper"
      />
    </>
  );
};

export default Map;
