import React, { useState } from "react";
import DepthItem from "components/molecule/layer/DepthItem";

const DepthRow = ({ attr }) => {
  const { id, type, name, text, rows, onClick, onChange, allClick, popDatas, dataIndex } = attr;
  console.log("DepthRow", dataIndex)
  const [rowItem, setRowItem] = useState(false);

  const clickHandler = () => {
    setRowItem(!rowItem);
    if (allClick) allClick(); // onclick이 있을 경우 호출
  };

  return (
    <div className={`depth-flex flex ${rows ? "has-depth" : ""}`}>
      <DepthItem attr={{ id, type, name, text, onClick, onChange, popDatas, dataIndex }} />
      {rows && (
        <>
          <span onClick={clickHandler} className="more">
            전체
          </span>
          {rowItem && (
            <ul className="tag-container">
              {rows.datas.map((item, index) => (
                <li key={index}>
                  <DepthItem
                    attr={{
                      id: rows.name + "_" + index,
                      type: rows.type,
                      name: rows.name,
                      text: item.text,
                      value: item.value,
                      checked: item.checked,
                      onClick: item.onClick,
                      onChange: item.onChange,
                      dataIndex,
                    }}
                  />
                </li>
              ))}
            </ul>
          )}
        </>
      )}
    </div>
  );
};

export default DepthRow;
