const Sample = (props) => {
  const bgBlack = {
    backgroundColor:'#000',
    color:'#fff'
  }

  return (
    <>
      <PageHeader onClick="" title="Header" />

      <section className="sec" style={bgBlack}>
        <div className="m-main">
          <dl>
            <dt>Button Icon</dt>
            <dd style={{marginTop:`10px`}}>
              <ul className="btn-ul">
                {iconBtn.map((item, index) => (
                <li key={index}>
                  <Button
                    attr={item}
                  />
                </li>
                ))}
              </ul>
            </dd>
          </dl>
        </div>
      </section>

      <section className="sec">
        <div className="m-main">
          <dl>
            <dt>Button</dt>
            <dd style={{marginTop:`10px`}}>
              <ul className="btn-ul flex">
                {btnData.map((item, index) => (
                  <li key={index}>
                    <Button
                      attr={item}
                    />
                  </li>
                ))}
              </ul>
              <br />

              <BtnLayout
                attr={btnData2}
                className="full"
              />

              <br />
              <BtnLayout
                attr={btnData2}
                className="half"
              />
            </dd>
          </dl>
        </div>
      </section>

      <section className="sec">
        <SearchSection data={secData}/>
      </section>

      <section className="sec">
        <div className="m-main">
          <BubbleTab tabs={bubbleTabData} />
        </div>
      </section>

      <section className="sec">
        <div className="m-main">
          <div className="form-group">
            <FormSearch attr={{
              name: "",
              placeholder: "팀명을 검색해보세요.",
              onClick: "",
            }}
            />
          </div>

          <LatelyTag data={latelyTag} />
        </div>
      </section>
    </>
  );
};

export default withRouter(Sample);
