import React from "react";
import { Link } from 'react-router-dom';

const noticeData = [
  {
    title: "공지사항이 뿌려집니다.공지사항이 뿌려집니다",
    date : "22.05.09",
    onClick: "",
  },
  {
    title: "공지사항이 뿌려집니다.공지사항이 뿌려집니다",
    date : "22.05.09",
    onClick: "",
  },
  {
    title: "공지사항이 뿌려집니다.공지사항이 뿌려집니다",
    date : "22.05.09",
    onClick: "",
  },
]

const MyNotice = () => {
  return (
    <div className="notice-container">
      <div className="m-main">
        <ul className="notice-list">
          {noticeData.slice(0,1).map((item, index) => (
            <li key={index}>
              <Link to="" onClick={item.onClick} className="link-item flex">
                <p className="ellip">{item.title}</p>
                <span className="date more">{item.date}</span>
              </Link>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default MyNotice;
