import React from 'react';

// Todo 글자수 입력 카운트
const Length = ({attr}) => {
  const {
    tip,
    tipSize,
    tipColor,
    length,
    lengthPosition,
    maxLength,
    validMsg,
    inValidMsg,
    status,
  } = attr

  return (
    <>
      <span className={`length ${lengthPosition || ""}`}>{length}/{maxLength}</span>
    </>

  )
}

export default Length;