import React from "react";
import Button from "components/atom/button/Button";

const TagProgress = ({progressData}) => {
  return (
    <>
      <div className="tag-prograss">
        {progressData.map((item, index) => (
          <div key={index} className={`mark ${item.color}`} style={{left:item.left}}></div>
        ))}
      </div>
    </>
  )
};

export default TagProgress;
