
// input[type=checkbox] 입력값 핸들링
const handleCheck = (e, state, setState) => {
    let { name, checked, dataset } = e.target;
    // console.log("handleCheck :: ", name, checked, dataset, state)
    let nameGroup = Object.keys(state[dataset.group]);

    
    if(name === 'all'){
    // "전체" 체크박스 클릭 시 
        let obj = {};
        
        for(let i = 0; i < nameGroup.length; i++){
            if(checked === true) {
                obj[nameGroup[i]] = true;
            }
            else {
                obj[nameGroup[i]] = false;
            }
        }
        setState({ ...state, [dataset.group] : obj });
        
    }else if(name !== 'all'){
        if(checked === false){ // 하나라도 체크 해제 시, "전체" 체크 표시 X
            setState({ ...state, [dataset.group] : { ...state[dataset.group], all: false, [name]: checked} });
        }else{ 
            let trueCount = 0;

            for(let i = 0; i < nameGroup.length; i++){
                if(nameGroup[i] !== 'all' ){
                    Object.values(state[dataset.group])[i] ? ++trueCount : <></>;
                }
            }
            
            let result = 2; // 체크박스 클릭 시, state 값은 바로 반영되지 않아 false 상태이고, "전체" 항목도 false 상태이므로 (항목의 전체 개수) - (체크된 값) = 2 가 나오면 모두 true 가 되어야한다.
            let isAllChecked = (nameGroup.length - trueCount) === result ? true : false;

            // 항목 모두 체크 시, "전체" 체크 표시 O
            if(isAllChecked){
                let obj = {};
                
                for(let i = 0; i < nameGroup.length; i++){
                    obj[nameGroup[i]] = true;
                }
                setState({ ...state, [dataset.group]: obj });
                return null;
            }

            // 항목 하나만 체크할 시
            setState({ ...state, [dataset.group] : { ...state[dataset.group], [name]: checked} });
        }
    }   
}

export default handleCheck;

