import React, {useState} from 'react';
import { withRouter } from "react-router-dom";
import Summary from "components/pages/H-MY/Summary";
import CustomList from "components/molecule/list/CustomList";

const Hmy07300000 = (props) => {

  const [confirmType, setConfirmType] = useState(null);
  const [confirmOpen, setConfirmOpen] = useState(false);

  const handleToggleChange = (newState) => {
    if (!newState) {
      setConfirmType("toggle");
      setConfirmOpen(true);
    }
  };

  const dataItem = {
    className:"setting",
    datas:[
      {
        title: "서비스 알림",
        datas: [
          {
            content: "야간 서비스 알림 (21시~08시)",
            addText: "필수 알림, 팀 알림 제외",
            toggle: true,
            onChange: handleToggleChange,
          },
        ],
      },
      {
        title: "마케팅 활용 동의",
        datas: [
          {
            content: "개인정보 수집 이용동의 (선택)",
            addText: "쿠폰 및 마케팅 정보 알림",
            toggle: true,
            onChange: handleToggleChange,
          },
          {
            content: "앱 푸시 수신 동의",
            toggle: true,
            onChange: handleToggleChange,
          },
          {
            content: "SMS 수신 동의",
            toggle: true,
            onChange: handleToggleChange,
          },
          {
            content: "메일 수신 동의",
            toggle: true,
            onChange: handleToggleChange,
          },
        ],
      },
    ],
  }

  return (
    <>
      <div className="page-my page-wrapper">
        <div className="m-main">
          <div className="my-flex flex">
            <div className="col-summary">
              <Summary />
            </div>

            <div className="col-right">
              <div className="right-outer t3">
                <div className="bg-box">
                  <header className="right-header">
                    <strong className="tit">알림설정</strong>
                  </header>
                  <section>
                    <div className="m-main">
                      <CustomList dataItem={dataItem} />
                    </div>
                  </section>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default withRouter(Hmy07300000);
