import Button from "components/atom/button/Button";
import MyInfo from "components/molecule/mypage/MyInfo";
import React, { useState } from "react";
import { Route, Link } from "react-router-dom";
import no_img from "../../../../assets/images/card/no_member.png";
import handleLocation from "handler/handleLocation";

function DropDown(logout) {
  const myInfo = {
    name: "김호각",
    ball: "20,000",
    season: false,
    img: no_img,
  };

  return (
    <div className="dropdown-menu">
      <section className="sec-info">
        <div className="my-info">
          <div className="m-main">
            <div className="info-top flex">
              <img src={myInfo.img} />
              <storng className="name">
                {myInfo.name}
                <Link to="/H-MY/M1000000" className="more">
                  내 정보 관리
                </Link>
              </storng>
            </div>
            <div className="ball-box">
              <div className="ball-flex flex">
                <strong className="ico ball more">
                  {myInfo.ball} <small>뽈</small>
                </strong>
                <Button
                  attr={{
                    className: "btn-charge",
                    text: "충전",
                    icon: "charge",
                    onClick: handleLocation("/H-MY/B2000000"),
                    color: "btn-primary",
                  }}
                />
              </div>
              {!myInfo.season ? (
                <Button
                  attr={{
                    className: "btn-season",
                    icon: "season more",
                    text: "시즌권 구매 ",
                    onClick: handleLocation("/H-MY/B3000000"),
                    color: "btn-line-black",
                    size: "btn-xxs",
                  }}
                />
              ) : (
                <dl className="btt-flex flex">
                  <dt>
                    <span className="btn-season">
                      <span className="txt ico season">
                        {myInfo.season.title}
                      </span>
                    </span>
                  </dt>
                  <dd>
                    <span className="more c-black">{myInfo.season.date}</span>
                  </dd>
                </dl>
              )}
            </div>
          </div>
        </div>
      </section>
      <section className="info_menu">
        <Link to="/H-MY/M3010000">시청내역</Link>
        <Link to="/H-MY/03000000">내 소속</Link>
        <Link to="/H-MAIN/09000000">찜</Link>
        <Link to="/H-MY/00000000">마이페이지</Link>
      </section>
    </div>
  );
}

export default DropDown;
