import React from 'react';
import Input from "components/atom/form/Input";
import Button from "components/atom/button/Button";

import TempImg from "assets/images/example/bg_image_2.jpg";


const ImgUpload = ({attr, type}) => {
  const {
    id,
    className,
    dates,
    name,
    value,
    optionLabel,
    ref,
    onChange,
    disabled,
    checked,
    status,
    label,
    size,
    shape,
    required,
    single,
    max,
  } = attr;

  const handleChange = (e) => {};

  return (
    <ul className={`upload-flex flex ${single ? "_single" : ""} ${shape ? shape : ""}`}>
      <li>
        <div className="upload-box form">
          <span className="icon"></span>
          <Input attr={{
            type:type,
            id:id,
            name:name,
            value:value,
            onChange:onChange,
            checked:checked,
            disabled:disabled,
          }}
          />
        </div>
      </li>
      {!single && (
        <>
          <li>
            <div className="upload-box">
              <div className="img-box" style={{backgroundImage:`url(${TempImg})`}}></div>
              <Button
                attr={{
                  className: "btn-remove2",
                  text: "삭제",
                  onClick: "",
                  iconOnly: true
                }}
              />
            </div>
          </li>
          <li>
            <div className="upload-box">
              <div className="img-box" style={{backgroundImage:`url(${TempImg})`}}></div>
              <Button
                attr={{
                  className: "btn-remove2",
                  text: "삭제",
                  onClick: "",
                  iconOnly: true
                }}
              />
            </div>
          </li>
          <li>
            <div className="upload-box">
              <div className="img-box" style={{backgroundImage:`url(${TempImg})`}}></div>
              <Button
                attr={{
                  className: "btn-remove2",
                  text: "삭제",
                  onClick: "",
                  iconOnly: true
                }}
              />
            </div>
          </li>
        </>
      )}
    </ul>
  )
}

export default ImgUpload;