import { useState } from "react";
import { Link } from "react-router-dom";

// import SearchForm from './search/form.js';
import corporate from "../../../branding/corporate-info";

import { PageSettings } from "../../../config/page-settings.js";
import TopMenuNav from "./top-menu/top-menu-nav.js";
import Button from "components/atom/button/Button.js";
import SearchForm from "components/molecule/search/SearchForm";
import DropDown from "./dropdown/DropDown";
import LatelyTag from "components/molecule/search/LatelyTag";

function Header(mediaQuery) {
  const media = mediaQuery.mediaQuery;

  const [isSearchOpen, setIsSearchOpen] = useState(false);
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);

  // 최근 검색어
  const latelyTag = [
    // {
    //   text: "텍스트입력",
    //   onClick: "",
    // },
    // {
    //   text: "테니스",
    //   onClick: "",
    // },
    // {
    //   text: "텍스트입력",
    //   onClick: "",
    // },
    // {
    //   text: "테니스",
    //   onClick: "",
    // },
    // {
    //   text: "텍스트입력",
    //   onClick: "",
    // },
    // {
    //   text: "테니스",
    //   onClick: "",
    // },
    // {
    //   text: "텍스트입력",
    //   onClick: "",
    // },
    // {
    //   text: "테니스",
    //   onClick: "",
    // },
  ];
  // latelyTag 있을 시 최근 검색어 표기 됩니다.

  // 로그인 상태
  const [user, setUser] = useState({
    isLoggedIn: true, // true로 변경 시 로그인 된 화면과 드랍 다운 메뉴 확인 가능
    name: "김호각",
  });

  const toggleSearchForm = () => {
    setIsSearchOpen(true);
  };

  const toggleDropdown = () => {
    setIsDropdownOpen((prev) => !prev);
  };

  const handleLogout = () => {
    setUser({ isLoggedIn: false, name: "" });
    setIsDropdownOpen(false);
  };

  console.log("mediaQuery", media);

  return (
    <PageSettings.Consumer>
      {() => (
        <div id="header" className="header navbar-default">
          {media !== "mobile" ? (
            <div className="m-main">
              <div className="navbar-header flexSbC">
                <h1 id="logo">
                  <Link to="/" className="navbar-brand">
                    <div className="logo">
                      <img
                        src={corporate.identity}
                        alt="Hogak"
                        style={{ borderRadius: 3 }}
                      />
                    </div>
                  </Link>
                </h1>

                <TopMenuNav />

                <div className="wrap_service">
                  <div className={`wrap_search ${isSearchOpen ? "open" : ""}`}>
                    <SearchForm placeholder={"검색어를 입력해주세요."} />
                    {isSearchOpen && latelyTag && latelyTag.length !== 0 && (
                      <div className="lately_search">
                        <dl className="lately-container">
                          <dt>
                            <strong>최근 검색어</strong>
                            <span className="btn-ico ico link-del">
                              전체삭제
                            </span>
                          </dt>
                          <dd>
                            <LatelyTag data={latelyTag} />
                          </dd>
                        </dl>
                      </div>
                    )}
                    {!isSearchOpen && (
                      <button
                        id="btnSearch"
                        className="btn_search icon_20"
                        onClick={toggleSearchForm}
                      >
                        <span className="screen_out">검색</span>
                      </button>
                    )}
                  </div>

                  <div className="wrap_alarm">
                    <button id="btnAlarm" className="btn_alarm icon_20">
                      <span className="screen_out">알림</span>
                    </button>
                  </div>

                  <div className="wrap_mypage">
                    {user.isLoggedIn ? (
                      <div className="wrap_login">
                        <button
                          id="btnUser"
                          className="btn_user"
                          onClick={toggleDropdown}
                        >
                          {user.name}
                        </button>
                        {isDropdownOpen && <DropDown logout={handleLogout} />}
                      </div>
                    ) : (
                      <button id="btnLogin" className="btn_login">
                        <Link to="/login" style={{ textDecoration: "none" }}>
                          <span>로그인</span>
                        </Link>
                      </button>
                    )}
                  </div>
                </div>
              </div>
            </div>
          ) : (
            <>
              <div className="navbar-header flexSbC">
                <Link to="/" className="navbar-brand">
                  <div className="logo">
                    <img
                      src={corporate.identity}
                      alt=""
                      style={{ borderRadius: 3 }}
                    />
                  </div>
                </Link>

                <div className="wrap_service">
                  <div className="wrap_search">
                    <Link
                      to={{
                        pathname: "/H-MAIN/05000000",
                        state: {
                          label: "검색",
                        },
                      }}
                    >
                      <button id="btnSearch" className="btn_search icon_20">
                        <span className="screen_out">검색</span>
                      </button>
                    </Link>
                  </div>
                  <div className="wrap_alarm">
                    <Link
                      to={{
                        pathname: "/H-MAIN/08000000",
                        state: {
                          label: "알림",
                        },
                      }}
                    >
                      <button id="btnAlarm" className="btn_alarm icon_20">
                        <span className="screen_out">알림</span>
                      </button>
                    </Link>
                  </div>
                </div>
              </div>
              <TopMenuNav />
            </>
          )}
        </div>
      )}
    </PageSettings.Consumer>
  );
}

export default Header;
