import React, { useState, useContext, useEffect, useRef } from "react";
import { withRouter } from "react-router-dom";
import CardSlider from "components/organism/Slider/CardSlider";
import {
  liveCardData,
  scheduleCardData,
  clipCardData,
  videoCardData,
} from "components/pages/main/data";
import { Link } from "react-router-dom/cjs/react-router-dom";
import ScheduleCard from "components/molecule/Card/ScheduleCard";
import ClipCard from "components/molecule/Card/ClipCard";
import VideoCard from "components/molecule/Card/VideoCard";
import BubbleTab from "components/atom/BubbleTab";
import useMediaQueryHook from "hook/useMediaQuery";
import city_banner from "assets/images/example/city_banner.png";
import logo from "assets/images/example/city_logo.png";
import bschk from "assets/images/icon/Icon_bschk.svg";
import human from "assets/images/icon/icon_human.svg";
import video from "assets/images/icon/icon_ocarmer.svg";
import share from "assets/images/icon/icon_share.svg";
import hums from "assets/images/icon/Icon_bjoin.svg";
import time from "assets/images/icon/Icon_time.svg";
import zzim from "assets/images/icon/icon_zzim.svg";
import heartstroke from "assets/images/icon/Icon_heartstroke.svg";

import NotiSlider from "components/organism/Slider/NotiSlider";
import Tabs from "components/molecule/tabs/Tabs";
import ImageSlider from "components/organism/Slider/ImageSlider";
import useGetPageTitle from "hook/useGetPageTitle";
import AppHeader from "components/organism/header/AppHeader";
import BballContainer from "components/atom/BballContainer";
import Confirm from "components/molecule/alert/Confirm";
import CardList from "components/molecule/list/CardList";

const Hgcb00000000 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const slice = isMobile ? 1 : isTablet ? 2 : 3;

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); // 이전 스크롤 위치
  const [heart, setHeart] = useState(false); // 찜 상태
  const [state, setState] = useState("teammate"); // user state (join / wait / manager / teammate)
  const [status, setStatus] = useState(false); // 운영상태 true시 운영하지 않는 팀.
  const [memberCount, setMemberCount] = useState(30); // 가입 멤버 수

  const [confirmOpen, setConfirmOpen] = useState(false);

  const stateImg = state === "join" ? hums : state === "wait" && time;
  const stateText =
    state === "join"
      ? "가입하기"
      : state === "wait"
      ? "승인대기"
      : state === "manager"
      ? "매니저"
      : state === "teammate"
      ? "팀원"
      : "";
  const handleExpand = () => {
    const section = document.getElementById("video_expanded");

    if (section) {
      const sectionTop = section.getBoundingClientRect().top + window.scrollY;
      window.scrollTo({
        top: sectionTop,
        behavior: "smooth",
      });
    }

    setIsExpanded(true);
  };

  const handleScroll = () => {
    const currentScrollY = window.scrollY;
    setScrollY(currentScrollY);

    if (currentScrollY > prevScrollY && currentScrollY > 100) {
      setIsExpanded(false);
    } else if (currentScrollY < prevScrollY) {
      setIsExpanded(true);
    }

    setPrevScrollY(currentScrollY);
  };

  const openConfirmHandler = () => {
    setConfirmOpen(!confirmOpen);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollY]);

  const handleHeart = () => {
    setHeart(!heart);
  };

  const clipCard = {
    totalCnt: "6",
    list: clipCardData,
    cardType: "clip",
  };

  const videoCard = {
    totalCnt: "6",
    list: videoCardData,
    cardType: "video",
  };

  const societyData = {
    name: "호각아카데미 광명점",
    member: 30,
    video: 182,
  };

  const notiData = [
    {
      date: "8.26",
      noti: "경기 지연 관련 공지사항",
      new: true,
    },
    {
      date: "8.26",
      noti: "경기 지연 관련 공지사항",
      new: false,
    },
    {
      date: "8.26",
      noti: "경기 지연 관련 공지사항",
      new: true,
    },
  ];

  const selectSetComponentRef = useRef({});
  const selectActiveRef = useRef({});
  const selectChangeHandler1 = (e) => {
    //BS cloas
    selectSetComponentRef.current.handleBsClose();
    //change label
    let label = e.target.dataset.label;
    let index = e.target.dataset.index;
    let newBubbleTabData = [...selectOptionData];
    newBubbleTabData[index].label = label;
    setDelectOptionData(newBubbleTabData);
    // tab active
    selectActiveRef.current.handleTabClick({label:label})
  };
  const [selectOptionData, setDelectOptionData] = useState([
    {
      select: true,
      selectedTab: "전체",
      onChange:selectChangeHandler1,
      label: "전체",
      options: [
        {
          text: "모두보기",
          type:"radio",
        },
        {
          text: "라이브",
          type:"radio",
        },
        {
          text: "방송예정",
          type:"radio",
        },
        {
          text: "지난경기영상",
          type:"radio",
        },
      ],
    },
    {
      select: true,
      selectedTab: "구장",
      onChange:selectChangeHandler1,
      label: "구장",
      options: [
        {
          text: "모두보기",
          type:"radio",
        },
        {
          text: "공설야구장 축구코트",
          type:"radio",
        },
        {
          text: "동해체육관 배구코트 A",
          type:"radio",
        },
      ],
    },
  ]);

  const selectChangeHandler2 = (e) => {
    //BS cloas
    selectSetComponentRef.current.handleBsClose();
    //change label
    let label = e.target.dataset.label;
    let index = e.target.dataset.index;
    let newBubbleTabData = [...selectClipData];
    newBubbleTabData[index].label = label;
    setSelectClipData(newBubbleTabData);
    // tab active
    selectActiveRef.current.handleTabClick({label:label})
  };
  const [selectClipData, setSelectClipData] = useState([
    {
      select: true,
      selectedTab: "전체",
      onChange:selectChangeHandler2,
      label: "전체",
      options: [
        {
          text: "모두보기",
          type:"radio",
        },
        {
          text: "라이브",
          type:"radio",
        },
        {
          text: "방송예정",
          type:"radio",
        },
        {
          text: "지난경기영상",
          type:"radio",
        },
      ],
    },
    {
      select: true,
      selectedTab: "구장",
      onChange:selectChangeHandler2,
      label: "구장",
      options: [
        {
          text: "모두보기",
          type:"radio",
        },
        {
          text: "공설야구장 축구코트",
          type:"radio",
        },
        {
          text: "동해체육관 배구코트 A",
          type:"radio",
        },
      ],
    },
  ]);


  const ConfirmData = {
    title: "가입",
    dec: (
      <>
        가입할 수 있는 인원이 초과되었습니다. <br /> 매니저에게 문의해주세요!
      </>
    ),
    alert: true,
    button: {
      text: "확인",
      onClick: openConfirmHandler,
    },
  };

  const tabData = [
    {
      label: "영상",
      content: (
        <>
          <section className="sec video_section main team">
            <div className="m-main">
              <BubbleTab
                tabs={selectOptionData}
                selectSetRef={selectSetComponentRef}
                ref={selectActiveRef}
              />
              {/* <div id="video_expanded" className="card_slider">
                {isMobile
                  ? videoCardData.map((item, index) => (
                      <VideoCard
                        key={index}
                        title={item.title}
                        team1={item.team1}
                        team2={item.team2}
                        score={item.score}
                        stadium={item.stadium}
                        date={item.date}
                        img={item.img}
                      />
                    ))
                  : videoCardData
                      .slice(0, 6)
                      .map((item, index) => (
                        <VideoCard
                          key={index}
                          title={item.title}
                          team1={item.team1}
                          team2={item.team2}
                          score={item.score}
                          stadium={item.stadium}
                          date={item.date}
                          img={item.img}
                        />
                      ))}
              </div> */}
              <div id="video_expanded">
                <CardList data={videoCard} />
              </div>
            </div>
          </section>
        </>
      ),
    },

    {
      label: "클립",
      content: (
        <>
          <section className="sec video_section main team">
            <div className="m-main">
              <BubbleTab
                tabs={selectClipData}
                selectSetRef={selectSetComponentRef}
                ref={selectActiveRef}
              />

              {/* <div className="card_slider">
                {isMobile
                  ? clipCardData.map((item, index) => (
                      <ClipCard
                        key={index}
                        title={item.title}
                        team={item.team}
                        content={item.content}
                        stadium={item.stadium}
                        img={item.img}
                      />
                    ))
                  : clipCardData
                      .slice(0, 6)
                      .map((item, index) => (
                        <ClipCard
                          key={index}
                          title={item.title}
                          team={item.team}
                          content={item.content}
                          stadium={item.stadium}
                          img={item.img}
                        />
                      ))}
              </div> */}
              <div id="video_expanded">
                <CardList data={clipCard} />
              </div>
            </div>
          </section>
        </>
      ),
    },
  ];

  return (
    <>
      {isMobile && (
        <AppHeader
          className={"content"}
          pageTitle={societyData.name}
          scrolled={true}
        />
      )}
      {/* {isExpanded && isMobile && <AppHeader className={"content"} />} */}
      <div className="subHome_wrapper">
        <section className="subHome_section grp">
          <ImageSlider images={[city_banner]} overlay={true} status={status} />

          <div className={`m-drag_wrapper ${isExpanded ? "active" : ""}`}>
            <div className={`${!isMobile && "m-main"}`}>
              <div className="team_info_wrapper">
                <BballContainer bball={800} />
                <div className="team_info flexCC">
                  <div className="team_image_wrapper flexCC">
                    <img src={logo} />
                    <img className="chk" src={bschk} />
                  </div>
                  <div className="team_name">
                    <div>{societyData.name}</div>
                    <Link
                      to={{
                        pathname: "/H-GCB/05000000",
                        state: {
                          label: societyData.name,
                        },
                      }}
                    >
                      {"정보 보기 >"}
                    </Link>
                  </div>
                </div>
                <div className="info_container">
                  <div className="info_item">
                    <div className="info_item_num flexCC">
                      <img src={human} />
                      <span>{societyData.member}</span>
                    </div>
                    <Link
                      to={{
                        pathname: "/H-GCB/06000000",
                        state: {
                          label: "소속",
                        },
                      }}
                    >
                      {"소속 >"}
                    </Link>
                  </div>
                  <div className="info_item">
                    <div className="info_item_num flexCC">
                      <img src={video} />
                      <span>{societyData.video}</span>
                    </div>
                    <div onClick={handleExpand} style={{ cursor: "pointer" }}>
                      {"경기영상 >"}
                    </div>
                  </div>
                  <div className="info_item">
                    <img src={share} />
                    공유하기
                  </div>
                  <div className="info_item" onClick={handleHeart}>
                    <img src={!heart ? zzim : heartstroke} />찜
                  </div>
                  <div className="info_item">
                    <div
                      className="info_item_button"
                      onClick={
                        state === "join" && memberCount > societyData.member
                          ? openConfirmHandler
                          : null
                      }
                    >
                      {(state === "join" || state === "wait") && (
                        <Link
                          to={{
                            pathname:
                              state === "join"
                                ? "/H-GCB/01010000"
                                : state === "teammate"
                                ? "/H-GRP/04000000"
                                : "",
                            state: {
                              label:
                                state === "join"
                                  ? "가입하기"
                                  : state === "teammate"
                                  ? "호각아카데미 광명점"
                                  : "",
                            },
                          }}
                        >
                          <div>
                            <img src={stateImg} />
                          </div>
                        </Link>
                      )}

                      <Link
                        to={{
                          pathname:
                            state === "manager"
                              ? "/H-GCB/03000000"
                              : state === "teammate"
                              ? "/H-GCB/04000000"
                              : "/H-GCB/01010000",
                          state: {
                            label:
                              state === "join"
                                ? "가입하기"
                                : state === "teammate"
                                ? "호각아카데미 광명점"
                                : "",
                          },
                        }}
                      >
                        {stateText + ">"}
                      </Link>
                    </div>
                  </div>
                </div>
                {notiData && <NotiSlider data={notiData} />}
              </div>
            </div>
            <section className="sec live_section main team">
              <div className="m-main">
                <CardSlider data={scheduleCardData} type="schedule" />
              </div>
            </section>

            <Tabs tabs={tabData} sticky={true}/>
          </div>
        </section>
      </div>
      {confirmOpen && (
        <Confirm data={ConfirmData} onClose={openConfirmHandler} />
      )}
    </>
  );
};

export default withRouter(Hgcb00000000);
