import { useState } from "react";
import check from "../../assets/images/icon/orange_check.svg";

// type (free / season) 값에 따른 볼 컨테이너 content 분기
const BballContainer = ({ onClick, bball, type }) => {
  return type && type === "free" ? (
    <div className={`bball_wrap flexCC `} onClick={onClick}>
      <span>무료</span>
    </div>
  ) : type === "season" ? (
    <div className={`bball_wrap flexCC `} onClick={onClick}>
      <span>시즌권 무료시청</span>
    </div>
  ) : (
    <div className={`bball_wrap flexCC `} onClick={onClick}>
      <img src={check} />
      <span>{bball}뽈이 필요해요</span>
    </div>
  );
};

export default BballContainer;
