import React, {useState} from "react";
import { withRouter } from "react-router-dom";
import Confirm from "components/molecule/alert/Confirm";

const alertData = [
  {
    alert : true,
    popBtn : "구장 위치 불일치",
    dec: "구장과의 위치가 일치하지 않습니다.\n위치를 다시 확인해주세요.",
    button: {
      text: "확인",
      onClick: "",
    },
  },
  {
    alert : true,
    popBtn : "다시 한번 시도해주세요.",
    dec: "다시 한번 시도해주세요.",
    button: {
      text: "취소",
      onClick: "",
    },
  },
  {
    alert : true,
    popBtn : "QR코드를 다시 확인",
    dec: "동해시에 속한 장비가 아닙니다.\nQR코드를 다시 확인해주세요.",
    button: {
      text: "확인",
      onClick: "",
    },
  },
  {
    alert : true,
    popBtn : "라이브 예약",
    dec: "오늘 라이브 예약이 꽉 찼어요.\n다음에 이용해주세요.",
    button: {
      text: "확인",
      onClick: "",
    },
  },
  {
    alert : true,
    popBtn : "장비 점검",
    dec: "장비 점검 중입니다.\n다음에 이용해 주세요.",
    button: {
      text: "확인",
      onClick: "",
    },
  },
  {
    alert : true,
    popBtn : "예약중입니다",
    dec: "다른 사용자가 예약중입니다.",
    button: {
      text: "확인",
      onClick: "",
    },
  },
  {
    alert : true,
    className : "c-red",
    popBtn : "초상권",
    dec: "초상권 및 저작권 내용\n(호각공유예정)",
    button: {
      text: "확인",
      onClick: "",
    },
  },
]

const Hmain07020000P = (props) => {
  const [confirmOpen, setConfirmOpen] = useState(false);

  const openConfirmHandler = (id) => {
    setConfirmOpen((el) => (el.confirmOpen !== id ? id : ""));
    if (confirmOpen === id) {
      setConfirmOpen(false);
    }
  };

  return (
    <>
      <section className="sec">
        <div className="m-main">
          <ul className="flex">
            {alertData.map((item, index) => (
              <li style={{marginRight:"5px"}}>
                <button key={index} className="btn" onClick={ () => {openConfirmHandler(index)} }>{item.popBtn}</button>
                {confirmOpen === index ? <Confirm data={item} onClose={openConfirmHandler} id={index} /> : ""}
              </li>
            ))}
          </ul>
        </div>
      </section>
    </>
  );
};

export default withRouter(Hmain07020000P);
