import Badge from "components/atom/Badge";
import TeamItem from "components/molecule/Card/TeamItem";

import ball from "assets/images/card/icon_ball.svg";
import Skeleton from "react-loading-skeleton";

const LiveCard = ({
  title,
  status,
  team1,
  team2,
  stadium,
  score,
  date,
  calc,
  onClick,
  underline,
}) => {
  const testdata = null;
  return (
    <div className={`card_wrapper ${calc && "calc"} live `} onClick={onClick}>
      <ul className="card_top live">
        {testdata ? (
          <>
            <li className="col-left">
              <Badge
                attr={{
                  type: !status ? "live" : status,
                  live: !status || status === "live" ? true : false,
                }}
              />
              <div className="card_title">{title}</div>
            </li>
            <li className="live_icon">
              <img src={ball} />
              <dl className="game-info">
                <dt>{date}</dt>
                <dd>
                  <span className="stadium">{stadium}</span>
                </dd>
              </dl>
            </li>
          </>
        ) : (
          <Skeleton width={60} />
        )}
      </ul>
      <div className="card_bottom live">
        {testdata ? (
          <TeamItem
            teamLogo={team1.teamLogo}
            teamName={team1.teamName}
            className={`${underline && "underline"}`}
          />
        ) : (
          <div className="flexCC_col">
            <Skeleton circle width={40} height={40} />
            <Skeleton width={40} />
          </div>
        )}

        <div className="score_wrapper">
          {testdata ? (
            <>
              <div className="stadium">{stadium}</div>
              <div className="game_score">
                <span
                  className={`score ${team1.score > team2.score ? "_win" : ""}`}
                >
                  {team1.score}
                </span>
                <span>:</span>
                <span
                  className={`score ${team1.score < team2.score ? "_win" : ""}`}
                >
                  {team2.score}
                </span>
              </div>
              <div className="game_date">{date}</div>{" "}
            </>
          ) : (
            <Skeleton width={80} count={3} />
          )}
        </div>
        {testdata ? (
          <TeamItem teamLogo={team2.teamLogo} teamName={team2.teamName} />
        ) : (
          <div className="flexCC_col">
            <Skeleton circle width={40} height={40} />
            <Skeleton width={40} />
          </div>
        )}
      </div>
    </div>
  );
};

export default LiveCard;
