import React, {useState} from "react";
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom/cjs/react-router-dom";
import Button from 'components/atom/button/Button';

const Hvod_pop_1 = (props) => {
  return (
    <>
      <section className="pop-layer">
        <div className="pop-container">
          <div className="pop-content msg-container">
            <header className="pop-header">
              <strong className="tit">새로운 메시지</strong>
              <Link>취소</Link>
            </header>
            <div className="pop-content">
              <div className="top-flex flex">
                <dl className="col-left">
                  <dt>받는사람 :</dt>
                  <dd>
                    <input type="text" />
                  </dd>
                </dl>
                <Button attr={{
                  className : `ico-add`,
                  text: '추가',
                  iconOnly: true,
                }} />
              </div>

              <div className="msg-content">
                <div className="box">
                  <div className="img-box"></div>
                  <textarea
                    placeholder={`'2024 대학 야구 통합리그대회'\n내 경기 실시간 중계, AI 호각 앱에서 확인해보세요.`}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default withRouter(Hvod_pop_1);
