import React from "react";
import { Link } from "react-router-dom/cjs/react-router-dom";

/**
 *
 * @param {*} props tags.onclick : onclick
 * @param {*} props tags.text : text
 * @param {*} props dec : dec
 *
 */

const TagWrapper = ({ attr = {} }) => {
  const { tags, dec } = attr;
  return (
    <>
      <div className="tag-wrapper">
        <div className="col-top">
          <p className="dec ellip">{dec}</p>
        </div>
        <div className="col-btt">
          <ul className="tag-wrap">
            {tags.map((item, index) => (
              <li key={index}>
                <Link to="" onClick={item.onClick} className="tag-item">#{item.text}</Link>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </>
  )
};

export default TagWrapper;
