import moment from "moment";
const handleDate = (state, setState) => {
  return {
    minDateRange: (current) => {
      // default 는 전체 선택 가능 / maxDate 선택 시에 날짜 범위 제한
      if (state.salesEndDate) {
        return current.isBefore(state.salesEndDate);
      }
      return true;
    },
    maxDateRange: (current) => {
      return current.isAfter(moment(state.salesStartDate).add(-1, "days"));
    },
    minDateChange: (value) => {
      setState({
        ...state,
        salesStartDate: value.format("YYYY-MM-DD HH:mm:ss"),
      });
      // setState( {...state, startDate : value.startOf('day').format("YYYY-MM-DD HH:mm:ss")} )
    },
    maxDateChange: (value) => {
      if (value.format("HH:mm:ss") === "00:00:00") {
        setState({
          ...state,
          salesEndDate: value.endOf("day").format("YYYY-MM-DD HH:mm:ss"),
        });
      } else {
        setState({
          ...state,
          salesEndDate: value.format("YYYY-MM-DD HH:mm:ss"),
        });
      }
    },
  };
};
export default handleDate;
