import { Link } from "react-router-dom/cjs/react-router-dom";

const SocietyCard = ({
  title,
  teamLogo,
  team,
  member,
  bg,
  onClick,
  link,
  fixed,
}) => {
  return (
    <Link
      className="card_link"
      to={{
        pathname: link,
        fixed: { fixed },
      }}
    >
      <div className={`card_wrapper society bg_${bg}`} onClick={onClick}>
        <div className="col-logo">
          <img src={teamLogo} alt={title} />
        </div>
        <div className="col-dec">
          <strong>{title}</strong>
          <div className="flex-ul flex">
            <dl>
              <dt>소속팀</dt>
              <dd>{team}</dd>
            </dl>
            <dl>
              <dt>멤버</dt>
              <dd>{member}</dd>
            </dl>
          </div>
        </div>
      </div>
    </Link>
  );
};

export default SocietyCard;
