import React from 'react';
import Length from "components/molecule/form/Length";

// Todo 글자수 입력 카운트
const Etc = ({attr, lengthData}) => {
  const {
    tip,
    tipSize,
    tipColor,
    validMsg,
    inValidMsg,
    status,
  } = attr

  return (
    <>
      {/*{maxLength && lengthPosition !== "bottom" && (
        <Length attr={attr} />
      )}*/}

      {status && (
        <div
          className={
            status === "is-valid" ? "valid-feedback" :
            status === "is-invalid" ? "invalid-feedback" : ""
          }
        >
          status === "is-valid" ?
          (validMsg ? validMsg : "Looks good!") :
          (status === "is-invalid" ?
          (inValidMsg ? inValidMsg : "Please choose a unique and valid username.": "")
        </div>
      )}
    </>

  )
}

export default Etc;