import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";

import useMediaQueryHook from "hook/useMediaQuery";

import AppHeader from "components/organism/header/AppHeader";
import Button from "components/atom/button/Button";
import FormWrap from "components/molecule/form/FormWrap";
import handleChange from "handler/handleChange";
import BtnLayout from "components/molecule/layout/BtnLayout";
import PageHeader from "components/molecule/layout/PageHeader";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const Hass03020200 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); // 이전 스크롤 위치

  const handleScroll = () => {
    const currentScrollY = window.scrollY;
    setScrollY(currentScrollY);

    if (currentScrollY > prevScrollY && currentScrollY > 100) {
      setIsExpanded(false);
    } else if (currentScrollY < prevScrollY) {
      setIsExpanded(true);
    }

    setPrevScrollY(currentScrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollY]);

  console.log("isExpanded", isExpanded);

  const [teamData, setTeamData] = useState({
    name: "광명시 야구소프트볼 협회",
    foundation: "2011년 창단",
    manager: "홍길동 감독",
  });

  const [foundationForms, setFoundationForms] = useState([
    {
      id: "foundation-0",
      name: "foundation",
      type: "text",
      value: "",
      formClassName: "out",
      placeholder: "설립",
      onChange: (e) => handleChange(e, teamData, setTeamData),
      formBtns: {
        onClick: () => handleRemoveFoundationForm("foundation-0"),
        text: "삭제",
        className: "btn-remove",
        use: true,
      },
    },
    {
      id: "foundation-0",
      name: "foundation",
      type: "text",
      value: teamData.foundation,
      onChange: (e) => handleChange(e, teamData, setTeamData),
    },
  ]);

  const [managerForms, setManagerForms] = useState([
    {
      id: "manager-0",
      name: "manager",
      type: "text",
      value: "",
      formClassName: "out",
      placeholder: "감독",
      onChange: (e) => handleChange(e, teamData, setTeamData),
      formBtns: {
        onClick: () => handleRemoveManagerForm("manager-0"),
        text: "삭제",
        className: "btn-remove",
        use: true,
      },
    },
    {
      id: "manager-0",
      name: "manager",
      type: "text",
      value: teamData.manager,
      onChange: (e) => handleChange(e, teamData, setTeamData),
    },
  ]);

  const [additionalForms, setAdditionalForms] = useState([]);

  const handleAddForm = () => {
    const newFormSet = [
      {
        label: "",
        id: `additional-${additionalForms.length}-0`,
        name: "",
        type: "text",
        value: "",
        formClassName: "_remove",
        placeholder: "항목 (5자 이내 입력) EX. 설립, 항목",
        onChange: (e) => handleChange(e, teamData, setTeamData),
        formBtns: {
          onClick: () =>
            handleRemoveForm(`additional-${additionalForms.length}`),
          text: "삭제",
          className: "btn-remove",
          use: true,
        },
      },
      {
        label: "",
        id: `additional-${additionalForms.length}-1`,
        name: "",
        type: "text",
        value: "",
        placeholder: "항목에 대한 내용을 입력해주세요.",
        onChange: (e) => handleChange(e, teamData, setTeamData),
      },
    ];

    setAdditionalForms([...additionalForms, newFormSet]);
  };

  // 기본 값으로 팀 설립 및 감독 고정

  const handleRemoveFoundationForm = (id) => {
    setFoundationForms(foundationForms.filter((form) => form.id !== id));
  };

  const handleRemoveManagerForm = (id) => {
    setManagerForms(managerForms.filter((form) => form.id !== id));
  };

  const handleRemoveForm = (id) => {
    setAdditionalForms(
      additionalForms.filter((formSet) => formSet[0].id !== id)
    );
  };

  return (
    <>
      <div className="subHome_wrapper">
        <PageHeader onClick="" title={teamData.name} noBtn="true" />

        <div className="page page-wrapper">
          <FullHeight attr={{pageHeader:true}}>
            <ColTop>
              <section className="subHome_section teamInfo_edit flexCC">
          <dl className="edit-dl">
            <dt>협회 정보</dt>
            <dd>
              <div className={`team_edit_wrapper ${isMobile && "mobile"}`}>
                <div className="form-box">
                  {foundationForms.map((item, index) => (
                    <div className="form-container form-group _w100">
                      <FormWrap key={index} attr={item} />
                    </div>
                  ))}
                </div>
                <div className="form-box">
                  {managerForms.map((item, index) => (
                    <div className="form-container form-group _w100">
                      <FormWrap key={index} attr={item} />
                    </div>
                  ))}
                </div>
                {additionalForms.map((formSet, index) => (
                  <div key={index} className="form-box">
                    <div className="form-container form-group _w100">
                      <FormWrap attr={formSet[0]} />
                      <FormWrap attr={formSet[1]} />
                    </div>
                  </div>
                ))}
              </div>
              <div className="team_edit_btn_wrapper">
                <Button
                  attr={{
                    className: "btn-add",
                    text: "+ 추가하기",
                    size: "btn-lg",
                    color: "",
                    onClick: handleAddForm,
                  }}
                />
              </div>
            </dd>
          </dl>
        </section>
            </ColTop>
            <ColBottom>
              <div className="btn_wrapper">
                <BtnLayout
                  attr={[
                    {
                      className: "w100",
                      text: "저장",
                      size: "btn-lg",
                      color: "btn-primary",
                    },
                  ]}
                  className="full"
                />
              </div>
            </ColBottom>
          </FullHeight>
        </div>
      </div>
    </>
  );
};

export default withRouter(Hass03020200);
