import { createStore, applyMiddleware } from 'redux';
import promiseMiddleware from 'redux-promise';
import ReduxThunk from 'redux-thunk';
import ReduxLogger from 'redux-logger';
import rootReducer from '../reducers';
import { persistStore, persistReducer } from 'redux-persist';
import storage from 'redux-persist/lib/storage';

const persistConfig = {
    key: 'root',
    storage
}

const createStoreWithMiddleware = applyMiddleware(promiseMiddleware, ReduxThunk, ReduxLogger)(createStore);

const enhancedReducer = persistReducer(persistConfig, rootReducer);

export default function configureStore() {
    
    const store = createStoreWithMiddleware(enhancedReducer, window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__());
    const persistor = persistStore(store);

    return { store, persistor };
};
