import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";

import useMediaQueryHook from "hook/useMediaQuery";

import logo from "assets/images/example/city_logo.png";

import AppHeader from "components/organism/header/AppHeader";
import Button from "components/atom/button/Button";
import PageHeader from "components/molecule/layout/PageHeader";

const Hlea03000000 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); // 이전 스크롤 위치
  const [state, setState] = useState("manager"); // user state
  const [edit, setEdit] = useState(false); // 수정 ui 변경 state
  const [isOpen, setIsOpen] = useState(false); // 아코디언 상태 관리
  const maxLength = 65;

  const handleScroll = () => {
    const currentScrollY = window.scrollY;
    setScrollY(currentScrollY);

    if (currentScrollY > prevScrollY && currentScrollY > 100) {
      setIsExpanded(false);
    } else if (currentScrollY < prevScrollY) {
      setIsExpanded(true);
    }

    setPrevScrollY(currentScrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollY]);

  console.log("isExpanded", isExpanded);

  const handleToggle = () => {
    setIsOpen(!isOpen); // 아코디언 열림/닫힘 토글
  };

  const handleEdit = () => {
    setEdit(!edit); // 수정 ui 변경
  };

  const [data, setData] = useState({
    name: "K3 LEAGUE 2024",
    logo: logo,
    member: 30,
    video: 182,
    date: "2024.07.23(화)~2024.08.05(월)",
    officeAddress: "서울시 송파구 북항로 32번길 66 우앤이건물 201호",
    stadiumAddress: [
      "서울특별시 노원구 화랑대로 223",
      "서울특별시 노원구 화랑대로 223",
      "서울특별시 노원구 화랑대로 223",
      "서울특별시 노원구 화랑대로 223",
      "서울특별시 노원구 화랑대로 223",
      "서울특별시 노원구 화랑대로 223",
    ],
    website: "https://aisportstv.co.kr/",
    association:
      "1967년 4월 25일 서울 동대문야구장에서 열린 제1회 대회는 박정희 대통령이 직접 시구하며 전국적인 관심 속에 시작된 본 대회는 매년 ",
    participate: [
      "* 본 대회 참가선수는 해당년도 대한야구소프트볼 협회에 등록을 필한 자에 한함.",
      "* 본 대회 참가선수 전원은 상해보험 또는 학교안전공제회에 가입해야 함",
    ],
    rule: [
      "* 조별리그 순위결정 : 승점-승자승-타이브레이크-골득실(전체)-다득점(전체)-페어플레이-추첨",
      "* 각조 1,2위팀 / 20강 토너먼트 진출",
      "* 조별 리그 승점 : 승-3점, 무-1점, 패-0점",
      "* 경기시작 : 전, 후반 각 40분, 연장전 20분(10+10) 후 승부차기",
    ],
    supervise: "포항시",
    host: "LG, 제주삼다수, 육우자조금관리위원회",
  });

  return (
    <>
      {/* {isMobile && (
        <AppHeader className={"content"} pageTitle={data.name} />
      )} */}
      <div className="subHome_wrapper ass">
        <PageHeader onClick="" title={data.name} noBtn="false" share={true} />

        <section className="subHome_section teamInfo flexCC">
          <div className={`team_inforamtion ${edit && "edit"}`}>
            <>
              <div className="info-row">
                <span className="label">기간</span>
                <span href="" className="info">
                  {data.date}
                </span>
              </div>
              {data.stadiumAddress.map((item, index) => (
                <div className="info-row">
                  <span className="label">구장 주소</span>
                  <span className="info-btn">
                    {item}{" "}
                    <Button
                      attr={{
                        className: `ico-copy`,
                        iconOnly: true,
                      }}
                    />
                  </span>
                </div>
              ))}

              <div className="info-row">
                <span className="label">홈페이지</span>
                <a href={data.website} className="info">
                  {data.website}
                </a>
              </div>
              <div className="info-row">
                <span className="label-toggle">
                  리그 설명{" "}
                  <span className="toggle-icon" onClick={handleToggle}>
                    {isOpen || data.association.length >= maxLength ? (
                      <Button
                        attr={{
                          className: isOpen ? `ico-dn` : `ico-up`,
                          iconOnly: true,
                        }}
                      />
                    ) : (
                      ""
                    )}
                  </span>
                </span>
                <div className="info" style={{ cursor: "pointer" }}>
                  {isOpen || data.association.length <= maxLength ? (
                    data.association
                  ) : (
                    <>{data.association.substring(0, maxLength)}...</>
                  )}
                </div>
              </div>
            </>
          </div>
        </section>

        <section className="subHome_section etcInfo ass">
          <div className="etcInfo_wrapper">
            <div className="etcInfo_container">
              <div className="ico logo">참가자격 </div>
              <div className="etcInfo_content">
                {data.participate.map((item, index) => (
                  <div>
                    <span className="info">{item} </span>
                  </div>
                ))}
              </div>
            </div>
            <div className="etcInfo_container">
              <div className="ico logo">리그룰 </div>
              <div className="etcInfo_content">
                {data.rule.map((item, index) => (
                  <div>
                    <span className="info">{item} </span>
                  </div>
                ))}
              </div>
            </div>

            <div className="etcInfo_container">
              <div className="ico logo">주관 및 주최</div>
              <div className="etcInfo_content">
                <div>
                  <span className="label">주관</span>
                  <span className="info">{data.supervise} </span>
                </div>
                <div>
                  <span className="label">주최</span>
                  <span className="info">{data.host} </span>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  );
};

export default withRouter(Hlea03000000);
