import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";

import useMediaQueryHook from "hook/useMediaQuery";

import map from "assets/images/example/exMap.png";
import copy from "assets/images/icon/ico_copy.svg";
import phone from "assets/images/icon/ico_phone.svg";

import AppHeader from "components/organism/header/AppHeader";
import Button from "components/atom/button/Button";
import FormItem from "components/molecule/form/FormItem";
import handleChange from "handler/handleChange";
import { Link } from "react-router-dom/cjs/react-router-dom.min";
import BtnLayout from "components/molecule/layout/BtnLayout";
import Map from "components/molecule/Map/Map";
import { useLocation } from "react-router-dom/cjs/react-router-dom";
import Input from "components/atom/form/Input";

const Hgrp03020202 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();
  const location = useLocation();
  const [data, setData] = useState(location?.state?.data);

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); // 이전 스크롤 위치
  const [state, setState] = useState("manager"); // user state
  const [edit, setEdit] = useState(false); // 수정 ui 변경 state
  const [isOpen, setIsOpen] = useState(false); // 아코디언 상태 관리

  const handleScroll = () => {
    const currentScrollY = window.scrollY;
    setScrollY(currentScrollY);

    if (currentScrollY > prevScrollY && currentScrollY > 100) {
      setIsExpanded(false);
    } else if (currentScrollY < prevScrollY) {
      setIsExpanded(true);
    }

    setPrevScrollY(currentScrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollY]);

  console.log("isExpanded", isExpanded);

  const handleToggle = () => {
    setIsOpen(!isOpen); // 아코디언 열림/닫힘 토글
  };

  const handleEdit = () => {
    setEdit(!edit); // 수정 ui 변경
  };

  console.log("data", data);

  const [teamData, setTeamData] = useState({
    name: "동해시",
    member: 30,
    video: 182,
    phone: "02-262-7899",
    officeAddress: "서울시 송파구 북항로 32번길 66 우앤이건물 201호",
    stadiumAddress: "서울특별시 노원구 화랑대로 223",
    website: "https://aisportstv.co.kr/",
    association:
      "대한야구소프트볼협회 U-13 클럽 정식 승인 다양한 팀구성 - U13팀(선수반) 새싹팀, 꿈 나무팀, 유소년팀, 주니어팀으로 구성",
    foundation: "2011년 창단",
    manager: "홍길동 감독",
    trainingTime: "- 화,수,목,금 16:00~18:30 <br/> - 주말 14:30~17:00 ",
    trainingSite: "청라공촌유수지야구장 / 전용 실내야구연습장",
  });

  return (
    <>
      {/* {isMobile && (
        <AppHeader className={"content"} pageTitle={teamData.name} />
      )} */}
      {!isMobile ? (
        <div className="teamInfo_Page flexCC">{teamData.name}</div>
      ) : (
        ""
      )}
      <div className="page _full">
        <div className="subHome_wrapper address">
          <div className={`${!isMobile && "m-main"}`}>
            <section className="subHome_section teamInfo_map flexCC">
              <div className="map_wrapper flexCC">
                <Map />
              </div>
            </section>
          </div>

          <section className="subHome_section address_detail">
            <div className="address_wrapper">
              <tbody>
                <tr className={`item ${data?.className}`}>
                  <td>
                    {data?.address}
                    {data?.detailAddress && (
                      <p className="addText">{data?.detailAddress}</p>
                    )}
                  </td>
                </tr>
              </tbody>
              <Input
                attr={{
                  label: "",
                  labelClassName: "",
                  formClassName: "_wirte",
                  copyToClipboard: "",
                  id: "title",
                  name: "title",
                  type: "text",
                  value: data?.detailAddress,
                  onChange: (e) => handleChange(e, data, setData),
                  onKeyPress: "",
                  placeholder: "상세주소를 입력해주세요.(건물명, 동, 호)",
                  autocomplete: "",
                  noBtn: true,
                }}
              />
            </div>
          </section>
        </div>
        <div className="save-button">
          <BtnLayout
            attr={[
              {
                className: "w100",
                text: "저장",
                size: "btn-lg",
                color: "btn-primary",
              },
            ]}
            className="full"
          />
        </div>
      </div>
    </>
  );
};

export default withRouter(Hgrp03020202);
