import React, {useState} from "react";

import TabItem from 'components/molecule/tabs/TabItem';

const TabNav = ({attr}) => {
  const {children, ref, activeTab, onClick} = attr
  return (
    <ul className="tab-nav" ref={ref}>
      {children.map((child, index) => (
        <li key={index}>
          <TabItem
            key={child.props.label}
            label={child.props.label}
            attr={child.props.attr}
            onClick={onClick}
            activeTab={activeTab}
          />
        </li>
      ))}
    </ul>
  )
};

export default TabNav;
