const Hmain05000000 = (props) => {
  const [selectVisible, setSelectVisible] = useState(false);
  const openSelectHandler = () => {
    setSelectVisible(!selectVisible)
    console.log(selectVisible)
  };

  return (
    <>
      <div className="page-search">
        <div className="search-container">
          <div className="m-main">
            <SearchForm latelyTag={latelyTag} onClick={openSelectHandler}/>
          </div>
        </div>

        <section className="sec-kv">
          <div className="img-box" style={{backgroundColor:"#002365"}}></div>
        </section>

        <section className="sec-kv">

        </section>

        <SearchSection data={secData}/>

        <ResultSection data={reseltData}/>

        {selectVisible && (
          <SelectLayer data={selectData} onClick={openSelectHandler}/>
        )}
      </div>
    </>
  );
};

export default withRouter(Hmain05000000);
