import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom/cjs/react-router-dom";
import useMediaQueryHook from "../../../hook/useMediaQuery";
import Carousel from "components/organism/carousel/Carousel";
import CardSlider from "components/organism/Slider/CardSlider";

import ScheduleCard from "components/molecule/Card/ScheduleCard";
import ClipCard from "components/molecule/Card/ClipCard";
import VideoCard from "components/molecule/Card/VideoCard";
import BubbleTab from "components/atom/BubbleTab";
import CardList from "components/molecule/list/CardList";

import {
  kvData,
  liveCardData,
  scheduleCardData,
  clipCardData,
  videoCardData,
} from "./data";
import GuideAlert from "components/molecule/alert/GuideAlert";

const scheduleCard = {
  totalCnt: "3",
  list: scheduleCardData,
  cardType: "schedule",
};

const clipCard = {
  totalCnt: "3",
  list: clipCardData,
  cardType: "clip",
};

const videoCard = {
  totalCnt: "3",
  list: videoCardData,
  cardType: "video",
};

const Main = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const [selectedTab, setSelectedTab] = useState("대회");
  const [guideAlert, setGuideAlert] = useState(true);

  const handleTabChange = (label) => {
    setSelectedTab(label); // 탭 변경 시 상태 업데이트
  };

  const handleAlertChange = () => {
    setGuideAlert(false);
  };

  const slice = isMobile ? 3 : isTablet ? 2 : 3;
  return (
    <>
      <div className="mainPage">
        {isMobile && guideAlert && (
          <GuideAlert
            content={
              <>
                관심설정으로&nbsp;<a>나만의 메뉴</a>를 만들 수 있어요.
              </>
            }
            onClick={handleAlertChange}
          />
        )}
        <section className="sec carousel_section main">
          <Carousel data={kvData} />
        </section>
        <section className="sec live_section main">
          <div className="m-main">
            <header className="sec-header">
              <h2>지금 라이브</h2>
              <div className="more">
                <Link
                  to={{
                    pathname: "/main/01000000",
                    state: {
                      label: "지금라이브",
                    },
                  }}
                >
                  더보기
                </Link>
              </div>
            </header>

            <CardSlider data={liveCardData} />
          </div>
        </section>

        <section className="sec schedule_section main">
          <div className="m-main">
            <header className="sec-header">
              <h2>
                <span className="ico calendar">경기 일정</span>
              </h2>
              <div className="more">
                <Link
                  to={{
                    pathname:
                      selectedTab === "리그"
                        ? `/main/03000000`
                        : `/main/02000000`,
                    state: {
                      label:
                        selectedTab === "리그"
                          ? "리그 경기 일정"
                          : "대회 경기 일정",
                    },
                  }}
                >
                  더보기
                </Link>
              </div>
            </header>

            <BubbleTab
              tabs={[
                { label: "대회", onClick: () => handleTabChange("대회") },
                { label: "리그", onClick: () => handleTabChange("리그") },
              ]}
            />
            <CardList data={scheduleCard} />
            {/*<div className="card_slider">
              {scheduleCardData
                .slice(0, slice) // slice 값에 따라 카드 개수 조정
                .map((item, index) => (
                  <ScheduleCard
                    key={index}
                    progressType={item.progressType}
                    img={item.img}
                    startDate={item.startDate}
                    endDate={item.endDate}
                  />
                ))}
            </div>*/}
          </div>
        </section>

        <section className="sec clip_section main">
          <div className="m-main">
            <div className="clip_label">
              <div>최신 클립</div>
              <div className="more">
                <Link
                  to={{
                    pathname: "/main/04000000",
                    state: {
                      label: "최신 클립",
                    },
                  }}
                >
                  더보기
                </Link>
              </div>
            </div>
            <CardList data={clipCard} />
            {/*<div className="card_slider">
              {clipCardData
                .slice(0, slice) // slice 값에 따라 카드 개수 조정
                .map((item, index) => (
                  <ClipCard
                    key={index}
                    title={item.title}
                    team={item.team}
                    content={item.content}
                    stadium={item.stadium}
                    img={item.img}
                  />
                ))}
            </div>*/}
          </div>
        </section>

        <section className="sec video_section main">
          <div className="m-main">
            <div className="video_label">
              <div>최신 경기영상</div>
            </div>
            <CardList data={videoCard} />
            {/*<div className="card_slider">
              {videoCardData
                .slice(0, slice) // slice 값에 따라 카드 개수 조정
                .map((item, index) => (
                  <VideoCard
                    key={index}
                    title={item.title}
                    team1={item.team1}
                    team2={item.team2}
                    score={item.score}
                    stadium={item.stadium}
                    date={item.date}
                    img={item.img}
                  />
                ))}
            </div>*/}
          </div>
        </section>
      </div>
    </>
  );
};

export default withRouter(Main);
