import React from "react";
import { Link } from "react-router-dom/cjs/react-router-dom";
import LatelyTag from "components/molecule/search/LatelyTag";

/**
 *
 * @param {*} props tags.text : tag text
 * @param {*} props tags.onClick : onClick
 *
 */

const LatelySearch = (pros) => {
  const { latelyTag } = pros;

  return (
    <>
      <dl className="lately-container">
        <dt>
          <strong>최근 검색어</strong>
          <Link className="btn-ico ico link-del">전체삭제</Link>
        </dt>
        <dd>
          <LatelyTag data={latelyTag} />
        </dd>
      </dl>
    </>
  )
};

export default LatelySearch;
