import React from "react";
import Button from "components/atom/button/Button";

const quickBtn = [
  {
    text: "내정보",
    className : "btn-my info",
    onClick: "",
  },
  {
    text: "고객센터",
    className : "btn-my custom",
    onClick: "",
  },
  {
    text: "활동내역",
    className : "btn-my history",
    onClick: "",
  },
]
const MyQuick = () => {
  return (
    <ul className="my-nav flex">
      {quickBtn.map((item, index) => (
        <li key={index}>
          <Button
            attr={{
              className: item.className,
              text: item.text,
              onClick: item.onClick,
              widthTxt:true
            }}
          />
        </li>
      ))}
    </ul>
  );
};

export default MyQuick;
