/**
 *
 * @param {*} props type : default bbs || nodata
 * @param {*} props title : title
 * @param {*} props time : time
 * @param {*} props onClick : onClick
 *
 */

const BbsCard = ({ type, title, dec, time, onClick, status, className }) => {
  return (
    <>
      {type === "nodata" ? (
        <div className={`nodata-box ${className ? className :""}`}>
          <p className="dec">
            {title && <dt>{title}</dt>}
            {dec}
          </p>
        </div>
      ) : (
        <dl className={`bba-card ${status ? status : ""}`} onClick={onClick}>
          <dt>{title}</dt>
          <dd>
            <p className="dec">{dec}</p>
            <small className="time">{time}</small>
          </dd>
        </dl>
      )}
    </>
  );
};

export default BbsCard;
