import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";
import Summary from "components/pages/H-MY/Summary";
import useMediaQueryHook from "hook/useMediaQuery";
import CustomList from "components/molecule/list/CustomList";
import BtnLayout from "components/molecule/layout/BtnLayout";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const Hmy05010100 = (props) => {

  const [data, setData] = useState({
    title:"동해시 공설운동장 테니스 A코트",
    stadium:"2024.11.15 잠실야구장",
    date:"2024년 10월 24일 12:00~14:55",
    teams:[
      {
        team:"판교테니스팀",
        score:"12",
      },
      {
        team:"호각테니스팀",
        score:"2",
      }
    ]
  })

  const [confirmType, setConfirmType] = useState(null);
  const [confirmOpen, setConfirmOpen] = useState(false);

  const handleToggleChange = (newState) => {
    if (!newState) {
      setConfirmType("toggle");
      setConfirmOpen(true);
    }
  };

  const dataItem = {
    className:"setting",
    datas:[
      {
        title: false,
        datas: [
          {
            content: "영상 스코어보드 노출",
            addText: "영상에 스코어보드 노출을 설정할 수 있습니다.",
            toggle: true,
            onChange: handleToggleChange,
          },
          {
            content: "최종 스코어 관리",
            addText: "앱에서 점수 표기 여부를 설정할 수 있습니다.",
            toggle: true,
            onChange: handleToggleChange,
          },
        ],
      },
    ],
  }

  return (
    <>
      <div className="page-my page-wrapper bg-gray t-shoot">
        <div className="m-main">
          <div className="my-flex flex">
            <div className="col-summary">
              <Summary />
            </div>

            <div className="col-right">
              <div className="right-outer t3">
                <div className="bg-box">
                  <header className="right-header">
                    <strong className="tit">최종 스코어 관리</strong>
                  </header>
                  <FullHeight attr={{pageHeader:true}}>
          <ColTop>
            <div className="location-container">
              <strong className="title">{data.title}</strong>
              <header className="location-header">
                <strong className="stadium">{data.stadium}</strong>
                <p className="date">{data.date}</p>
                <small className="tip">* 카메라 정비시간으로 인해 촬영은 종료시작 5분전(55분)에 종료됩니다.</small>
              </header>
            </div>

            <div className="score-setting">
              <div className="m-main">
                <CustomList dataItem={dataItem} />
              </div>
            </div>

            <div className="vs-container">
              <div className="vs-box">
                <ul className="top-flex flex">
                  {data.teams.map((item, index) => (
                    <li>
                      <dl>
                        <dt>
                          <small>{index === 0 ? "홈팀" : "어웨이팀"}</small>
                          <strong className="team">{item.team}</strong>
                        </dt>
                        <dd>
                          <div className="box">{item.score}</div>
                        </dd>
                      </dl>
                    </li>
                  ))}
                </ul>
              </div>
              <p className="tip">
                *  최종 스코어를 입력해주세요.<br />
                * 수정 시 입력하신대로 변경됩니다.
              </p>
                          </div>
          </ColTop>
          <ColBottom>
            <BtnLayout
              attr={[
                {
                  className : 'w100',
                  text: '취소',
                  disabled : "",
                  size : 'btn-lg',
                  color : 'btn-gray',
                  onClick:"",
                },
                {
                  className : 'w100',
                  text: '저장하기',
                  disabled : "",
                  size : 'btn-lg',
                  color : 'btn-primary',
                  onClick:"",
                },
              ]}
              className="half"
            />
          </ColBottom>
        </FullHeight>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default withRouter(Hmy05010100);
