import React, {useState, useContext, useEffect, useRef} from "react";
import { withRouter } from "react-router-dom";
import useMediaQueryHook from "hook/useMediaQuery";
import logo from "assets/images/example/city_logo.png";
import bschk from "assets/images/icon/Icon_bschk.svg";
import FormItem from "components/molecule/form/FormItem";
import handleChange from "handler/handleChange";
import Button from "components/atom/button/Button";
import PageHeader from "components/molecule/layout/PageHeader";
import AlertPopup from "components/organism/popup/AlertPopup";
import Checkbox from "components/molecule/form/Checkbox";
import handleCheck from "handler/handleCheck";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const Hgrp01000000 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); // 이전 스크롤 위치
  const [isOpen, setIsOpen] = useState(false);
  const [showAlert, setShowAlert] = useState(false);

  const handleScroll = () => {
    const currentScrollY = window.scrollY;
    setScrollY(currentScrollY);

    if (currentScrollY > prevScrollY && currentScrollY > 100) {
      setIsExpanded(false);
    } else if (currentScrollY < prevScrollY) {
      setIsExpanded(true);
    }

    setPrevScrollY(currentScrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollY]);

  const [data, setData] = useState({
    team: "동해시",
    logo: logo,
    type: "player",
    name: "홍길동",
    position: "투수",
    school: "경원초등학교",
    birthdate: "2010/10/11",
    backNumber: "",
    state: "team", // radio state 값 (team / personal)
  });

  const [terms, setTerms] = useState({
    terms: {
      all: false,
      ownership: false,
      precautions: false,
    },
  });

  const chkData = [
    {
      id: "checkbox-ownership",
      label: <a href="/">영상 소유권 내용 &gt;</a>,
      name: "ownership",
      value: "",
      checked: terms.terms.ownership,
      onChange: (e) => handleCheck(e, terms, setTerms),
      shape: "round",
      dataGroup: "terms",
      require: true,
    },

    {
      id: "checkbox-precautions",
      label: <a href="/">가입 유의사항 내용 &gt;</a>,
      name: "precautions",
      value: "",
      checked: terms.terms.precautions,
      onChange: (e) => handleCheck(e, terms, setTerms),
      size: "small",
      shape: "round",
      dataGroup: "terms",
      require: true,
    },
  ];

  const handleTClick = () => {
    {
      isMobile ? setIsOpen(!isOpen) : setShowAlert(!showAlert);
    }
  };

  //selectSet setting
  const selectSetComponentRef = useRef({});
  const selectChangeHandler = (e) => {
    if(isMobile) {
      //BS cloas
      selectSetComponentRef.current.handleBsClose();
      //change label
      let label = e.target.dataset.label;
      let index = e.target.dataset.index;
      let newBubbleTabData = [...formGroup];
      newBubbleTabData[index].placeholder = label;
      setFormGroup(newBubbleTabData);
    }
  };
  const [formGroup, setFormGroup] = useState([
    {
      label: "",
      type: "radio",
      id: "state01",
      name: "state",
      value: "team",
      dataGroup: "state",
      placeholder: "",
      groupClassName: "m-b-0",
      labelClassName: "nolabel",
      formClassName: "",
      disabled: "",
      onChange: (e) => handleChange(e, data, setData),
      copyToClipboard: "",
      autocomplete: "",
      options: [
        {
          id: "state01",
          name: "state",
          text: "팀 선택",
          value: "team",
          size: "big",
          checked: data.state === "team",
        },
      ],
    },
    {
      label: "",
      type: "select",
      id: "selectID",
      name: "team",
      labelClassName: "nolabel",
      onChange: selectChangeHandler,
      options: [
        {
          value: "1",
          text: "팀1",
          type: "radio",
        },
        {
          value: "2",
          text: "팀2",
          type: "radio",
        },
        {
          value: "3",
          text: "팀3",
          type: "radio",
        },
        {
          value: "4",
          text: "팀4",
          type: "radio",
        },
        {
          value: "5",
          text: "팀5",
          type: "radio",
        },
      ],
      placeholder: "팀을 선택해주세요.",
      layers: {
        selectType: "select",
        optionLabel: "팀 선택",
        tagHide: "",
        optionType: "",
      },
    },
  ]);

  const formGroup2 = [
    {
      label: "",
      type: "radio",
      id: "state02",
      name: "state",
      value: "personal",
      dataGroup: "state",
      placeholder: "",
      groupClassName: "m-b-0",
      labelClassName: "nolabel",
      formClassName: "",
      disabled: "",
      onChange: (e) => handleChange(e, data, setData),
      copyToClipboard: "",
      autocomplete: "",
      options: [
        {
          id: "state01",
          name: "state",
          text: "개인으로 가입하기",
          value: "personal",
          shape: "",
          size: "big",
          checked: data.state === "personal",
        },
      ],
    },
  ];

  const headerData = (
    <header className="teamJoin_header">
      <Checkbox
        attr={{
          id: "checkbox-all",
          label: "아래 내용을 모두 확인하였습니다.",
          checked: terms.terms.all,
          onChange: (e) => handleCheck(e, terms, setTerms),
          size: "small",
          shape: "round",
          name: "all",
          className: "main-checkbox",
          dataGroup: "terms",
        }}
      />
      <Button
        attr={{
          className: `ico-close-black`,
          text: "닫기",
          iconOnly: true,
          onClick: () => handleTClick(),
        }}
      />
    </header>
  );
  const optionData = (
    <div className="teamJoin_content">
      {chkData.map((item, index) => (
        <Checkbox key={index} attr={item} />
      ))}
      <Button
        attr={{
          className: "w100",
          text: "가입신청하기",
          onClick: "",
          color: "btn-primary",
          size: "btn-lg",
          disabled: true,
        }}
      />
    </div>
  );

  const alertData = (
    <>
      <header className="teamJoin_header">
        <Checkbox
          attr={{
            id: "checkbox-all",
            label: "아래 내용을 모두 확인하였습니다.",
            value: "",
            checked: terms.terms.all,
            onChange: (e) => handleCheck(e, terms, setTerms),
            size: "small",
            shape: "round",
            name: "all",
            className: "main-checkbox",
            dataGroup: "terms",
          }}
        />
        <Button
          attr={{
            className: `ico-close-black`,
            text: "닫기",
            iconOnly: true,
            onClick: () => handleTClick(),
          }}
        />
      </header>
      <div className="teamJoin_content">
        <div className="select-content">
          {chkData.map((item, index) => (
            <Checkbox key={index} attr={item} />
          ))}
        </div>
        <Button
          attr={{
            className: "w100",
            text: "가입신청하기",
            onClick: "",
            color: "btn-primary",
            size: "btn-lg",
            disabled: true,
          }}
        />
      </div>
    </>
  );

  const kvRef = useRef(null);
  return (
    <>
      <div className="teamJoin_wrapper grp">
        <PageHeader onClick="" title="가입하기" noBtn="true" />
        <div className="assJoin_Page flexCC" ref={kvRef}>
          <div className="assJoin_image_wrapper flexCC">
            <img src={data.logo} />
            <img className="chk" src={bschk} />
          </div>
          {data.team}
        </div>

        <div className="page page-wrapper">
          <FullHeight attr={{pageHeader:true, kvRef:kvRef}}>
            <ColTop>
              <section className="subHome_section teamJoin">
                <div className="teamJoin_noti">
                  단체에 소속시킬 {isMobile && <br />} 팀 정보를 선택해주세요.
                </div>
                <div className={`teamJoin_form ${isMobile && "mobile"} flexCC`}>
                  <div className="form-container">
                    {formGroup.map((item, index) => (
                      <FormItem
                        key={index} attr={item}
                        dataIndex={index}
                        selectSetRef={selectSetComponentRef}
                      />
                    ))}
                  </div>
                </div>
                <div className={`teamJoin_form ${isMobile && "mobile"} flexCC`}>
                  <div className="form-container">
                    {formGroup2.map((item, index) => (
                      <FormItem key={index} attr={item} />
                    ))}
                  </div>
                </div>
              </section>
            </ColTop>
            <ColBottom>
              <div className="btn_wrapper">
                <Button
                  attr={{
                    className: "w100",
                    text: "가입신청하기",
                    onClick: () => handleTClick(),
                    color: "btn-primary",
                    size: "btn-lg",
                  }}
                />
              </div>
            </ColBottom>
          </FullHeight>
        </div>
      </div>
      {showAlert && <AlertPopup children={alertData} />}
    </>
  );
};

export default withRouter(Hgrp01000000);
