/**
 *
 * @param {*} props type : default game || training
 * @param {*} props team1.teamLogo : teamLogo
 * @param {*} props team1.teamName : teamName
 * @param {*} props team2.teamLogo : teamLogo
 * @param {*} props team2.teamName : teamName
 *
 */

const InfoCard = ({ type, team1, team2, onClick }) => {
  return (
    <div className={`card_wrapper info ${type ? type : ""}`} onClick={onClick}>
      <div className="info-flex felx">
        <div className="col-info">
          <dl>
            <dt>{type === "training" ? "내팀" : "홈팀"}</dt>
            <dd>
              <div className="team-box" onClick="">
                <img className="log" src={team1.teamLogo} />
                <p className="team">{team1.teamName}</p>
              </div>
            </dd>
          </dl>
        </div>

        {type !== "training" ? (
          <div className="col-info">
            <dl>
              <dt>어웨이팀</dt>
              <dd>
                <div className="team-box" onClick="">
                  <img className="log" src={team2.teamLogo} />
                  <p className="team">{team2.teamName}</p>
                </div>
              </dd>
            </dl>
          </div>
        ) : (
          ""
        )}
      </div>
    </div>
  );
};

export default InfoCard;
