import React, {useState, useContext, useEffect, useRef} from "react";
import { withRouter } from "react-router-dom";

import useMediaQueryHook from "hook/useMediaQuery";

import manager from "assets/images/icon/ico_mtype1.svg";
import player from "assets/images/icon/ico_mtype2.svg";
import family from "assets/images/icon/ico_mtype3.svg";
import concern from "assets/images/icon/ico_mtype4.svg";
import logo from "assets/images/example/ex_teamlogo.png";
import FormItem from "components/molecule/form/FormItem";
import BtnLayout from "components/molecule/layout/BtnLayout";
import handleChange from "handler/handleChange";
import Button from "components/atom/button/Button";
import handleLocation from "handler/handleLocation";
import PageHeader from "components/molecule/layout/PageHeader";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const Hteam06000000 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); // 이전 스크롤 위치

  const handleScroll = () => {
    const currentScrollY = window.scrollY;
    setScrollY(currentScrollY);

    if (currentScrollY > prevScrollY && currentScrollY > 100) {
      setIsExpanded(false);
    } else if (currentScrollY < prevScrollY) {
      setIsExpanded(true);
    }

    setPrevScrollY(currentScrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollY]);

  const kvRef = useRef(null);
  return (
    <>
      <PageHeader onClick="" title="가입하기" noBtn={true}/>
      <div className="subHome_wrapper teamJoin">
        <div className="teamJoin_Page flexCC" ref={kvRef}>
          <img src={logo} />
          잠실유소년야구단
        </div>
        <FullHeight attr={{pageHeader:true, kvRef:kvRef}}>
          <ColTop>
            <section className="subHome_section teamJoin">
          <div
            className={`teamJoin_btn_wrapper ${isMobile && "mobile"} flexCC`}
          >
            <Button
              attr={{
                text: "감독/코치진 가입",
                className: `white join_btn ${isMobile && "mobile"}`,
                imgProps: {
                  src: manager,
                  style: {
                    width: "50px",
                    height: "50px",
                  },
                },
                arrow: true,
                onClick: handleLocation("/H-TEAM/06010000"),
              }}
            />
            <Button
              attr={{
                text: "선수 가입",
                className: `white join_btn ${isMobile && "mobile"}`,
                imgProps: {
                  src: player,
                  style: {
                    width: "50px",
                    height: "50px",
                  },
                },
                arrow: true,
                onClick: handleLocation("/H-TEAM/06010000"),
              }}
            />
            <Button
              attr={{
                text: "가족 가입",
                className: `white join_btn ${isMobile && "mobile"}`,
                imgProps: {
                  src: family,
                  style: {
                    width: "50px",
                    height: "50px",
                  },
                },
                arrow: true,
                onClick: handleLocation("/H-TEAM/06010000"),
              }}
            />
            <Button
              attr={{
                text: "관계자 가입",
                className: `white join_btn ${isMobile && "mobile"}`,
                imgProps: {
                  src: concern,
                  style: {
                    width: "50px",
                    height: "50px",
                  },
                },
                arrow: true,
                onClick: handleLocation("/H-TEAM/06010000"),
              }}
            />
          </div>
        </section>
          </ColTop>
          <ColBottom />
        </FullHeight>
      </div>
    </>
  );
};

export default withRouter(Hteam06000000);
