import {useState} from "react";

const Input = ({ attr }) => {
  const {
    type,
    id,
    name,
    value,
    required,
    placeholder,
    formClassName,
    readOnly,
    disabled,
    onChange,
    onKeyPress,
    onKeyUp,
    onBlur,
    autocomplete,
    maxLength,
    checked,
    ref,
    dataGroup,
    label,
    dataIndex,
  } = attr;
  console.log("Input", dataIndex)
  const handleChange = (e) => {};
  const handleBlur = (e) => {};

  return (
    <input
      id={id}
      name={name}
      type={type || "text"}
      defaultValue={value}
      className={"form-control "+ (formClassName || "")}
      ref={ref}
      readOnly={readOnly}
      disabled={disabled}
      onChange={onChange || handleChange}
      onKeyPress={onKeyPress || handleChange}
      onKeyUp={onKeyUp || handleChange}
      onBlur={onBlur || handleBlur}
      placeholder={placeholder}
      autoComplete={autocomplete || "off"}
      defaultChecked={checked}
      maxLength={maxLength}
      data-group={dataGroup}
      data-label={label}
      data-index={dataIndex}
    />
  );
};

export default Input;
