/**
 *
 * @param {*} props type : badge 타입 [live / clip / game]
 * @param {*} props style : 스타일
 * @param {*} props text : badge 안 텍스트
 * @param {*} props img : img
 * @param {*} props live : text 옆 live 표시
 */

const Badge = ({ attr = {} }) => {
  const { type, style, text, live, img } = attr;

  const typeText =
    type === "live"
      ? "라이브"
      : type === "clip"
      ? "클립"
      : type === "game"
      ? "지난경기"
      : type === "yet"
        ? "예정"
      : "";

  return (
    <div className={`badge ${type}`} style={style}>
      {img && <img src={img} alt="badge" />}
      {text ? text : typeText}
      {live && <span className="badge_live" />}
    </div>
  );
};

export default Badge;
