import React, {useState} from "react";
import { Link } from "react-router-dom";
import Button from "components/atom/button/Button";
import Radio from "components/molecule/form/Radio";
import Checkbox from "components/molecule/form/Checkbox";
import ConfirmSet from "components/molecule/alert/ConfirmSet";

const DepthItem = ({attr}) => {
  const {
    id,
    type,
    name,
    to,
    onClick,
    onChange,
    text,
    value,
    checked,
    rows,
    popDatas,
    dataIndex,
  } = attr;
  console.log("DepthItem", dataIndex)
  return (
    <>
      {type === "btn" && (
        to ? (
          <Link to={to} className="link-item" >
            {text}
          </Link>
        ) : (
          <div className="link-item" onClick={onClick}>
            {text}
          </div>
        )
      )}

      {type === "radio" && (
        <Radio attr={{
          id:id,
          name:name,
          className:"none",
          label :text,
          value:value,
          checked:checked,
          onChange:onChange,
          dataIndex,
        }} />
      )}

      {type === "checkbox" && (
        <Checkbox attr={{
          id:id,
          name:name,
          className:"none",
          label :text,
          value:value,
          checked:checked,
          onChange:onChange,
          dataIndex,
        }} />
      )}

      {type === "pop" && (
        <ConfirmSet
          confirmDatas={popDatas}
          btnDatas={{
            text:text
          }}
        />
      )}
    </>
  );
};

export default DepthItem;
