import React from "react";

const Detail = ({ data }) => {
  const { type, title, date, content, status, labelColor, reply } = data;

  return (
    <div className={`bbs-container bbs-detail ${type === "qna" ? "qna" : ""}`}>
      {type === "qna" && (
        <span className={`status-label ${labelColor && labelColor}`}>{status}</span>
      )}
      <dl className="detail-wrap">
        <dt>
          <strong className="tit">{title}</strong>
          <span className="date">{date}</span>
        </dt>
        <dd>
          <div className="editor-wrap">
            {content}
          </div>
        </dd>
      </dl>

      {reply && (
        <div className="reply-container">
          <span className={`status-label ${reply.labelColor && reply.labelColor}`}>{reply.status}</span>
          <div className="detail-wrap reply">
            <div className="editor-wrap">
              {reply.content}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Detail;
