import React, { useState, forwardRef, useImperativeHandle } from 'react';
import useMediaQueryHook from "../../../hook/useMediaQuery";
import Sticky from 'react-sticky-el';
import TabNav from 'components/molecule/tabs/TabNav';
import TabItem from 'components/molecule/tabs/TabItem';

const TabsWrap = forwardRef((props, ref) => {
  const { children, active = 0, attr, changeParentsFn} = props;
  const { stickyRef, tabRef, tabContentRef } = attr;
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const [activeTab, setActiveTab] = useState(
    children[active].props.label
  );

  useImperativeHandle(ref, () => ({
    // 부모 컴포넌트에서 사용할 함수를 선언
    handleTabClick
  }))

  function handleTabClick(tabLabel) {
    setActiveTab(tabLabel);
    let sid = tabLabel === "태그" ? "tag" : "";
    changeParentsFn(sid);
  }

  return (
    <div className="tabs" ref={tabRef}>
      {currentMediaQuery !== "web" && currentMediaQuery !== "large Web" ? (
        <Sticky>
          <TabNav
            attr={{
              children:children,
              ref:stickyRef,
              activeTab:activeTab,
              onClick:handleTabClick,
            }}
          />
        </Sticky>
      ) : (
        <TabNav
          attr={{
            children:children,
            ref:stickyRef,
            activeTab:activeTab,
            onClick:handleTabClick,
          }}
        />
      )}
      <div className="tab-content" ref={tabContentRef}>
        {children.map((child) =>
          child.props.label === activeTab ? child.props.children : null
        )}
      </div>
    </div>
  );
});

export default TabsWrap;
