import React, {useRef, useState} from 'react';
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom/cjs/react-router-dom";
import Summary from "components/pages/H-MY/Summary";
import BtnLayout from "components/molecule/layout/BtnLayout";

const HmyB3000000 = (props) => {
  const [data, setData] = useState([
    {
      title:"365일 시즌권",
      ball:"6,000",
      dec:"호각 내 모든 경기 영상 365일 무제한 시청",
      tip:"* 특정 단체의 영상은 시청이 제한될 수 있습니다.",
      link:"/H-MY/B3010000",
    },
    {
      title:"30일 시즌권",
      ball:"6,000",
      dec:"호각 내 모든 경기 영상 30일 무제한 시청",
      tip:"* 특정 단체의 영상은 시청이 제한될 수 있습니다.",
      link:"/H-MY/B3010000",
    },
    {
      title:"7일 시즌권",
      ball:"700",
      dec:"호각 내 모든 경기 영상 7일 무제한 시청",
      tip:"* 특정 단체의 영상은 시청이 제한될 수 있습니다.",
      link:"/H-MY/B3010000",
    },
    {
      title:"대회 시즌권",
      ball:"",
      dec:"특정 대회 영상만 대회 시즌동안 무제한 시청 \n대회 홈에서 구입 가능합니다.",
      tip:"",
      link:"/H-MY/B3010000",
    },
    {
      title:"단체 30일 시즌권",
      ball:"",
      dec:"특정 지자체 등 단체 영상만 무제한 시청\n특정 단체 홈에서 구입 가능합니다.",
      tip:"",
      link:"/H-MY/B3010000",
    }
  ])
  return (
    <>
      <div className="page-my page-wrapper bg-gray t-season">
        <div className="m-main">
          <div className="my-flex flex">
            <div className="col-summary">
              <Summary />
            </div>

            <div className="col-right">
              <div className="right-outer t2">
                <header className="right-header">
                  <strong className="tit">시즌권</strong>
                </header>
                <section className="sec-wrap full-height">
                  <div className="bg-box w100">
                    <ul className="season-list">
                      {data.map((item, index) => (
                        <li key={index}>
                          <Link to={item.link} className="link-item">
                            <dl className="season-ticket">
                              <dt>
                                <strong className="title">{item.title}</strong>
                                {item.ball && (
                                  <strong className="price more">{item.ball}뽈</strong>
                                )}
                              </dt>
                              <dd>
                                <p className="dec">{item.dec}</p>
                                {item.tip && (
                                  <p className="tip">{item.tip}</p>
                                )}
                              </dd>
                            </dl>
                          </Link>
                        </li>
                      ))}
                    </ul>
                    <div className="btt-btn">
                      <BtnLayout
                        attr={[
                          {
                            className : 'w100',
                            text: '구입하기',
                            disabled : true,
                            size : 'btn-lg',
                            color : 'btn-primary'
                          },
                        ]}
                        className="full"
                      />
                    </div>
                  </div>
                </section>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default withRouter(HmyB3000000);
