/**
 *

 * @param {*} props label : 라벨
 * @param {*} props id : id 값
 * @param {*} props type : 라이브여부  default live || yet
 * @param {*} props name : input 변수
 * @param {*} props defaultValue : 초기값*
 * @param {*} props status : 디자인 효과 (is-valid / is-invalid)
 * @param {*} props onChange : input 값 변경 시
 * @param {*} props checked : 체크값

 *
 */

import no_img from "assets/images/card/no_member.png";

// Checkbox 호출 시, id는 다른 엘리먼트와 중복 가능성이 있어 GroupId 에 숫자를 붙이며,
// name 은 각 체크박스의 변수 역할을 하므로 고유하게 작성한다.

const MemberCard = ({
  type,
  img,
  name,
  position,
  school,
  birthdate,
  backNumber,
  parentName,
}) => {
  return (
    <div className="member-card">
      <div className="member-photo">
        <img src={img ? img : no_img} alt="member" />
        {backNumber && <span className="back-number">{backNumber}</span>}{" "}
      </div>
      <div className="member-info">
        <div className="name-position">
          <span className="name">{name}</span>
          <span className="position">{position}</span>
        </div>
        {type === "player" ? (
          <div className="info">
            <div className="school-info">
              <span className="label">학교</span>
              <span className="value">{school}</span>
            </div>
            <div className="birth-info">
              <span className="label">생년월일</span>
              <span className="value">{birthdate}</span>
            </div>
          </div>
        ) : type === "parent" ? (
          <div className="parent">({parentName})</div>
        ) : (
          ""
        )}
      </div>
    </div>
  );
};

export default MemberCard;
