import React, {useEffect, useRef, useState} from 'react';
import useMediaQueryHook from "../../../hook/useMediaQuery";
import Sticky from 'react-sticky-el';
import TabNav from 'components/molecule/tabs/TabNav';
import TabItem from 'components/molecule/tabs/TabItem';

const TabsWrap = ({ children, active, attr }) => {
  const stickyRef = useRef(null);
  const tabRef = useRef(null);
  const tabContentRef = useRef(null);

  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();
  const {sticky, stickyBase} = attr;

  function positionRest() {
    if(sticky) {
      const offsetHeight = stickyBase;
      stickyRef.current.style.setProperty('margin-top', offsetHeight +'px')
      tabRef.current.style.setProperty('margin-top', '-'+offsetHeight +'px')
      //stickyDatas.tabContent.current.style.setProperty('padding-top', offsetHeight +'px')
    }
  }

  useEffect(() => {
    if (sticky && isMobile) {
      const handleResize = () => {
        positionRest();
      }

      window.addEventListener("resize", handleResize);
      handleResize();

      return () => window.removeEventListener("resize", handleResize);
    }
  }, []);

  const [activeTab, setActiveTab] = useState(
    children[active ? active : 0].props.label
  );
  const handleTabClick = (tabLabel) => {
    setActiveTab(tabLabel);
  };

  return (
    <div className="tabs" ref={tabRef}>
      {isMobile ? (
        <>
          {sticky ? (
            <Sticky>
              <TabNav
                attr={{
                  children:children,
                  ref:stickyRef,
                  activeTab:activeTab,
                  onClick:handleTabClick,
                }}
              />
            </Sticky>
          ) : (
            <TabNav
              attr={{
                children:children,
                activeTab:activeTab,
                onClick:handleTabClick,
              }}
            />
          )}
        </>
      ) : (
        <TabNav
          attr={{
            children:children,
            activeTab:activeTab,
            onClick:handleTabClick,
          }}
        />
      )}
      <div className="tab-content" ref={tabContentRef}>
        {children.map((child) =>
          child.props.label === activeTab ? child.props.children : null
        )}
      </div>
    </div>
  );
};

export default TabsWrap;
