import React, { useState } from "react";
import { Link } from "react-router-dom/cjs/react-router-dom";

const PageFooter = () => {

  const [copyVisible, setCopyVisible] = useState(false);
  const  handleCopyClick = () => {
    setCopyVisible(!copyVisible);
  }
  return (
    <>
      <dl className="page-footer">
        <dt>
          <Link to="#" onClick={handleCopyClick} className={`btn-drop ${copyVisible ? "_open" : ""}`}>(주) 호각 사업자정보</Link>
          {copyVisible && (
            <div className="drop-wrap">
              <dl>
                <dt>㈜호각 대표이사</dt>
                <dd>000</dd>
              </dl>
              <dl>
                <dt>사업자등록번호</dt>
                <dd>000-00-00000</dd>
              </dl>
              <dl>
                <dt>통신판매신고번호</dt>
                <dd>가가00000</dd>
              </dl>
              <dl>
                <dt>주소</dt>
                <dd>경기도 성남시 분당구 판교역로 182, 한국반도체산업협회 2층 (삼평동 644)</dd>
              </dl>
              <dl>
                <dt>이메일문의</dt>
                <dd>help@hogak.co.kr</dd>
              </dl>
              <dl>
                <dt>제휴문의</dt>
                <dd>help@hogak.co.kr</dd>
              </dl>
              <dl>
                <dt>호스팅 제공자</dt>
                <dd>㈜ 호각</dd>
              </dl>
            </div>
          )}
        </dt>
        <dd>
          <p className="copy">Copyright 호각. All Rights Reserved.</p>
        </dd>
      </dl>
    </>
  );
};

export default PageFooter;
