/**
 *
 * @param {*} props label : 라벨
 * @param {*} props id : id 값
 * @param {*} props name : input 변수
 * @param {*} props defaultValue : 초기값*
 * @param {*} props status : 디자인 효과 (is-valid / is-invalid)
 * @param {*} props onChange : input 값 변경 시
 * @param {*} props checked : 체크값
 *
 */

// Checkbox 호출 시, id는 다른 엘리먼트와 중복 가능성이 있어 GroupId 에 숫자를 붙이며,
// name 은 각 체크박스의 변수 역할을 하므로 고유하게 작성한다.

const Checkbox = ({ attr }) => {
  const {
    id,
    status,
    name,
    className,
    onChange,
    checked,
    label,
    dataGroup,
    disabled,
    size,
    shape,
    require,
  } = attr;

  const checkboxClass = `checkbox checkbox-css ${
    status ? status : "checkbox-inline"
  } ${className} ${size} ${shape}`;

  return (
    <div key={id} className={checkboxClass}>
      <input
        type="checkbox"
        id={id}
        name={name || id}
        checked={checked}
        disabled={disabled}
        onChange={onChange}
        data-group={dataGroup}
      />
      <label htmlFor={id}>
        {require === true ? "(필수)" : require === false ? "(선택)" : ""}{" "}
        {label}
      </label>
    </div>
  );
};

export default Checkbox;
