const Hvod_pop_2 = (props) => {
  const [confirmOpen, setConfirmOpen] = useState(false);

  const openConfirmHandler = (id) => {
    setConfirmOpen((el) => (el.confirmOpen !== id ? id : ""));
    if (confirmOpen === id) {
      setConfirmOpen(false);
    }
  };

  const handleShowToast = (msg) => {
    return () => toast(msg, {
      position: "top-center",
    });
  };

  return (
    <>
      <section>
        <div className="m-main">
          <ul className="flex">

            {ConfirmData.map((item, index) => (
              <li key={index} style={{margin:"2px"}}>
                <button key={index} className="btn btn-xxs" onClick={ () => {openConfirmHandler(index)} }>{item.popBtn}</button>
                {confirmOpen === index ? <Confirm data={item} onClose={openConfirmHandler} id={index} /> : ""}
              </li>
            ))}
            <li style={{margin:"2px"}}>
              <Button
                attr={{
                  size:"btn-xxs",
                  text: "라이브 예정 toast",
                  onClick:handleShowToast("라이브 예정 경기입니다."),
                }}
              />
            </li>
            <li style={{margin:"2px"}}>
              <Button
                attr={{
                  size:"btn-xxs",
                  text: "저장 toast",
                  onClick:handleShowToast("저장되었습니다."),
                }}
              />
            </li>
          </ul>
        </div>
      </section>
    </>
  );
};

export default Hvod_pop_2;
