import React, {useRef, useState} from "react";
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom/cjs/react-router-dom";
import Button from "components/atom/button/Button";
import BubbleTab from "components/atom/BubbleTab";
import BbsCard from "components/molecule/Card/BbsCard";
import CardList from "components/molecule/list/CardList";

import {
  scheduleCardData,
  liveCardData,
} from "components/pages/main/data";

const scheduleData = [
  {
    date: "9/7(금)",
    datas : {
      totalCnt : "2",
      list : scheduleCardData,
      cardType : "schedule"
    }
  },
  {
    date: "9/1(월)",
    datas : {
      totalCnt : "2",
      list : scheduleCardData,
      cardType : "schedule"
    }
  },
]

const liveCard = {
  totalCnt : "2",
  list : liveCardData,
  cardType : "live"
}

const Hmain06000000 = (props) => {

  //버블탭 selectSet setting
  const selectSetComponentRef = useRef({});
  const selectActiveRef = useRef({});
  const selectChangeHandler = (e) => {
    //BS cloas
    selectSetComponentRef.current.handleBsClose();
    //change label
    let label = e.target.dataset.label;
    let index = e.target.dataset.index;
    let newBubbleTabData = [...bubbleTabData];
    newBubbleTabData[index].label = label;
    setBubbleTabData(newBubbleTabData);
    // tab active
    selectActiveRef.current.handleTabClick({label:label})
  };
  const [bubbleTabData, setBubbleTabData] = useState([
    {
      refesh: true,
      onClick : "",
      cnt : "1",
    },
    {
      select: true,
      selectedTab : "축구",
      label: "축구",
      onChange:selectChangeHandler,
      name:"bubble1",
      options: [
        {
          text: "전체보기",
          type:"radio",
        },
        {
          text: "축구1",
          type:"radio",
        },
        {
          text: "축구2",
          type:"radio",
        },
        {
          text: "축구3",
          type:"radio",
        },
        {
          text: "축구4",
          onClick : "",
        },
      ],
    },
    {
      select: true,
      selectedTab : "",
      label: "협회",
      onChange:selectChangeHandler,
      name:"bubble2",
      options: [
        {
          text: "전체보기",
          type:"radio",
        },
        {
          text: "가입협회1",
          type:"radio",
        },
        {
          text: "가입협회2",
          type:"radio",
        },
        {
          text: "가입협회3",
          type:"radio",
        },
        {
          text: "가입협회4",
          type:"radio",
        },
      ],
    },
    {
      select: true,
      selectedTab : "",
      label: "팀",
      onChange:selectChangeHandler,
      name:"bubble3",
      options: [
        {
          text: "전체보기",
          type:"radio",
        },
        {
          text: "가입팀명1",
          type:"radio",
        },
        {
          text: "가입팀명2",
          type:"radio",
        },
        {
          text: "가입팀명3",
          type:"radio",
        },
        {
          text: "가입팀명4",
          type:"radio",
        },
      ],
    },
    {
      select: true,
      selectedTab : "",
      label: "대회",
      onChange:selectChangeHandler,
      name:"bubble4",
      options: [
        {
          text: "전체보기",
          type:"radio",
        },
        {
          text: "가입소속 내 전체대회1",
          type:"radio",
        },
        {
          text: "가입소속 내 전체대회2",
          type:"radio",
        },
        {
          text: "가입소속 내 전체대회3",
          type:"radio",
        },
      ],
    },
    {
      select: true,
      selectedTab : "",
      label: "리그",
      onChange:selectChangeHandler,
      name:"bubble5",
      options: [
        {
          text: "전체보기",
          type:"radio",
        },
        {
          text: "가입소속 내 전체대회1",
          type:"radio",
        },
        {
          text: "가입소속 내 전체대회2",
          type:"radio",
        },
        {
          text: "가입소속 내 전체대회3",
          type:"radio",
        },
      ],
    },
  ])

  return (
    <>
      <div className="page-wrapper top-fix page-playSchedule">
        <div className="top-select">
          <div className="m-main">
            <BubbleTab
              tabs={bubbleTabData}
              selectSetRef={selectSetComponentRef}
              ref={selectActiveRef}
            />
          </div>
        </div>

        <section className="sec sec-video bg-white">
          <div className="m-main">
            <ul className="bbs-list flex">
              <li>
                <BbsCard
                  type="nodata"
                  dec={`팀/단체 가입 정보가 없어요!\n가입하시면 경기일정을 한눈에 볼 수 있어요.`}
                />
              </li>
              <li>
                <BbsCard
                  type="nodata"
                  dec={`예정된 경기일정이 없습니다.`}
                />
              </li>
              <li>
                <BbsCard
                  type="nodata"
                  dec={`호각인만 경기일정을 보실 수 있습니다.\n회원가입을 해주세요!`}
                />
              </li>
            </ul>
            <header className="mont-container">
              <Button attr={{
                className : `ico-prev`,
                text: '이전달',
                iconOnly: true,
                onClick: "",
              }}
              />
              <strong className="month">2024.09</strong>
              <Button attr={{
                className : `ico-next`,
                text: '다음달',
                iconOnly: true,
                onClick: "",
              }}
              />
            </header>
            <CardList data={liveCard}/>
          </div>
        </section>
      </div>
    </>
  );
};

export default withRouter(Hmain06000000);
