const Hvod00000000 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const vodRef = useRef(null);
  const stickyRef = useRef(null);
  const tabRef = useRef(null);
  const tabContentRef = useRef(null);
  const tabsRef = useRef(null);
  const tabComponentRef = useRef({});

  //vod control
  const [vodActive, setVodActive] = useState({
    clipLayer:false,
    multiLayer:false,
    tagLayer:false,
    tagBS:false,
    tagTime:false,
  });
  const VodStateChange = (sid) => {
    var newVodActive = {...vodActive};
    newVodActive.clipLayer = false;
    newVodActive.multiLayer = false;
    newVodActive.tagLayer = false;
    newVodActive.tagBS = false;
    newVodActive.tagTime = false;

    if(sid === "tag"){
      newVodActive.tagBS = true;
      newVodActive.tagTime = true;
    }
    if(sid === "tagLayer"){
      newVodActive.tagBS = true;
      newVodActive.tagTime = true;
      newVodActive.tagLayer = true;
    }
    if(sid === "tagLayerClose"){
      newVodActive.tagBS = true;
      newVodActive.tagTime = true;
      newVodActive.tagLayer = false;
    }
    if(sid === "clip"){
      newVodActive.clipLayer = true;
    }
    if(sid === "multiLayer"){
      newVodActive.multiLayer = true;
    }

    setVodActive(newVodActive);
    window.scrollTo(0, tabsRef.current.offsetTop);
  }

  //floatBtn
  const [floatBtnVisible, setFloatBtnVisible] = useState(false);
  const tabScroll = (label) => {
    setFloatBtnVisible(true)
    tabComponentRef.current.handleTabClick(label);
    window.scrollTo(0, tabsRef.current.offsetTop);
  }

  //for tab stiky
  const handleResize = () => {
    let winWidth = window.innerWidth;
    let vodHeight = winWidth * 0.64163;
    stickyRef.current.style.setProperty('margin-top', vodHeight +'px')
    tabRef.current.style.setProperty('margin-top', '-'+vodHeight +'px')
    tabContentRef.current.style.setProperty('padding-top', vodHeight +'px')
  };
  useEffect(() => {
    if (isMobile) {
      handleResize();
      window.addEventListener('resize', handleResize);
      return () => {
        window.removeEventListener('resize', handleResize); //clean up
      };
    }
  }, []);

  return (
    <>
      <div className="page-landing">
        <div className="m-main">
          <div className="vod-layout flex">
            <div className="vod-left">
              <div className="box-wrap">
                <section className="sec-vod">
                  <div className="vod-outer" ref={vodRef}>
                    <VodContainer
                      vodData={vodData}
                      vodActive={vodActive}
                      changeParentsFn={VodStateChange}
                    />
                  </div>
                </section >

                <section className="sec-info">
                  <div className="info-container">
                    <div className="m-main">
                      <VodHeader data={{
                        title:"2004 대학 야구 통합 리그대회",
                        time:"22시간전",
                        ball:"100",
                      }} />
                      <div className="tag-container">
                        <div className="top-flex flex">
                          <ul className="btn-ul">
                            <li>
                              <Button attr={{
                                size:"btn-xs",
                                color:"btn-line-white",
                                shape:"round",
                                onClick:() => tabScroll("클립"),
                                text:"클립",
                                icon: "clip",
                              }} />
                            </li>
                            <li>
                              <Button attr={{
                                size:"btn-xs",
                                color:"btn-line-white",
                                shape:"round",
                                onClick:() => tabScroll("태그"),
                                text:"태그",
                                icon: "tag",
                              }} />
                            </li>
                          </ul>
                          <p className="col-right">100뽈이 필요해요</p>
                        </div>
                        <TagWrapper attr={tagData} />
                      </div>
                    </div>
                  </div>

                  <div className="card-container">
                    <ul className="card-flex flex">
                      {liveCardData.slice(0, 1).map((item, index) => (
                        <li key={index} className="f1">
                          <LiveCard
                            title={item.title}
                            team1={item.team1}
                            team2={item.team2}
                            stadium={item.stadium}
                            score={item.score}
                            date={item.date}
                          />
                        </li>
                      ))}
                    </ul>
                  </div>

                  <TagWrapper attr={tagData} />
                </section>
              </div>
            </div>

            <div className="vod-tab">
              <section className="sec-tab">
                <div className="tab-container t4" ref={tabsRef}>
                  <TabsWrap
                    attr={{
                      stickyRef:stickyRef,
                      tabRef:tabRef,
                      tabContentRef:tabContentRef,
                    }}
                    ref={tabComponentRef}
                    changeParentsFn={VodStateChange}
                  >
                    <div label="하이라이트">
                      <TabContainer
                        attr={{tabData:tabHighlight}}
                      />
                    </div>

                    <div label="클립">
                      {/*<TabContainer
                        attr={{
                          tabData:tabClip,
                          onClick:clipFmClick,
                        }}
                      />*/}
                      <TabContainer
                        attr={{tabData:tabClip}}
                        changeParentsFn={VodStateChange}
                      />
                    </div>

                    <div label="태그">
                      <TabContainer
                        attr={{tabData:tabTag}}
                        changeParentsFn={VodStateChange}
                      />
                    </div>

                    <div label="경기분석">
                      <TabContainer
                        attr={{tabData:tabAnalysis}}
                      />
                    </div>
                  </TabsWrap>
                </div>
              </section>
            </div>
          </div>
        </div>
      </div>
      <div className="_temp">
        <Hvod_pop_2 />
      </div>
    </>
  );
};

export default withRouter(Hvod00000000);
