import React, { useContext } from "react";
import { Switch, Route, withRouter } from "react-router-dom";
import routes from "../../../branding/route";
import { PageSettings } from "../../../config/page-settings";
import classnames from "classnames";
import pageTitle from "../../../branding/page/page-title";

function Contents(props) {
  const { pageContentFullWidth, pageContentClass, pageContentInverseMode } =
    useContext(PageSettings);

  const pathname = props.location.pathname; // 상세 페이지 이동 용도 변수

  const layoutClasses = classnames(
    pageContentFullWidth && "content-full-width ",
    pageContentInverseMode && "content-inverse-mode ",
    pageContentClass
  );

  return (
    <div id="contents" className={layoutClasses}>
      <Switch>
        {routes.map((route, index) => (
          <Route
            key={index}
            path={route.path}
            exact={route.exact}
            component={route.component}
          />
        ))}
      </Switch>
    </div>
  );
}

export default withRouter(Contents);
