import React, { useState } from "react";
import Button from "components/atom/button/Button";
import DepthRow from "components/molecule/layer/DepthRow";
import TermSearch from "components/molecule/layer/TermSearch";
import AreaSelect from "components/molecule/layer/AreaSelect";

const SelectLayer = ({ layerAttr }) => {
  const { id, name, onChange, layers, options, close, status, dataIndex} = layerAttr;
  console.log("SelectLayer", dataIndex)
  const { selectType, optionLabel, tagHide, optionType, className } = layers;
  const [tagVisible, setTagVisible] = useState(false);
  const [selectedOption, setSelectedOption] = useState(null); // 선택된 옵션 상태
  const [headerTitle, setHeaderTitle] = useState(optionLabel); // 헤더 제목 상태

  const handleOptionClick = (option) => {
    setSelectedOption(option.rows.datas); // 선택된 옵션의 태그들을 저장
    setHeaderTitle(option.text); // 헤더 제목을 선택된 옵션으로 변경
  };

  return (
    <div className="bs-container">
      <div className="dim-close" onClick={close}></div>
      <div className={`select-btt ${className}`}>
        <header>
          {tagHide && (
            <>
              {!tagVisible && (
                <button
                  className="btn btn-tag"
                  onClick={() => setTagVisible(!tagVisible)}
                >
                  <span className="txt">태그숨기기</span>
                </button>
              )}
              {tagVisible && (
                <button
                  className="btn btn-tag"
                  onClick={() => setTagVisible(!tagVisible)}
                >
                  <span className="txt">태그보기</span>
                </button>
              )}
            </>
          )}
          {headerTitle}
          <Button
            attr={{
              className: `ico-close-black`,
              text: "닫기",
              iconOnly: true,
              onClick: close,
            }}
          />
        </header>
        <ul className="select-content">
          {optionType === "termSearch" ? (
            <TermSearch />
          ) : optionType === "areaSelect" ? (
            <AreaSelect />
          ) : (
            <>
              {!selectedOption ? (
                options.map((item, index) => (
                  <li key={index}>
                    <DepthRow
                      attr={{
                        id: name + "_" + index,
                        type: item.type ? item.type : "radio",
                        name: name,
                        text: item.text,
                        rows: item.rows,
                        onClick: item.onClick,
                        onChange: onChange,
                        allClick: () => handleOptionClick(item),
                        popDatas: item.popDatas,
                        close:close,
                        dataIndex,
                      }}
                    />
                  </li>
                ))
              ) : (
                <ul className="tag-list">
                  {selectedOption.map((tag, index) => (
                    <li key={index}>
                      <div className="link-item" onClick="">{tag.text}</div>
                    </li> // 선택된 태그들만 표시
                  ))}
                </ul>
              )}
            </>
          )}
        </ul>
      </div>
    </div>
  );
};

export default SelectLayer;
