import useMediaQueryHook from "hook/useMediaQuery";
import React, { useState } from "react";
import Slider from "react-slick";
import noImg from "../../../assets/images/no_background_img.png";

export default function EventSlider({
  images,
  overlay,
  status,
  title,
  slideCounter,
}) {
  const [currentSlide, setCurrentSlide] = useState(0); // 현재 슬라이드 상태

  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    afterChange: (current) => setCurrentSlide(current), // 슬라이드가 변경될 때마다 현재 슬라이드 업데이트

    responsive: [
      {
        breakpoint: 360,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          adaptiveHeight: true,
        },
      },
      {
        breakpoint: 860,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 1200,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <div className="event_slider">
      <Slider {...settings}>
        {images && images?.length !== 0 ? (
          images.map((image, index) => (
            <div key={index} className="slide">
              <img
                src={image}
                alt={`image ${index + 1}`}
                className="slide-image"
              />
            </div>
          ))
        ) : (
          <div className="slide">
            <img src={noImg} alt={`noImg`} className="slide-image" />
          </div>
        )}
      </Slider>
    </div>
  );
}
