import React, { useState } from "react";
import Button from "components/atom/button/Button";
const PageHeader = (props) => {
  const { onClick, title, noBtn, share, shareOnclick } = props;
  return (
    <header className="page-header">
      <div className="m-main">
        {noBtn ? (
          ""
        ) : (
          <Button
            attr={{
              className: "ico-back black",
              text: "뒤로가기",
              iconOnly: true,
              onlick: onClick ? onClick : "",
            }}
          />
        )}
        <h2 className="page-tit">{title}</h2>
        {share && (
          <Button
            attr={{
              className: `ico-share`,
              text: "공유하기",
              iconOnly: true,
              onlick: shareOnclick ? shareOnclick : "",
            }}
          />
        )}
      </div>
    </header>
  );
};

export default PageHeader;
