import React, {useRef, useState} from 'react';
import { withRouter } from "react-router-dom";
import Summary from "components/pages/H-MY/Summary";
import BubbleTab from "components/atom/BubbleTab";
import Button from "components/atom/button/Button";
import TabsWrap from 'components/molecule/tabs/TabsWrap';
import SelectSet from 'components/molecule/form/SelectSet';
import CardList from "components/molecule/list/CardList";
import SelectLayer from "components/molecule/layer/SelectLayer";
import "react-datetime/css/react-datetime.css";
import Datetime from "react-datetime";

import {
  clipCardData,
  videoCardData,
} from "components/pages/main/data";

const cardData1 = {
  totalCnt : "5",
  list : videoCardData,
  cardType : "video",
  className : "t2",
}

const cardData2 = {
  totalCnt : "5",
  list : videoCardData,
  cardType : "video",
  className : "t2",
}

const cardData3 = {
  totalCnt : "5",
  list : clipCardData,
  cardType : "clip",
  className : "t2",
}

const bubbleTabData1 = [
  {
    label: "영상",
  },
  {
    label: "클립",
  },
]

const bubbleTabData3 = [
  {
    select: true,
    selectedTab : "전체",
    label: "전체",
    options: [
      {
        text: "모두보기",
        value: "",
        name: "s1",
      },
      {
        text: "라이브",
        value: "",
        name: "s1",
      },
      {
        text: "방송예정",
        value: "",
        name: "s1",
      },
      {
        text: "지난경기영상",
        value: "",
        name: "s1",
      },
      {
        text: "클립",
        value: "",
        name: "s1",
      },
    ],
  },
]

const HmyM3010000 = (props) => {

  const [termVisible, setTermVisible] = useState(false);
  const handleTabClick = () => {
    setTermVisible(!termVisible);
  };

  const termLayer = {
    layers:{
      optionLabel: "조회",
      optionType:"termSearch",
      className:"term-layer",
    },
    close : () => setTermVisible(!termVisible)
  }

  const [data, setData] = useState({
    month:"1개월",
  })

    return (
    <>
      <div className="page-my page-wrapper t-history">
        <div className="m-main">
          <div className="my-flex flex">
            <div className="col-summary">
              <Summary />
            </div>

            <div className="col-right">
              <div className="right-outer t3">
                <div className="bg-box">
                  <header className="right-header">
                    <strong className="tit">활동내역</strong>
                  </header>
                  <section className="sec-tab">
          <div className="tab-container t3">
            <TabsWrap attr={{
              sticky:true,
              stickyBase:"36",
            }}>
              <div label="시청내역" attr="1">
                <div className="m-main">
                  <div className="top-flex">
                    <div className="col-left">
                      <BubbleTab tabs={bubbleTabData1} />
                    </div>

                    <div className="col-right">
                      <Button attr={{
                        text: data.month,
                        className: "selectLayer",
                        onClick:handleTabClick,
                      }}/>
                    </div>
                  </div>

                  <div className="list-container">
                    <CardList data={cardData1} />
                  </div>
                </div>
              </div>

              <div label="내촬영영상">
                <div className="m-main">
                  <div className="top-flex">
                    <div className="col-left"></div>

                    <div className="col-right">
                      <Button attr={{
                        text: data.month,
                        className: "selectLayer",
                        onClick:handleTabClick,
                      }}/>
                    </div>
                  </div>

                  <div className="list-container">
                    <CardList data={cardData2} />
                  </div>
                </div>
              </div>

              <div label="내클립영상">
                <div className="m-main">
                  <div className="top-flex">
                    <div className="col-left">
                      <BubbleTab tabs={bubbleTabData3} />
                    </div>

                    <div className="col-right">
                      <Button attr={{
                        text: data.month,
                        className: "selectLayer",
                        onClick:handleTabClick,
                      }}/>
                    </div>
                  </div>

                  <div className="list-container">
                    <CardList data={cardData3} />
                  </div>
                </div>
              </div>
            </TabsWrap>
          </div>
        </section>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {termVisible && (
        <SelectLayer layerAttr={termLayer} />
      )}
    </>
  );
};

export default withRouter(HmyM3010000);
