/**
 *
 * @param {*} props label : 라벨
 * @param {*} props id : id 값
 * @param {*} props name : input 변수
 * @param {*} props defaultValue : 초기값*
 * @param {*} props status : 디자인 효과 (is-valid / is-invalid)
 * @param {*} props onChange : input 값 변경 시
 * @param {*} props checked : 체크값
 *
 */

import Badge from "components/atom/Badge";
import Skeleton from "react-loading-skeleton";

// Checkbox 호출 시, id는 다른 엘리먼트와 중복 가능성이 있어 GroupId 에 숫자를 붙이며,
// name 은 각 체크박스의 변수 역할을 하므로 고유하게 작성한다.

const ScheduleCard = ({
  progressType,
  img,
  startDate,
  endDate,
  calc,
  onClick,
}) => {
  const progressData =
    progressType === "progress"
      ? "진행중"
      : progressType === "schedule"
      ? "예정"
      : progressType === "end"
      ? "종료"
      : "";

  const cardTopClass =
    progressType === "progress"
      ? "card_top progress"
      : progressType === "schedule"
      ? "card_top schedule"
      : progressType === "end"
      ? "card_top end"
      : "card_top progress";

  const imgBox = {
    backgroundImage: "url(" + img + ")",
  };

  const testdata = null;

  return (
    <div
      className={`card_wrapper ${calc && "calc"} schedule `}
      onClick={onClick}
    >
      {testdata ? (
        <div className={cardTopClass}>
          <div className="schedule_wrapper flexCW">
            {progressData}
            {progressType === "progress" ? <Badge attr={{ live: true }} /> : ""}
          </div>
          <div className="schedule_date">
            {startDate} ~ {endDate}
          </div>
        </div>
      ) : (
        <div className={cardTopClass}>
          <Skeleton width={60} />
        </div>
      )}

      <div className="card_bottom schedule">
        {testdata ? (
          <div className="img-box" style={imgBox}></div>
        ) : (
          <Skeleton containerClassName={"skt_img schedule"} />
        )}
      </div>
    </div>
  );
};

export default ScheduleCard;
