import React, {useRef, useState} from 'react';
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom/cjs/react-router-dom";
import Summary from "components/pages/H-MY/Summary";
import Button from 'components/atom/button/Button';
import BtnLayout from "components/molecule/layout/BtnLayout";
import Checkbox from "components/molecule/form/Checkbox";
import Terms from "components/atom/Terms";
import Confirm from "components/molecule/alert/Confirm";
import ButtonAlert from "components/molecule/alert/ButtonAlert";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const popData = [
  {
    text: '연장하기',
    color:"btn-gray",
    size:"btn-xxs",
    onClick:"",
    alertDatas : {
      alert: "",
      dec: "기존 시즌권 종료일로부터\n연장한 시즌권 기간만큼\n자동으로 연장됩니다.",
      balls: [
        {
          dt: "보유하신 뽈",
          ball: "10,000뽈",
        },
        {
          dt: "환불예정 뽈",
          ball: "10,000뽈",
        }
      ],
      button: {
        text: "연장하기",
        onClick: "",
      },
    },
  },
  {
    text: '구매하기',
    color:"btn-gray",
    size:"btn-xxs",
    onClick:"",
    alertDatas : {
      alert: "",
      dec: "보유하신 시즌권이 종료되었습니다.\n시즌권 구매 시 호각 스포츠 영상을\n시청하실 수 있습니다.",
      balls: [
        {
          dt: "보유하신 뽈",
          ball: "10,000뽈",
        },
        {
          dt: "환불예정 뽈",
          ball: "10,000뽈",
        }
      ],
      button: {
        text: "구매하기",
        onClick: "",
      },
    },
  },
]

const HmyB3010001 = (props) => {
  const [showAlert, setShowAlert] = useState(false);
  const [openTerms, setOpenTerms] = useState(false);
  const [data, setData] = useState({
    title:"365일 시즌권",
    ball:"6,000",
    dec:"호각 내 모든 경기 영상 365일 무제한 시청",
    date:"2024.08.01~2024.08.07",
    have:"10,000뽈",
    use:"1,000뽈",
  })

  const ConfirmData = {
    alert:"",
    title:"시즌권을 연장하시겠어요?",
    dec: "기존 시즌권 종료일로부터\n연장한 시즌권 기간만큼\n자동으로 연장됩니다.",
    button: {
      text: "연장하기",
      onClick: "",
    },
  }

  const openConfirmHandler = (e) => {
    e.preventDefault();
    setShowAlert(!showAlert);
  };

  return (
    <>
      <div className="page-my page-wrapper bg-gray t-season">
        <div className="m-main">
          <div className="my-flex flex">
            <div className="col-summary">
              <Summary />
            </div>

            <div className="col-right">
              <div className="right-outer t2">
                <div className="bg-box">
                  <header className="right-header">
                    <strong className="tit">환불신청</strong>
                  </header>
                  <FullHeight attr={{pageHeader:true}}>
          <ColTop>
            <div className="list">
              <article className="article a1">
                <header className="label-header">내 시즌권</header>
                <dl className="season-ticket">
                  <dt>
                    <strong className="title">{data.title}</strong>
                    {data.ball && (
                      <strong className="price more">{data.ball}뽈</strong>
                    )}
                  </dt>
                  <dd>
                    <p className="dec">{data.dec}</p>
                    {data.date && (
                      <dl className="date-dl">
                        <dt>이용기간</dt>
                        <dd>{data.date}</dd>
                      </dl>
                    )}
                  </dd>
                </dl>
                <small className="tip t1">*한국시간 기준으로 카운트됩니다.</small>
              </article>

              <article className="article a2">
                <dl className="season-ticket">
                  <dt>
                    <strong className="title">시즌권 연장하기</strong>
                  </dt>
                  <dd>
                    <dl className="inner-dl">
                      <dt>구매상품정보</dt>
                      <dd>{data.title}</dd>
                    </dl>
                    <dl className="inner-dl">
                      <dt>상품금액</dt>
                      <dd>{data.ball}뽈</dd>
                    </dl>
                  </dd>
                </dl>
              </article>
            </div>
          </ColTop>
          <ColBottom>
            <div className="term-flex flex">
              <Checkbox attr={{
                id:"chk2",
                name:"chk2",
                value:"",
                required:"",
                label:"유료 상품 이용 약관 동의",
              }} />
              <Button attr={{
                className : `icon-arrow-down ${openTerms ? "_open" : ""}`,
                text: '열기/닫기',
                iconOnly: true,
                onClick : () => setOpenTerms(!openTerms),
              }} />
            </div>
            {openTerms && (
              <div className="term-wrap">
                <div className="over-wrap">
                  <Terms type="privacy" />
                </div>
              </div>
            )}
            <BtnLayout
              attr={[
                {
                  className : 'w100',
                  text: '시즌권 연장하기',
                  disabled : "",
                  size : 'btn-lg',
                  color : 'btn-primary',
                  onClick : "",
                },
              ]}
              className="full"
            />
          </ColBottom>
        </FullHeight>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/*temp*/}
      <div className="_temp">
        {popData.map((item, index) => (
        <ButtonAlert attr={item} />
      ))}
      </div>
    </>
  );
};

export default withRouter(HmyB3010001);
