import React from 'react';
import Input from "components/atom/form/Input";

const ChkLabel = ({attr, type}) => {
  const {
    id,
    className,
    name,
    value,
    ref,
    onChange,
    disabled,
    checked,
    status,
    label,
    size,
    shape,
    required,
    require,
    dataGroup,
    dataIndex
  } = attr;
  console.log("ChkLabel", dataIndex)
  const handleChange = (e) => {};

  return (
    <div className={`chk-label ${className ? className : "" } ${type} ${size ? size : ""} ${shape ? shape : ""}`}>
      <Input attr={{
        type,
        id,
        name,
        value,
        onChange,
        checked,
        disabled,
        dataGroup,
        label,
        dataIndex,
      }}
      />
      <label htmlFor={id} className="txt">
        <span className={`${required ? 'req' : ""}`}>
          {require === true ? "(필수)" : require === false ? "(선택)" : ""}{" "}
          {label}
        </span>
      </label>
    </div>
  )
}

export default ChkLabel;