import React from "react";
import { withRouter } from "react-router-dom";
import FormWrap from "components/molecule/form/FormWrap";
import FormSearch from "components/molecule/search/FormSearch";
import ResultList from "components/molecule/list/ResultList";
import PageHeader from "components/molecule/layout/PageHeader";
import BtnLayout from "components/molecule/layout/BtnLayout";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const Hmain07030401 = (props) => {
  return (
    <>
      <PageHeader onClick="" title="대회/리그" />
      <div className="page page-wrapper page-shoot">
        <FullHeight attr={{pageHeader:true}}>
          <ColTop>
            <div className="search-container form-group">
              <FormSearch attr={{
                name: "",
                placeholder : "대회/리그 명을 검색해보세요.",
                onClick: "",
              }}
              />
            </div>

            <div className="info-container">
              <div className="shoot-container">
                <dl className="shoot-dl">
                  <dt>내소속 진행중 대회/리그</dt>
                  <dd>
                    <ResultList />
                  </dd>
                </dl>
              </div>

            </div>
          </ColTop>
          <ColBottom>
            <div className="agree-container">
              <BtnLayout
                attr={[
                  {
                    className : 'w100',
                    text: '선택완료',
                    disabled: true,
                    color:"btn-black",
                    size:"btn-lg",
                    onClick:"",
                  },
                ]}
                className="full"
              />
            </div>
          </ColBottom>
        </FullHeight>
      </div>
    </>
  );
};

export default withRouter(Hmain07030401);
