import React, { useState, } from "react";
import { withRouter } from "react-router-dom";
import FormItem from "components/molecule/form/FormItem";
import Button from 'components/atom/button/Button';
import BtnLayout from "components/molecule/layout/BtnLayout";
import Checkbox from "components/molecule/form/Checkbox";
import Terms from "components/atom/Terms";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const fromData = [
  {
    label: "신고종류",
    type: "text",
    id : "in1",
    name : "in1",
    value: "영상신고",
    required: true,
    placeholder: "",
    groupClassName: "",
    labelClassName: "",
    formClassName: "",
    status: "",
    readOnly: true,
    disabled: "",
    onChange: "",
    onKeyPress: "",
    onKeyUp: "",
    onBlur: "",
    copyToClipboard: "",
    autocomplete: "",
  },
  {
    label: "상세사유 입력",
    labelClassName : "",
    required : true,
    formClassName : "",
    formBtn : "",
    copyToClipboard : "",
    id : "in2",
    name : "",
    type : "textarea",
    value : "",
    status : "",
    onChange : "",
    readOnly : "",
    onKeyPress : "",
    placeholder : "신고정보와 내용을 상세히 입력해주세요.",
    etcs : {
      tip: "*환불 상세사유 최소10자 입력해주세요.",
      tipSize: "",
      tipColor: "c-red",
      validMsg: "",
      inValidMsg: "",
    },
    lengths: {
      length: "0",
      maxLength: "300",
      position: "bottom",
    },
  },
]

const Hvod01000000 = (props) => {
  const [open, setOpen] = useState(false);

  return (
    <div className="page page-wrapper">
      <FullHeight>
        <ColTop>
          <div className="form-container _wrap">
            { fromData
              .map((item, index) => (
                <FormItem
                  key={index}
                  attr={item}
                />
              ))
            }
          </div>
          <ul className="blit-list mt-25 c-gray">
            <li>앱 푸시를 통해 신고 결과를 알려드립니다.<br />앱 푸시를 활성화 해주세요.</li>
          </ul>
        </ColTop>
        <ColBottom>
          <div className="term-flex flex">
            <Checkbox attr={{
              id:"chk1",
              name:"chk1",
              value:"",
              required:true,
              label:"개인정보 수집 및 이용동의",
            }} />
            <Button attr={{
              className : `icon-arrow-down ${open ? "_open" : ""}`,
              text: '열기/닫기',
              iconOnly: true,
              onClick : () => setOpen(!open),
            }} />
          </div>
          {open && (
            <div className="term-wrap">
              <div className="over-wrap">
                <Terms type="privacy" />
              </div>
            </div>
          )}
          <BtnLayout
            attr={[
              {
                className : 'w100',
                text: '신고하기',
                disabled : true,
                size : 'btn-lg',
                color : 'btn-primary'
              },
            ]}
            className="full"
          />
        </ColBottom>
      </FullHeight>
    </div>
  );
};

export default withRouter(Hvod01000000);
