import React, {useState} from 'react';
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom/cjs/react-router-dom";
import Summary from "components/pages/H-MY/Summary";
import BubbleTab from "components/atom/BubbleTab";
import Button from "components/atom/button/Button";
import FormItem from "components/molecule/form/FormItem";
import FormWrap from "components/molecule/form/FormWrap";
import Select from "components/atom/form/Select";
import BtnLayout from "components/molecule/layout/BtnLayout";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const HmyB2000000 = (props) => {
  const fromData = [
    {
      label: "충전 금액",
      type: "select",
      id : "select1",
      name : "select",
      value: "",
      required: "",
      placeholder: "10,000",
      groupClassName: "",
      labelClassName: "",
      formClassName: "",
      status: "",
      readOnly: "",
      disabled: "",
      onChange: "",
      onKeyPress: "",
      onKeyUp: "",
      onBlur: "",
      copyToClipboard: "",
      autocomplete: "",
      options: [
        {
          text: "5,000원(500뽈)",
          value: "",
        },
        {
          text: "10,000원(1,000뽈)",
          value: "",
        },
        {
          text: "20,000원(2,000뽈)",
          value: "",
        },
        {
          text: "50,000원(5,000뽈)",
          value: "",
        },
        {
          text: "100,000원(10,000뽈)",
          value: "",
        },
      ],
      layers : {
        selectType: "select",
        optionLabel: "충전금액",
        optionType: "radio",
      },
    },
  ]

  const bubbleTabData = [
    {
      select: "",
      selectedTab : "",
      label: "계좌",
    },
    {
      select: "",
      selectedTab : "",
      label: "카드",
    },
    {
      select: "",
      selectedTab : "",
      label: "휴대폰",
    },
    {
      select: "",
      selectedTab : "",
      label: "기프트뽈",
    },
  ]

  return (
    <>
      <div className="page-my page-wrapper bg-gray t-charge">
        <div className="m-main">
          <div className="my-flex flex">
            <div className="col-summary">
              <Summary />
            </div>

            <div className="col-right">
              <div className="right-outer">
                <div className="bg-box">
                  <header className="right-header">
                    <strong className="tit">뽈 사용내역</strong>
                  </header>
                  <FullHeight attr={{pageHeader:true}}>
                    <ColTop>
                      <div className="top-first">
                        <BubbleTab tabs={bubbleTabData} />
                      </div>

                      <div className="bg-box">
                        <div className="form-container _wrap">
                        { fromData
                          .map((item, index) => (
                            <FormItem
                              key={index}
                              attr={item}
                            />
                          ))
                        }
                        <div className="flex form-group">
                          <span className="col-form-label">충전 계좌</span>
                          <div className="col-form">
                            <div className="form-wrap  select">
                              <button type="button" className="btn selectLayer">
                                <span className="txt ">10,000</span>
                              </button>
                            </div>

                            <FormWrap attr={{
                              label:"",
                              type:"text",
                              id:"",
                              name:"",
                              placeholder:"계좌번호를 입력해주세요.",
                              disabled:"",
                              etcs: {
                                tip:"*본인 계좌가 아닙니다. 본인계좌를 다시 입력해주세요.",
                                tipColor:"c-red",
                              }
                            }} />
                          </div>
                        </div>
                      </div>
                      </div>
                    </ColTop>
                    <ColBottom>
                      <div className="bg-box">
                        <dl className="tip-dl">
                          <dt>충전안내</dt>
                          <dd>
                            <ul className="blit-list star">
                              <li>최소 1만원부터 최대 100만원까지 충전 가능합니다.</li>
                              <li>23:50~00:10는 정기점검 시간으로 충전이 불가능할 수 있습니다.</li>
                              <li>은행 점검 시간에는 충전이 불가능합니다.</li>
                              <li>계좌 잔액 부족 시 충전이 불가능합니다.</li>
                            </ul>
                          </dd>
                        </dl>
                        <BtnLayout
                          attr={[
                            {
                              className : 'w100',
                              text: '충천하기',
                              disabled : true,
                              size : 'btn-lg',
                              color : 'btn-primary'
                            },
                          ]}
                          className="full"
                        />
                      </div>
                    </ColBottom>
                  </FullHeight>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default withRouter(HmyB2000000);
