import React from "react";
import Slider from "react-slick";

import noti from "assets/images/icon/Icon_noti.svg";
import newIcon from "assets/images/icon/icon_new.svg";

function NotiSlider({ data }) {
  const settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 1.1,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 480,
        settings: {
          infinite: true,
          speed: 300,
          slidesToShow: 1.02,
          slidesToScroll: 1,
          adaptiveHeight: true,
        },
      },
      {
        breakpoint: 860,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 1200,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <div className="noti_wrapper">
      <Slider {...settings}>
        {data.map((item, index) => (
          <div className="noti_slider" key={index}>
            <img className="notiIcon" src={noti} />
            <div className="date">{item.date}</div>
            <div className="noti">{item.noti}</div>
            {item.new === true && <img className="newIcon" src={newIcon} />}
          </div>
        ))}
      </Slider>
    </div>
  );
}

export default NotiSlider;
