import React, { useState } from "react";
import { withRouter } from "react-router-dom";
import useMediaQueryHook from "../../../hook/useMediaQuery";
import BubbleTab from "../../atom/BubbleTab";
import CardList from "components/molecule/list/CardList";

import { clipCardData } from "../main/data";

const bubbleTabData = [
  {
    refesh: true,
    onClick: "",
    cnt: "1",
  },
  {
    selectedTab: "축구",
    onClick: "",
    label: "축구",
  },
  {
    selectedTab: "",
    onClick: "",
    label: "야구",
  },
  {
    selectedTab: "",
    onClick: "",
    label: "핸드볼",
  },
  {
    selectedTab: "",
    onClick: "",
    label: "배구",
  },
];

const clipCard = {
  totalCnt : "10",
  list : clipCardData,
  cardType : "clip"
}

const Hmain04000000 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  return (
    <>
      <div className={`page-wrapper pag-main top-fix w-100 ${!isMobile && "flexCC_col"}`}>
        <div className="page-title w-100">
          <h2 className="page-tit">최신클립</h2>
        </div>
        <div className="top-select">
          <div className="m-main">
            <BubbleTab tabs={bubbleTabData} />
          </div>
        </div>

        <section className="sec sec-main-sub w-100">
          <div className="m-main">
            <CardList data={clipCard}/>
          </div>
        </section>
      </div>
    </>
  );
};

export default withRouter(Hmain04000000);
