import React, {useState} from "react";
import { Link } from "react-router-dom";

const timeData = [
  {
    time :"12:00",
    disabled : "",
  },
  {
    time :"13:00",
    disabled : "",
  },
  {
    time :"14:00",
    disabled : "",
  },
  {
    time :"15:00",
    disabled : true,
  },
  {
    time :"16:00",
    disabled : true,
  },
  {
    time :"17:00",
    disabled : true,
  },
  {
    time :"18:00",
    disabled : true,
  },
  {
    time :"19:00",
    disabled : true,
  },
  {
    time :"20:00",
    disabled : true,
  },
  {
    time :"21:00",
    disabled : true,
  },
  {
    time :"22:00",
    disabled : true,
  },
  {
    time :"23:00",
    disabled : true,
  },
  {
    time :"24:00",
    disabled : true,
  },
]

const SelectLayer = (props) => {

    return (
      <div className="time-container">
        <ul className="select-list">
          {timeData.map((item, index) => (
            <li key={index}>
              <div className="chk-label checkbox time-btn">
                <input
                  type="checkbox"
                  name=""
                  id={`time_${index}`}
                  disabled={item.disabled}
                />
                <label className="txt" htmlFor={`time_${index}`}>{item.time}</label>
              </div>
            </li>
          ))}
        </ul>
      </div>
    )
  }

export default SelectLayer;

