import React, { useState, useRef } from "react";
import { withRouter } from "react-router-dom";
import useMediaQueryHook from "hook/useMediaQuery";
import Summary from "components/pages/H-MY/Summary";
import FormItem from "components/molecule/form/FormItem";
import Button from 'components/atom/button/Button';
import BtnLayout from "components/molecule/layout/BtnLayout";
import Checkbox from "components/molecule/form/Checkbox";
import Terms from "components/atom/Terms";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const HmyM2030000 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const [openRefund, setOpenRefund] = useState(false);
  const [openTerms, setOpenTerms] = useState(false);

  //selectSet setting
  const selectSetComponentRef = useRef({});
  const selectChangeHandler = (e) => {
    if(isMobile) {
      //BS cloas
      selectSetComponentRef.current.handleBsClose();
      //change label
      let label = e.target.dataset.label;
      let index = e.target.dataset.index;
      let newBubbleTabData = [...formData];
      newBubbleTabData[index].placeholder = label;
      setFormData(newBubbleTabData);
    }
  };
  const [formData, setFormData] = useState([
    {
      label: "환불신청",
      type: "select",
      id : "select1",
      name : "select",
      value: "",
      required: true,
      placeholder: "선택안함",
      onChange: selectChangeHandler,
      options: [
        {
          text: "충전 환불",
          value: "충전 환불",
          type: "radio",
        },
      ],
      layers : {
        selectType: "select",
        optionLabel: "환불신청",
        optionType: "radio",
      },
    },
    {
      label: "환불사유",
      type: "select",
      id : "select2",
      name : "select",
      value: "",
      required: true,
      placeholder: "선택안함",
      onChange: selectChangeHandler,
      options: [
        {
          text: "환불사유1",
          value: "환불사유1",
          type: "radio",
        },
        {
          text: "환불사유2",
          value: "환불사유2",
          type: "radio",
        },
        {
          text: "환불사유3",
          value: "환불사유3",
          type: "radio",
        }
      ],
      layers : {
        selectType: "select",
        optionLabel: "환불사유",
        optionType: "radio",
      },
    },
    {
      label: "환불사유 입력",
      labelClassName : "",
      required : true,
      formClassName : "",
      formBtn : "",
      copyToClipboard : "",
      id : "in2",
      name : "",
      type : "textarea",
      value : "",
      status : "",
      onChange : "",
      readOnly : "",
      onKeyPress : "",
      placeholder : "환불사유를 상세히 입력해주세요.",
      etcs : {
        tip: "*환불 상세사유 최소10자 입력해주세요.",
        tipSize: "",
        tipColor: "c-red",
        validMsg: "",
        inValidMsg: "",
      },
      lengths: {
        length: "0",
        maxLength: "300",
        position: "bottom",
      },
    },
  ])

  return (
    <div className="page-my page-wrapper">
      <div className="m-main">
        <div className="my-flex flex">
          <div className="col-summary">
            <Summary />
          </div>

          <div className="col-right">
            <div className="right-outer t3">
              <div className="bg-box">
                <header className="right-header">
                  <strong className="tit">뽈 사용내역</strong>
                </header>
                <FullHeight attr={{pageHeader:true}}>
                  <ColTop>
                    <div className="form-container _wrap">
                      { formData
                        .map((item, index) => (
                          <FormItem
                            key={index}
                            attr={item}
                            dataIndex={index}
                            selectSetRef={selectSetComponentRef}
                          />
                        ))
                      }
                    </div>
                    <p className="btt-tip">
                      가입 시 등록된 회원정보로 답변이 발송될 예정이니, <br />
                      반드시 내정보가 맞는지 확인 후 환불신청 해주세요.
                    </p>
                  </ColTop>
                  <ColBottom>
                    <div className="term-flex flex">
                      <Checkbox attr={{
                        id:"chk1",
                        name:"chk1",
                        value:"",
                        required:true,
                        label:"환불 약관 동의",
                      }} />
                      <Button attr={{
                        className : `icon-arrow-down ${openRefund ? "_open" : ""}`,
                        text: '열기/닫기',
                        iconOnly: true,
                        onClick : () => setOpenRefund(!openRefund),
                      }} />
                    </div>
                    {openRefund && (
                      <div className="term-wrap">
                        <div className="over-wrap">
                          <Terms type="refund" />
                        </div>
                      </div>
                    )}
                    <div className="term-flex flex">
                      <Checkbox attr={{
                        id:"chk2",
                        name:"chk2",
                        value:"",
                        required:true,
                        label:"개인정보 수집 및 이용동의",
                      }} />
                      <Button attr={{
                        className : `icon-arrow-down ${openTerms ? "_open" : ""}`,
                        text: '열기/닫기',
                        iconOnly: true,
                        onClick : () => setOpenTerms(!openTerms),
                      }} />
                    </div>
                    {openTerms && (
                      <div className="term-wrap">
                        <div className="over-wrap">
                          <Terms type="privacy" />
                        </div>
                      </div>
                    )}
                    <BtnLayout
                      attr={[
                        {
                          className : 'w100',
                          text: '환불 신청하기',
                          disabled : true,
                          size : 'btn-lg',
                          color : 'btn-primary'
                        },
                      ]}
                      className="full"
                    />
                  </ColBottom>
                </FullHeight>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default withRouter(HmyM2030000);
