import React from 'react';
import Length from "components/molecule/form/Length";

const Tip = ({attr, lengthData}) => {
  /*const {
    tip,
    tipSize,
    tipColor,
    length,
    lengthPosition,
    maxLength,
    validMsg,
    inValidMsg,
    status,
  } = attr*/

  return (
    <>
      {attr && attr.tip || lengthData ? (
        <div className="btt-flex flex">
          <div className="col-left">
            {attr && attr.tip && (
              <p className={`tip ${attr.tipColor || ""}`} style={{fontSize : attr.tipSize ? attr.tipSize : "12px"}}>
                {attr.tip}
              </p>
            )}
          </div>
          <div className="col-right">
            {lengthData && (
              <Length attr={lengthData} />
            )}
          </div>
        </div>
      ) : ""}
    </>

  )
}

export default Tip;