const useGetPageTitle = (props) => {
  switch (props.pathname) {
    case "/page1":
      return "페이지 1 제목";
    case "/page2":
      return "페이지 2 제목";
    case "/page3":
      return "페이지 3 제목";
    default:
      return props.pathname; // 기본 제목 설정
  }
};

export default useGetPageTitle;
