import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";

import useMediaQueryHook from "hook/useMediaQuery";

import ex_image from "../../../assets/images/example/boimages.png";
import PageHeader from "components/molecule/layout/PageHeader";
import Slider from "react-slick";

const Heve04000000 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); // 이전 스크롤 위치

  const handleScroll = () => {
    const currentScrollY = window.scrollY;
    setScrollY(currentScrollY);

    if (currentScrollY > prevScrollY && currentScrollY > 100) {
      setIsExpanded(false);
    } else if (currentScrollY < prevScrollY) {
      setIsExpanded(true);
    }

    setPrevScrollY(currentScrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollY]);

  console.log("isExpanded", isExpanded);

  const [data, setData] = useState({
    name: "제 58회 대통령배 전국고교 야구대회",

    images: [ex_image, ex_image, ex_image],
  });

  var settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,

    responsive: [
      {
        breakpoint: 360,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          adaptiveHeight: true,
        },
      },
      {
        breakpoint: 860,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 1200,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <>
      <div className="subHome_wrapper">
        <PageHeader onClick="" title={data.name} noBtn="false" share={true} />

        <div className="page page-wrapper">
          <section className="subHome_section teamInfo_edit flexCC eve">
            {!isMobile ? (
              <div className="image_container flexCC_col">
                {data.images.map((item, index) => (
                  <div className="image_wrapper">
                    <img src={item} />
                  </div>
                ))}
              </div>
            ) : (
              <Slider {...settings}>
                {data.images && data.images?.length !== 0
                  ? data.images.map((image, index) => (
                      <div key={index} className="image_wrapper">
                        <img
                          src={image}
                          alt={`image ${index + 1}`}
                          className="m-slide-image"
                        />
                      </div>
                    ))
                  : ""}
              </Slider>
            )}
          </section>
        </div>
      </div>
    </>
  );
};

export default withRouter(Heve04000000);
