import React from "react";
import { Link } from "react-router-dom/cjs/react-router-dom";
import Button from "components/atom/button/Button";

/**
 *
 * @param {*} props tags.text : tag text
 * @param {*} props tags.onClick : onClick
 *
 */

const LatelyTag = (pros) => {
  const { data } = pros;

  return (
    <>
      <ul className="lately-tag">
        {data.map((item, index) => (
          <li key={index}>
            <span className="btn-tag">
              <Button attr={{
                size : `btn-xs`,
                text: item.text,
                onClick : item.onClick
              }} />
              <Button attr={{
                className : `ico-del`,
                text: item.text,
                iconOnly: true,
                onClick : item.onClick
              }} />
            </span>
          </li>
        ))}
      </ul>
    </>
  )
};

export default LatelyTag;
