import React, {useState} from 'react';
import { withRouter } from "react-router-dom";
import Summary from "components/pages/H-MY/Summary";
import BubbleTab from "components/atom/BubbleTab";
import CardList from "components/molecule/list/CardList";

import {
  societyCardData,
} from "components/pages/main/data";

const bubbleTabData = [
  {
    label: "팀",
  },
  {
    label: "대회",
  },
  {
    label: "단체",
  },
]

const cardData = {
  totalCnt : "5",
  list : societyCardData,
  cardType : "society",
  className : "t2",
}

const Hmy03000000 = (props) => {
  return (
    <>
      <div className="page-my page-wrapper bg-gray">
        <div className="m-main">
          <div className="my-flex flex">
            <div className="col-summary">
              <Summary />
            </div>

            <div className="col-right">
              <div className="right-outer t3">
                <div className="bg-box">
                  <header className="right-header">
                    <strong className="tit">내 소속 리스트</strong>
                  </header>
                  <section>
                    <div className="m-main">
                      <div className="bubble-list">
                        <BubbleTab tabs={bubbleTabData} />
                        <CardList data={cardData} />
                      </div>
                    </div>
                  </section>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default withRouter(Hmy03000000);
