import Button from "components/atom/button/Button";
import ButtonAlert from "components/molecule/alert/ButtonAlert";
import React from "react";

const BtnLayout = (props) => {
  const { attr, className } = props;
  return (
      <ul className={`btn-ul flex ${className === 'half' ? 'f2' : ''}`}>
        { attr.map( (i, index) => {
          return (
        <li className={`${className === 'full' ? 'w100' : ''}`} key={index}>
          {i.alertDatas && i.alertDatas !== undefined ? (
            <ButtonAlert attr={i} />
          ) : (
            <Button attr={i} />
          )}
        </li>
            )
        })}
      </ul>
      
  )
}

export default BtnLayout;