import Toggle from "components/atom/Toggle";
import { Link } from "react-router-dom/cjs/react-router-dom";
import arrow from "../../../assets/images/icon/icon_arrow_right_gray.svg";

const CustomList = ({ dataItem }) => {
  return (
    <table className="customList">
      <tbody>
        {dataItem.map((item, index) => (
          <tr key={index}>
            <table className="inner">
              {item.title && (
                <thead>
                  <tr className="header">
                    <th>
                      <p>{item.title}</p>
                    </th>
                  </tr>
                </thead>
              )}
              <tbody>
                {item.datas &&
                  item.datas.map((item2, index2) => (
                    <Link to={item2.link}>
                      <tr className={`item ${item2.className}`} key={index2}>
                        <td>
                          {item2.content}
                          {item2.addText && (
                            <p className="addText">{item2.addText}</p>
                          )}
                        </td>

                        {item2.toggle && (
                          <Toggle
                            initialChecked={false}
                            onChange={item2.onChange}
                          />
                        )}
                        {item2.arrow && (
                          <div className="arrow_wrapper">
                            <img src={arrow} alt="right-arrow" />
                          </div>
                        )}
                      </tr>
                    </Link>
                  ))}
              </tbody>
            </table>
          </tr>
        ))}
      </tbody>
    </table>
  );
};

export default CustomList;
