import React, { useState } from "react";
import { Route, Link } from "react-router-dom";

function TopMenuNavList(props) {
  const [active, setActive] = useState(-1);
  const [clicked, setClicked] = useState(-1);

  const handleExpand = (e, i, match) => {
    e.preventDefault();

    setActive(active === i ? -1 : i);
    setClicked(1);
  };

  var img = props.data.img && (
    <div className="icon-img">
      <img src={props.data.img} alt="" />
    </div>
  );
  var caret = props.data.children && !props.data.badge && (
    <b className="caret"></b>
  );
  var label = props.data.label && (
    <span className="label label-theme m-l-5">{props.data.label}</span>
  );
  var badge = props.data.badge && (
    <span className="badge pull-right">{props.data.badge}</span>
  );
  var title = props.data.title && (
    <span>
      {props.data.title} {label}
    </span>
  );

  return (
    <Route
      path={props.data.path}
      exact={props.data.exact}
      className={"item_wrapper"}
      children={({ match }) => (
        <div
          className={`item-wrapper
            ${match ? "active " : ""} ${props.data.children ? "has-sub " : ""}
          `}
        >
          {props.data.children ? (
            <Link to={props.data.path} onClick={props.expand} className="nav-item">
              {img} {title} {caret} {badge}
            </Link>
          ) : (
            <Link to={props.data.path} className="nav-item">
              {img} {title} {caret} {badge}
            </Link>
          )}
          {props.data.children && (
            <ul className={"sub-menu " + (props.active ? "d-block" : "")}>
              {props.data.children &&
                props.data.children.map((submenu, i) => (
                  <TopMenuNavList
                    data={submenu}
                    key={i}
                    expand={(e) => handleExpand(e, i, match)}
                    active={i === active}
                    clicked={clicked}
                  />
                ))}
            </ul>
          )}
        </div>
      )}
    />
  );
}

export default TopMenuNavList;
