import React, { useRef } from "react";
import Button from "components/atom/button/Button";
import FormWrap from "components/molecule/form/FormWrap";
import SelectSet from "components/molecule/form/SelectSet";
import { Link } from "react-router-dom/cjs/react-router-dom";

/**
 *
 * @param {*} props label :             필수 label    - string
 * @param {*} props type :              필수 id       - text || pasword || checkbox || radio || select || textarea
 * @param {*} props id :                필수 id       - string
 * @param {*} props name :              필수 name     - boolean
 * @param {*} props value :             form-group className - string
 * @param {*} props required :          required    - boolean
 * @param {*} props placeholder :       placeholder - string
 * @param {*} props groupClassName :    form-group className
 * @param {*} props labelClassName :    label className
 * @param {*} props formClassName :     form-wrap className
 * @param {*} props status :            status
 * @param {*} props readOnly :          readOnly      - boolean
 * @param {*} props disabled :          disabled      - boolean
 * @param {*} props onChange :          onChange      - function
 * @param {*} props onKeyPress :        onKeyPress    - function
 * @param {*} props onKeyUp :           onKeyUp       - function
 * @param {*} props onBlur :            onBlur        - function
 * @param {*} props copyToClipboard :   copyToClipboard
 * @param {*} props autocomplete :      autocomplete  - boolean
 * @param {*} props options :           options       - only select, checkbox, radio  사용
 * @param {*} props etcs :              etcs data
 * @param {*} props formBtns :          formBtns      - only type=text, password 버튼
 * @param {*} props layers :            layers data   - only select 사용
 * @param {*} props buttons :           buttons datas - only select 사용
 * @param {*} props datePicker :        datePicker datas - only datePicker 사용
 *
 */

const FormItem = ({ attr, dataIndex, selectSetRef }) => {
  const {
    label,
    type,
    id,
    name,
    value,
    required,
    placeholder,
    groupClassName,
    labelClassName,
    formClassName,
    status,
    readOnly,
    disabled,
    onChange,
    onKeyPress,
    onKeyUp,
    onBlur,
    copyToClipboard,
    autocomplete,
    options,
    etcs,
    lengths,
    formBtns,
    layers,
    buttons,
    datePicker,
    link,
  } = attr;
  // Todo onkeypress .form-wrap addClass "_write"
  const inputRef = useRef(null);

  const handleChange = (e) => {};

  // Todo onclick delValue
  const delValue = () => {};

  return (
    <div className={`flex form-group ${groupClassName || ""}`}>
      {type !== "checkbox" && type !== "radio" ? (
        <label
          className={`col-form-label ${labelClassName || ""} ${
            required ? "req" : ""
          } `}
          htmlFor={id}
        >
          {label}
        </label>
      ) : type === "rangepicker" ? (
        ""
      ) : (
        <span
          className={`col-form-label ${labelClassName || ""} ${
            required ? "req" : ""
          } `}
        >
          {label}
        </span>
      )}

      <div className="col-form">
        {link ? (
          <Link
            to={{
              pathname: link.pathname,
              state: {
                label: link.label,
                name: link.name,
              },
            }}
          >
            <FormWrap attr={attr} dataIndex={dataIndex} selectSetRef={selectSetRef}/>
          </Link>
        ) : (
          <FormWrap attr={attr} dataIndex={dataIndex} selectSetRef={selectSetRef}/>
        )}
      </div>
    </div>
  );
};

export default FormItem;
