import React, {useState} from 'react';
import { withRouter } from "react-router-dom";
import Summary from "components/pages/H-MY/Summary";
import CustomList from "components/molecule/list/CustomList";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const dataItem = {
  datas: [
    {
      title: false,
      datas: [
        {
          content: "1:1 문의",
          arrow : true,
          link: "/H-MY/M2010000",
        },
        {
          content: "나의 문의내역",
          arrow : true,
          link: "/H-MY/M2020000",
        },
        {
          content: "환불신청",
          arrow : true,
          link: "/H-MY/M2030000",
        },
        {
          content: "환불내역",
          arrow : true,
          link: "/H-MY/M2040000",
        },
        {
          content: "신고하기",
          arrow : true,
          link: "/H-MY/M2050000",
        },
      ],
    },
  ]
}

const HmyM2000000 = (props) => {
  return (
    <>
      <div className="page-my page-wrapper bg-gray t-cs">
        <div className="m-main">
          <div className="my-flex flex">
            <div className="col-summary">
              <Summary />
            </div>

            <div className="col-right">
              <div className="right-outer t2">
                <div className="bg-box">
                  <header className="right-header">
                    <strong className="tit">고객센터</strong>
                  </header>
                  <FullHeight attr={{pageHeader:true}}>
          <ColTop>
            <div className="list-container">
              <CustomList dataItem={dataItem} />
            </div>
          </ColTop>
          <ColBottom />
        </FullHeight>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default withRouter(HmyM2000000);
