import React from "react";
import LiveCard from "components/molecule/Card/LiveCard";
import ScheduleCard from "components/molecule/Card/ScheduleCard";
import ClipCard from "components/molecule/Card/ClipCard";
import VideoCard from "components/molecule/Card/VideoCard";
import SocietyCard from "components/molecule/Card/SocietyCard";
import InfoCard from "components/molecule/Card/InfoCard";
import useMediaQueryHook from "hook/useMediaQuery";
import MemberCard from "../Card/MemberCard";
import GameScheduleCard from "../Card/GameScheduleCard";

const CardList = ({ data }) => {
  const { currentMediaQuery, isMobile } = useMediaQueryHook();

  return (
    <ul
      className={`card-flex flex full ${data.className ? data.className : ""}`}
    >
      {data.list &&
        data.list
          .slice(0, data.totalCnt ? data.totalCnt : data.list?.length)
          .map((item, index) => (
            <li key={index}>
              {data.cardType === "live" ? (
                <LiveCard
                  title={item.title}
                  team1={item.team1}
                  team2={item.team2}
                  stadium={item.stadium}
                  score={item.score}
                  date={item.date}
                  status={item.status}
                  onClick={item.onClick}
                />
              ) : data.cardType === "video" ? (
                <VideoCard
                  title={item.title}
                  team1={item.team1}
                  team2={item.team2}
                  stadium={item.stadium}
                  score={item.score}
                  date={item.date}
                  img={item.img}
                  onClick={item.onClick}
                />
              ) : data.cardType === "clip" ? (
                <ClipCard
                  title={item.title}
                  team={item.team}
                  content={item.content}
                  stadium={item.stadium}
                  img={item.img}
                  onClick={item.onClick}
                />
              ) : data.cardType === "schedule" ? (
                <ScheduleCard
                  progressType={item.progressType}
                  img={item.img}
                  startDate={item.startDate}
                  endDate={item.endDate}
                  onClick={item.onClick}
                />
              ) : data.cardType === "info" ? (
                <InfoCard
                  type={item.type}
                  team1={item.team1}
                  team2={item.team2}
                  onClick={item.onClick}
                />
              ) : data.cardType === "society" ? (
                <SocietyCard
                  title={item.title}
                  teamLogo={item.teamLogo}
                  team={item.team}
                  member={item.member}
                  bg={`${item.bg}${!isMobile ? "_lg" : ""}`}
                  onClick={item.onClick}
                  link={item.link}
                  fixed={item.fixed}
                />
              ) : data.cardType === "member" ? (
                <MemberCard
                  type={item.type}
                  img={item.img}
                  name={item.name}
                  position={item.position}
                  school={item.school}
                  birthdate={item.birthdate}
                  backNumber={item.backNumber}
                  parentName={item.parentName}
                />
              ) : data.cardType === "gameSchedule" ? (
                <GameScheduleCard
                  title={item.title}
                  team1={item.team1}
                  team2={item.team2}
                  stadium={item.stadium}
                  score={item.score}
                  date={item.date}
                  status={item.status}
                />
              ) : (
                ``
              )}
            </li>
          ))}
    </ul>
  );
};

export default CardList;
