import Toggle from "components/atom/Toggle";
import { Link } from "react-router-dom/cjs/react-router-dom";
import SelectSet from "components/molecule/form/SelectSet";

const CustomList = ({ dataItem }) => {
  const {className, datas} = dataItem;
  return (
    <ul className={`custom-list ${className}`}>
      {datas.map((item, index) => (
        <li key={index}>
          <dl className="inner">
            {item.title && (
              <dt className="header">
                <p>{item.title}</p>
              </dt>
            )}
            <dd>
              <ul className="list-ul">
                {item.datas && item.datas.map((item2, index2) => (
                  <li key={index2}>
                    <div className={`item-flex flex ${item2.toggle ? "_toggle" : ""}`}>
                      {item2.type === "select" ? (
                        ""
                      ) : (
                        <>
                          {item2.link ? (
                            <Link to={item2.link} className={`item flex ${item2.arrow ? "arrow" : ""}`}>
                              <p className="content">{item2.content}</p>
                              {item2.addText && (
                                <p className="addText">{item2.addText}</p>
                              )}
                            </Link>
                          ) : (
                            <div className={`item ${item2.arrow ? "arrow" : ""}`}>
                              <p className="content">{item2.content}</p>
                              {item2.addText && (
                                <p className="addText">{item2.addText}</p>
                              )}
                            </div>
                          )}
                          {item2.toggle && (
                            <Toggle
                              initialChecked={false}
                              onChange={item2.onChange}
                            />
                          )}
                        </>
                      )}
                    </div>
                    {item2.rows && (
                      <ul className="list-ul rows">
                        {item2.rows.map((item3, index3) => (
                          <li key={index3}>
                            {item3.type === "select" ? (
                              <SelectSet attr={{
                                label:item3.content,
                                id:item3.id,
                                name:item3.name,
                                onChange:item3.onChange,
                                options:item3.options,
                                layers:item3.layers,
                              }} />
                            ) : (
                              <Link to={item3.link} className={`item flex ${item3.arrow? "arrow" : ""}`}>
                                <p className="content">{item3.content}</p>
                                {item3.addText && (
                                  <p className="addText">{item3.addText}</p>
                                )}
                              </Link>
                            )}
                          </li>
                        ))}
                      </ul>
                    )}
                  </li>
                ))}
              </ul>
            </dd>
          </dl>
        </li>
      ))}
    </ul>
  );
};

export default CustomList;
