import BtnLayout from "components/molecule/layout/BtnLayout";
import Checkbox from "components/molecule/form/Checkbox";
import Terms from "components/atom/Terms";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";

const fromData = [
  {
    label: "상담분류",
    type: "select",
    id : "select1",
    name : "select",
    value: "",
    required: true,
    placeholder: "선택안함",
    groupClassName: "",
    labelClassName: "",
    formClassName: "",
    status: "",
    readOnly: "",
    disabled: "",
    onChange: "",
    onKeyPress: "",
    onKeyUp: "",
    onBlur: "",
    copyToClipboard: "",
    autocomplete: "",
    options: [
      {
        text: "이용방법 및 회원정보 문의",
        value: "",
        checked: "",
      },
      {
        text: "시즌권 문의",
        value: "",
        checked: "",
      },
      {
        text: "엘리트팀/협회/대회 신청",
        value: "",
        checked: "",
      },
      {
        text: "오류문의",
        value: "",
        checked: "",
      },
      {
        text: "권리침해 신고",
        value: "",
        checked: "",
      },
      {
        text: "통신장애",
        value: "",
        checked: "",
      },
      {
        text: "기타문의",
        value: "",
        checked: "",
      },
    ],
    layers : {
      selectType: "select",
      optionLabel: "상담분류",
      optionType: "radio",
    },
  },
  {
    label: "제목",
    type: "text",
    id : "text2",
    name : "text2",
    value: "",
    required: true,
    placeholder: "제목을 입력해주세요.",
    groupClassName: "",
    labelClassName: "",
    formClassName: "",
    status: "",
    readOnly: "",
    disabled: "",
    onChange: "",
    onKeyPress: "",
    onKeyUp: "",
    onBlur: "",
    copyToClipboard: "",
    autocomplete: "",
    options: [
      {
        text: "환불사유1",
        value: "",
        checked: "",
      },
      {
        text: "환불사유2",
        value: "",
        checked: "",
      },
      {
        text: "환불사유3",
        value: "",
        checked: "",
      }
    ],
    layers : {
      selectType: "select",
      optionLabel: "환불사유",
      optionType: "radio",
    },
  },
  {
    label: "문의내용",
    labelClassName : "",
    required : true,
    formClassName : "",
    formBtn : "",
    copyToClipboard : "",
    id : "in2",
    name : "",
    type : "textarea",
    value : "",
    status : "",
    onChange : "",
    readOnly : "",
    onKeyPress : "",
    placeholder : "어떤 이유로 호각을 탈퇴할 결심을 하셨나요?",
    lengths: {
      length: "0",
      maxLength: "300",
      position: "bottom",
    },
  },
]

const HmyM2010000 = (props) => {
  const [openRefund, setOpenRefund] = useState(false);
  const [openTerms, setOpenTerms] = useState(false);

  const handleShowToast = (msg) => {
    return () => toast(msg, {
      position: "top-center",
    });
  };

  return (
    <div className="page-my page-wrapper">
      <div className="m-main">
        <div className="my-flex flex">
          <div className="col-summary">
            <Summary />
          </div>

          <div className="col-right">
            <div className="right-outer t2">
              <div className="bg-box">
                <header className="right-header">
                  <strong className="tit">1:1 문의</strong>
                </header>
                <FullHeight attr={{pageHeader:true}}>
        <ColTop>
          <div className="form-container _wrap">
            { fromData
              .map((item, index) => (
                <FormItem
                  key={index}
                  attr={item}
                />
              ))
            }
          </div>
          <p className="btt-tip">
            문의에 대한 답변은 평균 3영업일 정도 소요됩니다. <br />
            고객센터 미운영시간에는 1:1문의를 남겨주세요.
          </p>
        </ColTop>
        <ColBottom>
          <div className="term-flex flex">
            <Checkbox attr={{
              id:"chk2",
              name:"chk2",
              value:"",
              required:true,
              label:"개인정보 수집 및 이용동의",
            }} />
            <Button attr={{
              className : `icon-arrow-down ${openTerms ? "_open" : ""}`,
              text: '열기/닫기',
              iconOnly: true,
              onClick : () => setOpenTerms(!openTerms),
            }} />
          </div>
          {openTerms && (
            <div className="term-wrap">
              <div className="over-wrap">
                <Terms type="privacy" />
              </div>
            </div>
          )}
          <BtnLayout
            attr={[
              {
                className : 'w100',
                text: '작성 완료',
                disabled : true,
                size : 'btn-lg',
                color : 'btn-primary'
              },
            ]}
            className="full"
          />
        </ColBottom>
      </FullHeight>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="_temp">
        <Button
          attr={{
            size:"btn-xxs",
            text: "문의 등록 toast",
            onClick:handleShowToast("문의가 등록되었습니다."),
          }}
        />
      </div>
    </div>
  );
};

export default withRouter(HmyM2010000);
