
const AnimatedIcon = (props) => {
  const { animatedIcon, iconProps } = props;
  const { wrappedSize, iconSize, className, iconColor } = iconProps;

    return (
      <div className={`fa-${wrappedSize}`}>
        <i className={`fas fa-${animatedIcon} fa-spin fa-${iconSize} ${className} text-${iconColor}`}></i>
      </div>
    )
}

export default AnimatedIcon;