
const SolidIcon = (props) => {
  // iconFw = fixed width
  const { solidIcon, iconProps } = props;
  const { iconSize, className, iconColor } = iconProps;

    return (
        <i className={`fas fa-${solidIcon} fa-${iconSize} ${className} text-${iconColor}`}></i>
    )
}

export default SolidIcon;