import React, {useRef, useState} from "react";
import useMediaQueryHook from "../../../hook/useMediaQuery";
import Button from "components/atom/button/Button";
import MultiContainer from "components/molecule/vod/MultiContainer";
import ClipContainer from "components/molecule/vod/ClipContainer";
import TagContainer from "components/molecule/vod/TagContainer";
import TagProgress from "components/molecule/vod/TagProgress";
import SelectSet from "components/molecule/form/SelectSet";
import ConfirmSet from "components/molecule/alert/ConfirmSet";

const VodContainer = ({vodData, vodActive, changeParentsFn}) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const ComfirmCompRef = useRef({});

  const [isFull, setIsFull] = useState(false);
  const handleFullClick = () => {
    setIsFull(!isFull);
  };

  const [tagVisible, setTagVisible] = useState(false);

  function handleVodClick(sid) {
    changeParentsFn(sid);
  }

  // Todo connfirm save btn action
  const handleSaveClick = () => {

    ComfirmCompRef.current.openConfirmHandler();
  };

  return (
    <div className={`vod-container ${isFull ? 'full' : ''} _dim`}>
      <video controls className="video" poster={vodData.thumb}>
        <source src="" type="video/webm" />
      </video>

      {/*전체화면 우측 중간*/}
      <div className="right-btn-container">
        <ul className="right-btn ico-ul">
          {!vodActive.clipLayer && (
            <li>
              <Button attr={{
                className : `ico-clip`,
                text: '클립',
                widthTxt: true,
                onClick:() => handleVodClick('clip'),
              }} />
            </li>
          )}
          {!vodActive.clipLayer && (
            <li>
            <Button attr={{
              className : `ico-tag`,
              text: '태그',
              widthTxt: true,
              onClick :() => handleVodClick(),
            }} />
          </li>
          )}
          {vodActive.clipLayer && (
            <>
              <li>
                <ConfirmSet
                  btnDatas={{
                    className : `ico-save`,
                    text: '저장',
                    widthTxt: true,
                  }}
                  confirmDatas={{
                    popBtn : "클립 공개대상",
                    title: "클립 공개대상",
                    chkList: [
                      {
                        type:"radio",
                        name :"r1",
                        label : "전체공개",
                        checked : true,
                      },
                      {
                        type:"radio",
                        name :"r1",
                        label : "팀공개",
                      },
                      {
                        type:"radio",
                        name :"r1",
                        label : "촬영예약하기",
                      },
                    ],
                    button: {
                      text: '저장하기',
                      onClick:handleSaveClick,
                    },
                  }}
                  ref={ComfirmCompRef}
                />
              </li>
              <li>
                <Button attr={{
                  className : `ico-cancel`,
                  text: '취소',
                  widthTxt: true,
                  onClick:() => handleVodClick(),
                }} />
              </li>
            </>
          )}
        </ul>
      </div>

      {/*뒤로가기버튼*/}
      <Button attr={{
        className : `ico-back`,
        text: vodData.title,
        widthTxt: true,
      }} />

      {/*동영상 컨트롤*/}
      <ul className="play-btn">
        <li>
          <Button attr={{
            className : `vod-prev`,
            text: '-10',
          }} />
        </li>
        <li>
          <Button attr={{
            className : `vod-play`,
            text: 'play',
            iconOnly: true,
          }} />
          <Button attr={{
            className : `vod-pause`,
            text: 'play',
            iconOnly: true,
          }} />
        </li>
        <li>
          <Button attr={{
            className : `vod-prev`,
            text: '+10',
          }} />
        </li>
      </ul>

      {/*우측상단*/}
      <ul className="top-btn ico-ul">
        {vodActive.tagBS && (
          <li>
            <SelectSet attr={{
              label: "태그선택",
              name:"selectName",
              id:"selectId",
              options : [
                {
                  text: "전체공개",
                  value:"",
                  checked:"",
                  onChange:"",
                  rows : {
                    type : "checkbox",
                    name:"r3",
                    checked:"",
                    datas: [
                      {
                        text: "태그1",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "태그2",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "태그3",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "태그4",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "태그5",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "태그6",
                        value:"",
                        checked:"",
                      },
                    ]
                  }
                },
                {
                  text: "팀공개",
                  value:"",
                  checked:"",
                  onChange:"",
                  rows : {
                    type : "checkbox",
                    name:"r3",
                    checked:"",
                    datas: [
                      {
                        text: "태그1",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "태그2",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "태그3",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "태그4",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "태그5",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "태그6",
                        value:"",
                        checked:"",
                      },
                    ]
                  }
                },
                {
                  text: "비공개",
                  value:"",
                  checked:"",
                  onChange:"",
                },
              ],
              layers: {
                selectType : "btn",
                optionLabel : "태그선택",
                tagHide : true,
                optionType : "radio",   // default radio || checkbox
              },
              buttons : {
                className : `ico-tag`,
                text: '태그 종류 VIEW 선택 기능',
                iconOnly: true,
              },
            }} />
          </li>
        )}
        {(isMobile || isTablet) && (
          <li className="airplay">
            <Button attr={{
              className : `ico-screencast`,
              text: 'Airplay',
              iconOnly: true,
            }} />
          </li>
        )}
        {!vodActive.multiLayer && (
          <li>
            <Button
              attr={{
                className : `ico-multiview`,
                text: '멀티캠',
                iconOnly: true,
                onClick:() => handleVodClick('multiLayer'),
              }}
            />
          </li>
        )}
      </ul>

      {/*우측하단*/}
      <ul className="btt-btn ico-ul">
        <li>
          <Button attr={{
            className : `ico-speed`,
            text: '재생속도',
            iconOnly: true,
          }} />
        </li>
        <li>
          <Button attr={{
            className : `ico-picture`,
            text: '화질설정',
            iconOnly: true,
          }} />
        </li>
        <li>
          <Button attr={{
            className : `ico-vol`,
            text: '음향 조절',
            iconOnly: true,
          }} />
        </li>
        <li>
          <Button attr={{
            className : `ico-full`,
            text: '전체화면',
            iconOnly: true,
            onClick:() => handleFullClick()
          }} />
        </li>
      </ul>

      <div className="middle-txt">
        <Button attr={{
          className : `btn-prev`,
          text: `이전`,
          iconOnly: true,
        }} />
        <strong className="tit">클립재생</strong>
        <Button attr={{
          className : `btn-next`,
          text: `다음`,
          iconOnly: true,
        }} />
      </div>

      <div className="alert-bar">클립 구간바 재생</div>

      {vodActive.multiLayer && (
        <MultiContainer multiData={vodData.multiData} close={changeParentsFn} />
      )}

      {vodActive.clipLayer && (
        <ClipContainer clipData={vodData.clipData} />
      )}

      {vodActive.tagTime && (
        <TagProgress progressData={vodData.tagPrograss} />
      )}

      {vodActive.tagLayer && (
        <TagContainer close={changeParentsFn} />
      )}
    </div>
  );
};

export default VodContainer;
