import React, { useState, forwardRef, useImperativeHandle } from "react";
import BtnLayout from "components/molecule/layout/BtnLayout";
import Button from "components/atom/button/Button";
import Confirm from "components/molecule/alert/Confirm";

const ConfirmSet = forwardRef((props, ref) => {
  const {btnDatas, confirmDatas} = props
  const {
    color,
    size,
    state,
    className,
    style,
    onClick,
    disabled,
    text,
    solidIcon,
    regularIcon,
    animatedIcon,
    iconProps,
    iconOnly,
    icon,
    widthTxt,
    imgProps,
    addText,
    arrow,
    out,
    shape,
    radius,
  } = btnDatas
  console.log("ConfirmSet", confirmDatas)

  const [confirmOpen, setConfirmOpen] = useState(false);
  const openConfirmHandler = () => {
    setConfirmOpen(!confirmOpen);
    console.log("openConfirmHandler", confirmOpen)
  };

  const closeConfirmHandler = () => {
    setConfirmOpen(false);
    console.log("closeConfirmHandler", confirmOpen)
  };

  useImperativeHandle(ref, () => ({
    // 부모 컴포넌트에서 사용할 함수를 선언
    closeConfirmHandler
  }))

  return (
    <>
      <Button
        attr={btnDatas}
        subClick={() => openConfirmHandler()}
      />
      {confirmOpen && (
        <Confirm
          data={confirmDatas}
          onClose={openConfirmHandler}
        />
      )}
    </>
  );
});

export default ConfirmSet;
