const Hvod_2 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const vodRef = useRef(null);
  const stickyRef = useRef(null);
  const tabRef = useRef(null);
  const tabContentRef = useRef(null);

  function positionRest() {
    const offsetHeight = vodRef.current.offsetHeight;
    stickyRef.current.style.setProperty('margin-top', offsetHeight +'px')
    tabRef.current.style.setProperty('margin-top', '-'+offsetHeight +'px')
    tabContentRef.current.style.setProperty('padding-top', offsetHeight +'px')
  }

  useEffect(() => {
    if (vodRef.current && isMobile) {
      const handleResize = () => {
        positionRest();
      }

      window.addEventListener("resize", handleResize);
      handleResize();

      return () => window.removeEventListener("resize", handleResize);
    }
  }, []);

  const handleResize = () => {
    const currentScrollY = window.scrollY;
  };
  return (
    <>
      <div className="page-landing">
        <div className="m-main">
          <div className="vod-layout flex">
          <div className="vod-left">
            <div className="box-wrap">
              <section className="sec-vod" ref={vodRef}>
                <div className="vod-container _dim">
                  <video controls className="video" poster={vodData.thumb}>
                    <source src="" type="video/webm" />
                  </video>
                  <Button attr={{
                    className : `ico-close`,
                    text:`닫기`,
                    iconOnly: true,
                  }} />

                  <div className="middle-txt">
                    <Button attr={{
                      className : `btn-prev`,
                      text: `이전`,
                      iconOnly: true,
                    }} />
                    <strong className="tit">클립재생</strong>
                    <Button attr={{
                      className : `btn-next`,
                      text: `다음`,
                      iconOnly: true,
                    }} />
                  </div>
                </div>
              </section>

              <section className="sec-info">
                <div className="m-main">
                  <div className="info-container">
                    <header className="tit-header flex">
                      <dl className="col-tit">
                        <dt><strong className="tit">2004 대학 야구 통합 리그대회</strong></dt>
                        <dd><p className="time">22시간전</p></dd>
                      </dl>
                      <div className="col-right">
                        <ul className="ico-ul">
                          <li><button className="btn btn-ico ico-alarm"><span className="a11y">알람</span></button></li>
                          <li><button className="btn btn-ico ico-share"><span className="a11y">공유하기</span></button></li>
                          <li><button className="btn btn-ico ico-heart"><span className="a11y">찜</span></button></li>
                          <li><button className="btn btn-ico ico-etc"><span className="a11y">기타</span></button></li>
                        </ul>
                        <p className="dec c-blue">100뽈이 필요해요</p>
                      </div>
                    </header>
                    <div className="tag-container">
                      <div className="top-flex flex">
                        <ul className="btn-ul">
                          <li><button className="btn btn-line-white round btn-xs"><span className="ico clip">클립</span></button></li>
                          <li><button className="btn btn-line-white round btn-xs"><span className="ico tag">태그</span></button></li>
                        </ul>
                        <p className="col-right">100뽈이 필요해요</p>
                      </div>
                      <TagWrapper attr={tagData} />
                    </div>
                  </div>
                </div>

                <div className="card-container">
                  <div className="m-main">
                    <ul className="card-flex flex">
                      {liveCardData.slice(0, 1).map((item, index) => (
                        <li key={index} className="f1">
                          <LiveCard
                            title={item.title}
                            team1={item.team1}
                            team2={item.team2}
                            stadium={item.stadium}
                            score={item.score}
                            date={item.date}
                          />
                        </li>
                      ))}
                    </ul>
                  </div>
                </div>

                <TagWrapper attr={tagData} />
              </section>
            </div>
          </div>

            <div className="vod-tab">
              <section className="sec-tab">
                <div className="tab-container t4">
                  <TabsWrap
                    attr={{
                      stickyRef:stickyRef,
                      tabRef:tabRef,
                      tabContentRef:tabContentRef
                    }}
                  >
                    <div label="하이라이트">
                      <TabContainer attr={{tabData:tabHighlight}}/>
                    </div>

                    <div label="클립">
                      {/*<TabContainer
                        attr={{
                          tabData:tabClip,
                          onClick:clipFmClick,
                        }}
                      />*/}
                      <TabContainer
                        attr={{tabData:tabClip}}
                      />
                    </div>

                    <div label="태그">
                      <TabContainer attr={{tabData:tabTag}}/>
                    </div>

                    <div label="경기분석">
                      <TabContainer attr={{tabData:tabAnalysis}}/>
                    </div>
                  </TabsWrap>
                </div>
              </section>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default withRouter(Hvod_2);
