const sitemap = (props) => {
  return (
    <>
      <section className="sitemap-section">
        <div className="m-main">
          <header>
            <h1>호각 Sitemap</h1>
            <nav className="right">
              <span className="ing">확인요청</span>
              <span className="up">수정</span>
              <span className="ed">완료</span>
            </nav>
          </header>
          <table className="front">
            <colgroup>
              <col width={"50px"} />
              <col width={"200px"} />
              <col width={"100px"} />
              <col width={"100px"} />
              <col />
              <col width={"80px"} />
              <col width={"80px"} />
              <col width={"80px"} />
            </colgroup>
            <thead>
              <tr>
                <th>page</th>
                <th>화면설계서</th>
                <th>depth1</th>
                <th>depth2</th>
                <th>메모</th>
                <th>상태</th>
                <th>날짜</th>
                <th>담당장</th>
              </tr>
            </thead>
            <tbody>
              {dataItem.map((item, index) => (
                <tr key={index}>
                  <td colSpan="8">
                    <table className="inner">
                      <colgroup>
                        <col width={"50px"} />
                        <col width={"200px"} />
                        <col width={"100px"} />
                        <col width={"100px"} />
                        <col />
                        <col width={"80px"} />
                        <col width={"80px"} />
                        <col width={"80px"} />
                      </colgroup>
                      <thead>
                        <tr className="header" key={index}>
                          <th colSpan="8">
                            <p>{item.title}</p>
                          </th>
                        </tr>
                      </thead>
                      <tbody>
                        {item.datas &&
                          item.datas.map((item2, index2) => (
                            <tr className={`item ${item2.ing}`} key={item2}>
                              <td>{item2.page}</td>
                              <td>
                                <p>
                                  {item2.link ? (
                                    <Link to={item2.link} target="_blank">
                                      {item2.seq}
                                    </Link>
                                  ) : (
                                    item2.seq
                                  )}
                                </p>
                              </td>
                              <td>
                                <p>{item2.depth1}</p>
                              </td>
                              <td>{item2.depth2 ? item2.depth2 : ""}</td>
                              <td>
                                <p>{item2.memo}</p>
                              </td>
                              <td>{item2.status ? item2.status : ""}</td>
                              <td>{item2.update}</td>
                              <td>{item2.writer}</td>
                            </tr>
                          ))}
                      </tbody>
                    </table>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </section>
    </>
  );
};
export default withRouter(sitemap);
